export function startDay(date?: Date): Date {
	const startDay = new Date (_checkDate(date));
	startDay.setHours(0, 0, 0, 0);
	return startDay;
}
export function endDay(date?: Date): Date {
	const endDay = new Date (_checkDate(date));
	endDay.setHours(23, 59, 59, 999);
	return endDay;
}
export function lastMonth(date?: Date): Date {
	const lastMonth = new Date (_checkDate(date));
	lastMonth.setDate(1);
	lastMonth.setHours(0, 0, 0, 0);
	return lastMonth;
}
export function lastYear(date?: Date): Date {
	const lastYear = new Date (_checkDate(date));
	lastYear.setMonth(0);
	lastYear.setDate(1);
	lastYear.setHours(0, 0, 0, 0);
	return lastYear;
}
export function dayToDate (date?: Date): Date {
	const dayToDate = new Date (_checkDate(date));
	dayToDate.setDate(dayToDate.getDate() - 1);
	return dayToDate;
}
export function weekToDate(date?: Date): Date {
	
	const weekToDate = new Date (_checkDate(date));
	weekToDate.setDate(weekToDate.getDate() - 7);
	return weekToDate;
}
export function monthToDate(date?: Date): Date {
	const monthToDate = new Date (_checkDate(date));
	monthToDate.setMonth(monthToDate.getMonth() - 1);
	return monthToDate;
}
export function yearToDate(date?: Date): Date {
	const yearToDate = new Date (_checkDate(date));
	yearToDate.setFullYear(yearToDate.getFullYear() - 1);
	return yearToDate;
}

function _checkDate(date: Date | undefined): Date {
	return (date !== undefined) ? date : new Date();
}