import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import TransactionsFilter from "./components/TransactionsFilter";
import TransactionsTable from "./components/TransactionsTable";
import PaginationActivity from "../../common/components/PaginationActivity/PaginationActivity";
import * as account from "../../../store/ducks/account.duck";
import * as models from "@cryptopay/dashboard-contract/models";
import { RootState } from "../../../store/rootDuck";
import { connect, ConnectedProps } from "react-redux";
import { RouteComponentProps, Link } from "react-router-dom";
import * as helper from "../../../utils/helper";

interface ParamsAccountPath {
	accountId: string;
}

type State = {
	accountId: string | null;
}

type Props = RouteComponentProps<ParamsAccountPath> & ConnectedProps<typeof connector> & {
	accountData: account.AccountState["accountData"];
	transactionsFilter: account.AccountState["transactionsFilter"];
	selectedTransaction: account.AccountState["selectedTransaction"];
	transactions: account.AccountState["transactions"];
	getAccountAction: typeof account.getAccountAction,
	getTransactionAction: typeof account.getTransactionAction,
	getTransactionsListAction: typeof account.getTransactionsListAction
}

export class Account extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {
			accountId: null
		};
	}

	componentDidMount() {
		const params = this.props.match.params;
		if (params.accountId) {
			if (this.state.accountId !== params.accountId) {
				this.setState({ accountId: params.accountId }, this._loadAccountData);
			}
		} else {
			this.setState({ accountId: null });
		}
		//const params = this.props.match.params;
		//if (params.accountId) {
		//	this.setState({ accountId: params.accountId });

		//	this.props.getAccountAction(params.accountId);
		//	const filter = this._parseQueryParamsToFilter(this.props);
		//	if (this.props.history.location.search === "") {
		//		this.props.history.replace(`/account/${params.accountId}?${helper.parseObjectToQueryParams(filter)}`);
		//	}
		//	this.props.getTransactionsListAction(params.accountId, filter);
		//} else {
		//	this.setState({ accountId: null });
		//}
	}

	componentDidUpdate(prevProps: Props, prevState: State) {
		const params = this.props.match.params;
		if (params.accountId) {
			if (this.state.accountId !== params.accountId) {
				this.setState({ accountId: params.accountId }, this._loadAccountData);
			}
		} else {
			this.setState({ accountId: null });
		}
		// maybe move to updateTransactionsFilter
		if (this.props.transactionsFilter !== prevProps.transactionsFilter) {
			this.props.history.replace(`/account/${params.accountId}?${helper.parseObjectToQueryParams(this.props.transactionsFilter)}`);
		}
	}

	private _loadAccountData = () => {
		const filter = this._parseQueryParamsToFilter(this.props);
		if (this.state.accountId !== null) {
			if (this.props.history.location.search === "") {
				this.props.history.replace(`/account/${this.state.accountId}?${helper.parseObjectToQueryParams(filter)}`);
			}
			this.props.getAccountAction(this.state.accountId);
			this.props.getTransactionsListAction(this.state.accountId, filter);
		}
	}

	updateTransactionsFilter = (newFilterData: { page: number, count: number } | models.FilterTransactions) => {
		const filter: models.FilterTransactions = {
			...this.props.transactionsFilter,
			...newFilterData
		}
		if (this.state.accountId !== null) {
			this.props.getTransactionsListAction(this.state.accountId, filter);
		}
	}

	render() {
		const account: models.Account | null = this.props.accountData;
		if (account === null) {
			return null;
		}

		return (
			<Row className="mb-4 mt-4">
				<Col xs={12} md={12} className="mt-4 pl-4">
					<Container className="general_container card">
						<Row className="mb-4 mt-4">
							<Col xs={6} md={4} className="mt-4 pl-4">
								<div>
									<div className="mb-4">
										<label className="font-italic text-muted">Account Id</label>
										<div>{account.apiAccountId}</div>
									</div>

									<div className="mb-4">
										<label className="font-italic text-muted">Description</label>
										<div>{account.description}</div>
									</div>
									{
										(account.type === models.AccountType.CRYPTO || account.type === models.AccountType.CRYPTO_DEPOSIT) &&
										<div className="mb-4">
											<label className="font-italic text-muted">Crypto Address</label>
											<div>{account.address}</div>
										</div>
									}
									{
										(account.type === models.AccountType.CRYPTO_DEPOSIT) &&
										<div className="mb-4">
											<label className="font-italic text-muted">Order Id</label>
											<div><Link to={`/order/${account.apiOrderUuid}`}>{account.apiOrderUuid}</Link></div>
										</div>
									}
								</div>
							</Col>
							<Col xs={6} md={4} className="mt-4 pl-4">
								<div>
									<div className="mb-4">
										<label className="font-italic text-muted">Client Account Id</label>
										<div>{account.clientAccountId}</div>
									</div>
									<div className="mb-4">
										<label className="font-italic text-muted">Merchant</label>
										<div>{account.merchant}</div>
									</div>
									<div className="mb-4">
										<label className="font-italic text-muted">Tags</label>
										<div>{account.tags}</div>
									</div>
								</div>
							</Col>
							<Col xs={6} md={4} className="mt-4 pl-4">
								<div>
									<div className="mb-4">
										<label className="font-italic text-muted">Account type</label>
										<div>{account.type}</div>
									</div>
									{
										(account.type === models.AccountType.CRYPTO || account.type === models.AccountType.CRYPTO_DEPOSIT) &&
										<div className="mb-4">
											<label className="font-italic text-muted">Network</label>
											<div>{account.network}</div>
										</div>
									}
									<div className="mb-4">
										<label className="font-italic text-muted">Balance</label>
										<div>{account.balance} {account.currency}</div>
									</div>
									<div className="mb-4">
										<label className="font-italic text-muted">Created At</label>
										<div>{new Date(account.createdAt).toISOString()}</div>
									</div>
								</div>
							</Col>
						</Row>

					</Container>
				</Col>
				<Col xs={12} md={12} className="mt-4 pl-4">
					<Container className="card mb-4">
						{
							false && // waiting for a beautiful design
							<TransactionsFilter />
						}

						<TransactionsTable
							transactions={this.props.transactions !== null ? this.props.transactions.transactions : []}
						/>
						{
							this.props.transactions !== null &&
							<PaginationActivity
								countMax={this.props.transactions.countTransactions}
								countOnPage={this.props.transactionsFilter.count}
								page={this.props.transactionsFilter.page}
								itemsLength={3}
								submitPagination={this.updateTransactionsFilter}
							/>
						}
					</Container>
				</Col>
			</Row>
		);
	}


	private _parseQueryParamsToFilter = (props: Props): models.FilterTransactions => {
		const searchParams = new URLSearchParams(props.history.location.search);

		const typeStr = searchParams.get("type");
		const status = searchParams.get("status");
		const dateFromStr = searchParams.get("dateFrom");
		const dateToStr = searchParams.get("dateTo");
		const countStr = searchParams.get("count");
		const pageStr = searchParams.get("page");

		return {
			type: typeStr !== null ? models.TransactionType.ensure(typeStr) : undefined,
			status: status !== null ? (status) : undefined,
			dateFrom: (dateFromStr !== null) ? new Date(dateFromStr) : undefined,
			dateTo: (dateToStr !== null) ? new Date(dateToStr) : undefined,
			count: (countStr !== null) ? Number.parseInt(countStr) : props.transactionsFilter.count,
			page: (pageStr !== null) ? Number.parseInt(pageStr) : props.transactionsFilter.page
		}
	}
}

const mapState = (state: RootState) => ({
	accountData: state.account.accountData,
	transactionsFilter: state.account.transactionsFilter,
	selectedTransaction: state.account.selectedTransaction,
	transactions: state.account.transactions
});

const mapDispatch = {
	getAccountAction: account.getAccountAction,
	getTransactionAction: account.getTransactionAction,
	getTransactionsListAction: account.getTransactionsListAction
}

const connector = connect(mapState, mapDispatch);

export default connector(Account);