import React, { Component } from "react";

import styles from "./index.module.scss";


type State = {

};

type Props = {
	label: string,
	variant: "add" | "delete" | "disabled",
	onClick?: () => void,
	className?: string
}

export default class ChipElement extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		this.state = {
		}
	}

	render() {

		return (
			<>
				{
					this.props.variant === "add" &&
					<div className={`${styles.chip_element} ${styles.delete_variant} ${this.props.className && this.props.className}`}>
						{this.props.label} <img alt="close" onClick={this.props.onClick} src="images/close_16x16.png" />
					</div>
				}
				{
					this.props.variant === "delete" &&
					<div className={`${styles.chip_element} ${styles.add_variant} ${this.props.className && this.props.className}`}>
						{this.props.label} <img alt="close" onClick={this.props.onClick} src="images/plus_16x16.png" />
					</div>
				}
				{
					this.props.variant === "disabled" &&
					<div className={`${styles.chip_element} ${this.props.className && this.props.className}`}>
						{this.props.label}
					</div>
				}

			</>
		);
	}
}