import React, { Component } from "react";
import * as models from "@cryptopay/dashboard-contract/models";
import { Button } from "react-bootstrap";
import { Link, Switch, Route } from "react-router-dom";
import { RootState } from "../../../../store/rootDuck";
import { connect } from "react-redux";
import { getGatewaysListAction } from "../../../../store/ducks/gateway.duck";
import GatewaysList from "./GatewaysList/GatewaysList";
import GatewayConstruct from "./GatewayConstruct/GatewayConstruct";

type State = {
}
type Props = {
	gatewaysList: models.GatewaysList | null,
	getGatewaysListAction: typeof getGatewaysListAction
}

export class Gateway extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {
		}
	}

	componentDidMount() {
	}
	componentDidUpdate(prevProps: Props, prevState: State) {
		this.props.getGatewaysListAction();
	}


	render() {
		const isGatewayExists = (this.props.gatewaysList !== null && this.props.gatewaysList.length > 0);
		return (
			<div>
				<Switch>
					<Route path="/settings/gateway/create" component={GatewayConstruct} />
					<Route path="/settings/gateway/:gatewayUuid" component={GatewayConstruct} />
					<Route path="/settings/gateway">
						{
							!isGatewayExists &&
							<Link to={`/settings/gateway/create`} className="my-2"><Button variant="outline-primary">Start Integration</Button></Link>
						}
						{
							isGatewayExists &&
							<GatewaysList />
						}
					</ Route>
				</Switch>
			</div >
		)
	}
}

const mapState = (state: RootState) => ({
	gatewaysList: state.gateway.gatewaysList
});

const mapDispatch = {
	getGatewaysListAction: getGatewaysListAction
}

const connector = connect(mapState, mapDispatch);

export default connector(Gateway);