import React, { Component } from "react";
import { Table } from "react-bootstrap";
import { connect } from "react-redux";
import { RootState } from "../../../../store/rootDuck";
import * as models from "@cryptopay/dashboard-contract/models";

type State = {
}

type Props = {
	filterCurrencies: Array<string>;
	filterMerchants: Array<string>;
	filterSettings: models.FilterSettingsMerchants | null;
	merchants: models.Merchants["merchants"];
	permissions: Array<models.DashboardPermission>;
}

export class MerchantsTable extends Component<Props, State> {

	render() {
		const merchantsToShow: Array<string> = (this.props.filterMerchants.length > 0)
			? this.props.filterMerchants
			: this.props.filterSettings !== null
				? [...this.props.filterSettings.merchants] : []
		return (
			<Table responsive className="table-hover text-center">
				<thead>
					<tr>
						<th>Name</th>
						<th className="border-right">Merchant Provider</th>
						{
							this.props.filterCurrencies.map((currency, index) => {
								return (
									<React.Fragment key={currency}>
										<th>Balance {currency}</th>
										<th>Turnover {currency}</th>
										<th className="border-right">Fee {currency}</th>
									</React.Fragment>
								);
							})
						}
					</tr>
				</thead>
				<tbody>
					{
						merchantsToShow.map((merchantName, index) => {
							return (
								<tr key={merchantName}>
									<td>
										<p>{merchantName}</p>
									</td>
									<td className="border-right">{this.props.merchants[merchantName]?.merchantProvider}</td>
									{
										this.props.filterCurrencies.map((currency, index) => {
											return (
												<React.Fragment key={`${merchantName}-${currency}`}>
													<td>{this.props.merchants[merchantName]?.financial[currency] ? this.props.merchants[merchantName]?.financial[currency]?.balance : 0}</td>
													<td>{this.props.merchants[merchantName]?.financial[currency] ? this.props.merchants[merchantName]?.financial[currency]?.turnover : 0}</td>
													<td className="border-right">{this.props.merchants[merchantName]?.financial[currency] ? this.props.merchants[merchantName]?.financial[currency]?.fee : 0}</td>
												</React.Fragment>
											);
										})
									}
								</tr>
							);

						})
					}
				</tbody>
			</Table>
		);
	}
}

const mapState = (state: RootState) => ({
	filterSettings: state.merchants.settings,
	merchants: state.merchants.merchants?.merchants ? state.merchants.merchants.merchants : {},
	permissions: []//state.auth.permissions,
	
});

const connector = connect(mapState);

export default connector(MerchantsTable);