import { SubscriptionList } from "@cryptopay/dashboard-contract";
import { RootState } from "../../../../store/rootDuck";
import React, { Component } from "react";
import { connect } from "react-redux";
import { Button, Table, TableBody, TableHead, TableRow, TableCell} from "@material-ui/core";
import { loadSubscriptionList, deleteSubscription } from "../../../../store/ducks/subscriptions.duck";
import { Delete } from "@material-ui/icons";


type State = {};

type Props = {
	subscriptions: SubscriptionList;
	loadSubscriptionList: typeof loadSubscriptionList;
	deleteSubscription: typeof deleteSubscription;
}
export class SubscriptionsList extends Component<Props, State> {
	
	handleDeleteSubscription = (id: string) => {
		this.props.deleteSubscription(id);
	}

	componentDidMount() {
		
	}
	componentDidUpdate(prevProps: Props, prevState: State) {
		this.props.loadSubscriptionList();
	}

	render() {
		return (<div>
			<Table aria-label="Subscriptions list">
 			<TableHead>
 				<TableRow>
 					<TableCell>Url</TableCell>
 					<TableCell>Topic</TableCell>
 					<TableCell>Id</TableCell>
 					<TableCell>Created At</TableCell>
 					<TableCell>Certificate</TableCell>
 					<TableCell>Action</TableCell>
 				</TableRow>
 			</TableHead>
 			<TableBody>
 				{this.props.subscriptions.map(({ url, topic, subscriptionId, trustedCACertificatePEM, createdAt}) => {
 					return (
 				<TableRow key={subscriptionId}>
 					<TableCell>{url}</TableCell>
 					<TableCell>{topic}</TableCell>
 					<TableCell>{subscriptionId}</TableCell>
 					<TableCell>{createdAt.toString()}</TableCell>
 					<TableCell>{trustedCACertificatePEM }</TableCell>
 					<TableCell>
 						<Button
 							variant="contained"
 							color="default"
 							startIcon={<Delete />}
 							onClick={() => this.handleDeleteSubscription(subscriptionId)}
 						>Delete</Button>
 					</TableCell>
 				</TableRow>);
 				})}
 			</TableBody>
 		</Table>
		</div>);
	}
}

const mapState = (state: RootState) => ({
	subscriptions: state.subscriptions.subscriptionList,
});

const mapDispatch = {
	loadSubscriptionList,
	deleteSubscription,
};

const connector = connect(mapState, mapDispatch);

export default connector(SubscriptionsList);
