export function parseObjectToQueryParams(object: any): string {
	const urlSearchParams = new URLSearchParams();
	for (const key in object) {
		if (object.hasOwnProperty(key)) {
			const value = object[key];
			if (value !== null && value !== undefined) {
				if (value instanceof Date) {
					urlSearchParams.set(key, value.toISOString());
				} else if (Array.isArray(value)) {
					if (value.length > 0) {
						urlSearchParams.set(key, value.join(","));
					}
				} else if (value !== "") {
					urlSearchParams.set(key, value);
				}
			}
		}
	}
	const escapedSearchPath: string = urlSearchParams.toString();
	return escapedSearchPath;
}

export function convertCurrenciesForAutocomplite(currencies: Array<string>): Array<CurrencyPriority> {
	let result: Array<CurrencyPriority> = [];
	for (const currency of currencies) {
		if (currenciesPriorityDict[currency]) {
			result.push(currenciesPriorityDict[currency]);
		} else {
			console.warn(`Unknown currency ${currency}. Please add it to "currenciesPriorityDict".`);
			result.push({
				type: "UNKNOWN",
				value: currency,
				priority: 101
			});
		}

	}
	return result;
}
export interface CurrencyPriority {
	type: string;
	value: string;
	priority: number;
}
