import storage from "redux-persist/lib/storage";
import { put, call, takeEvery } from "redux-saga/effects";

import * as models from "@cryptopay/dashboard-contract/models";

import { cps } from '../../api/CryptoPayClient'
import { handleErrorAction } from "./general.duck";
import { OrdersColumn } from "../../pages/home/orders/components/OrdersTable";

/**
 * State of orders
 */
export interface OrdersState {
	filterSettings: models.FilterSettingsOrders | null;
	orders: models.Orders | null;
	activeColumns: Array<string>;
}

export enum ActionTypes {
	CLEAR_ORDERS_LIST_STORAGE = "CLEAR_ORDERS_LIST_STORAGE",

	GET_ORDERS_LIST = "GET_ORDERS_LIST",
	GET_ORDERS_SETTINGS = "GET_ORDERS_SETTINGS",
	
	LOAD_ORDERS_LIST = "LOAD_ORDERS_LIST",
	LOAD_ORDERS_SETTINGS = "LOAD_ORDERS_SETTINGS",

	UPDATE_ORDERS_COLUMNS = "UPDATE_ORDERS_COLUMNS"
};

export interface ClearOrdersListStorageAction {
	type: ActionTypes.CLEAR_ORDERS_LIST_STORAGE
}
export function clearOrdersListStorageAction(): OrdersListActionTypes {
	return {
		type: ActionTypes.CLEAR_ORDERS_LIST_STORAGE
	}
}

export interface GetOrdersListAction {
	type: ActionTypes.GET_ORDERS_LIST;
	payload: { filter: models.FilterOrders }
}
export function getOrdersListAction(filter: models.FilterOrders): OrdersListActionTypes {
	return {
		type: ActionTypes.GET_ORDERS_LIST,
		payload: { filter }
	}
}

export interface GetOrdersSettingsAction {
	type: ActionTypes.GET_ORDERS_SETTINGS;
}
export function getOrdersSettingsAction(): OrdersListActionTypes {
	return {
		type: ActionTypes.GET_ORDERS_SETTINGS
	}
}

export interface LoadOrdersListAction {
	type: ActionTypes.LOAD_ORDERS_LIST;
	payload: {
		orders: models.Orders | null
	}
}
export function loadOrdersListAction (orders: models.Orders | null): OrdersListActionTypes {
	return {
		type: ActionTypes.LOAD_ORDERS_LIST,
		payload: { orders }
	}
}

export interface LoadOrdersSettingsAction {
	type: ActionTypes.LOAD_ORDERS_SETTINGS;
	payload: {
		settings: models.FilterSettingsOrders | null
	}
}
export function loadOrdersSettingsAction (settings: models.FilterSettingsOrders | null): OrdersListActionTypes {
	return {
		type: ActionTypes.LOAD_ORDERS_SETTINGS,
		payload: { settings }
	}
}

export interface UpdateOrdersColumnsAction {
	type: ActionTypes.UPDATE_ORDERS_COLUMNS;
	payload: {
		activeColumns: Array<string>;
	}
}
export function updateOrdersColumnsAction(activeColumns: Array<string>): OrdersListActionTypes {
	return {
		type: ActionTypes.UPDATE_ORDERS_COLUMNS,
		payload: { activeColumns }
	}
}


export type OrdersListActionTypes = ClearOrdersListStorageAction
	| GetOrdersListAction
	| GetOrdersSettingsAction
	| LoadOrdersListAction
	| LoadOrdersSettingsAction
	| UpdateOrdersColumnsAction;

export const initialState: OrdersState = {
	filterSettings: null,
	orders: null,
	activeColumns: [OrdersColumn.MERCHANT, OrdersColumn.ORDER_ID, OrdersColumn.INSTRUMENT, OrdersColumn.STATE,
		OrdersColumn.STATUS, OrdersColumn.CURRENCY_FROM, OrdersColumn.AMOUNT_FROM,
		OrdersColumn.CURRENCY_TO, OrdersColumn.AMOUNT_TO, OrdersColumn.CREATED_AT
	]
}

export function ordersListReducer(previousState = initialState, action: OrdersListActionTypes): OrdersState {
	switch (action.type) {
		case ActionTypes.CLEAR_ORDERS_LIST_STORAGE:
			return initialState;
		case ActionTypes.LOAD_ORDERS_LIST:
			return {
				...previousState,
				orders: action.payload.orders
			}
		case ActionTypes.LOAD_ORDERS_SETTINGS:
			return {
				...previousState,
				filterSettings: action.payload.settings
			}
		case ActionTypes.UPDATE_ORDERS_COLUMNS:
			return {
				...previousState,
				activeColumns: action.payload.activeColumns
			}
		case ActionTypes.GET_ORDERS_LIST:
		case ActionTypes.GET_ORDERS_SETTINGS:
		default:
			return previousState;
	}
}

export const reducer = ordersListReducer;

export function* saga() {
	yield takeEvery(ActionTypes.GET_ORDERS_LIST, workerGetOrdersList);

	yield takeEvery(ActionTypes.GET_ORDERS_SETTINGS, workerGetOrdersSettings);
}

export function* workerGetOrdersList(action: GetOrdersListAction) {
	try {
		const orders = yield call(loadingOrdersListData, action.payload.filter);
		yield put(loadOrdersListAction(orders));
	} catch (e) {
		yield put(loadOrdersListAction(null));
		yield put(handleErrorAction(null, e));
	}
}

export function* workerGetOrdersSettings(action: GetOrdersSettingsAction) {
	try {
		const settings = yield call(loadingFilterSettings);
		yield put(loadOrdersSettingsAction(settings));
	} catch (e) {
		yield put(loadOrdersSettingsAction(null));
		yield put(handleErrorAction(null, e));
	}

}

export async function loadingFilterSettings(): Promise<models.FilterSettingsOrders> {
	return await cps.getOrdersFilterSettings();
}

export async function loadingOrdersListData(filter: models.FilterOrders): Promise<models.Orders> {
	return await cps.getOrdersList(filter);
}