import React, { Component, Fragment } from "react";
import { connect } from "react-redux";
import { RouteComponentProps } from "react-router-dom";
import { Button, Container, Row, Col, Form, InputGroup, FormControl, DropdownButton, Dropdown } from "react-bootstrap";
import validator from "validator";

import styles from "./index.module.scss";

import StepperIntegrationFlow from "../StepperIntegrationFlow/StepperIntegrationFlow";
import GatewaySecretsPopUp from "../GatewaySecretsPopUp/GatewaySecretsPopUp";

import { cps } from "../../../../../api/CryptoPayClient";
import { getGatewaysListAction } from "../../../../../store/ducks/gateway.duck";
import { handleErrorAction } from "../../../../../store/ducks/general.duck";
import * as models from "@cryptopay/dashboard-contract/models";
import { RootState } from "../../../../../store/rootDuck";

interface ParamsGatewayPath {
	gatewayUuid?: string;
}

const URL_VALIDATOR_CONFIG_GATEWAY = {
	protocols: ["http", "https"],
	require_protocol: true
}

type State = {
	isEdit: boolean;
	showDemoOrderPopup: boolean;
	// secrets after create gateway
	respData: models.CreateGatewayResponse | null;
	// gateway data
	gatewayUuid: string | null;
	description: string | null;
	dirtyDomain: string | null;
	enabled: boolean;
	domainWhitelist: Array<string>;
	callbackMethod: models.GatewayCallbackType | null;
	callbackUrl: string | null;
	callbackCaCert: string | null;
	allowUnsignedRequests: boolean;
}
type Props = RouteComponentProps<ParamsGatewayPath> & {
	gatewaysList: models.GatewaysList | null,
	handleErrorAction: typeof handleErrorAction,
	getGatewaysListAction: typeof getGatewaysListAction
}

export class GatewayConstruct extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		this.state = {
			isEdit: false,
			showDemoOrderPopup: false,
			gatewayUuid: null,
			description: null,
			dirtyDomain: null,
			enabled: false,
			domainWhitelist: [],
			callbackMethod: null,
			callbackUrl: null,
			callbackCaCert: null,
			allowUnsignedRequests: false,
			respData: null
		}
	}

	componentDidMount() {
		const params = this.props.match.params;
		if (params.gatewayUuid && validator.isUUID(params.gatewayUuid)) {
			this._setGatewayState(params.gatewayUuid);
		} else {
			this.setState({ isEdit: true });
		}
	}
	componentDidUpdate(prevProps: Props, prevState: State) {

		if (prevProps.gatewaysList !== this.props.gatewaysList && this.state.gatewayUuid !== null) {
			this._setGatewayState(this.state.gatewayUuid);
		}
	}

	_setGatewayState = (gatewayUuid: string) => {
		const gatewaysList = this.props.gatewaysList;
		if (gatewaysList !== null) {
			const gateway = gatewaysList.find((gate) => {
				if (gate.gatewayUuid === gatewayUuid) {
					return true;
				}
				return false;
			})
			if (gateway) {
				this.setState({
					gatewayUuid: gateway.gatewayUuid,
					description: gateway.description,
					dirtyDomain: null,
					enabled: gateway.enabled,
					domainWhitelist: gateway.config.domainWhitelist,
					callbackMethod: gateway.callbackConfig !== null ? gateway.callbackConfig.type : null,
					callbackUrl: gateway.callbackConfig !== null ? gateway.callbackConfig.url : null,
					callbackCaCert: gateway.callbackConfig !== null ? gateway.callbackConfig.rootCertificate : null,
					allowUnsignedRequests: gateway.config.allowUnsignedRequests
				});
			} else {
				this.props.handleErrorAction(`Gateway data is not defined by uuid: ${gatewayUuid}`, null);
			}
		} else {
			this.props.handleErrorAction(`Gateways List is not defined.`, null);
		}
	}

	onBlurDomainHandler = () => {
		const dirtyDomain = this.state.dirtyDomain;
		if (dirtyDomain !== null && !validator.isURL(dirtyDomain, URL_VALIDATOR_CONFIG_GATEWAY)) {
			this.props.handleErrorAction(`Domain Url: ${dirtyDomain} is not valid`, null);
		}
	}
	onBlurCallbackUrlHandler = () => {
		const callbackUrl = this.state.callbackUrl;
		if (callbackUrl !== null && !validator.isURL(callbackUrl, URL_VALIDATOR_CONFIG_GATEWAY)) {
			this.props.handleErrorAction(`Callback Url: ${callbackUrl} is not valid`, null);
		}
	}

	onClickAddDomainHandler = () => {
		const dirtyDomain = this.state.dirtyDomain;
		if (dirtyDomain !== null) {
			if (!validator.isURL(dirtyDomain, URL_VALIDATOR_CONFIG_GATEWAY)) {
				this.props.handleErrorAction(`Domain Url: ${dirtyDomain} is not valid`, null);
				return;
			}
			const domainWhitelist = [...this.state.domainWhitelist];
			if (domainWhitelist.includes(dirtyDomain)) {
				return;
			}
			domainWhitelist.push(dirtyDomain);
			this.setState({
				domainWhitelist,
				dirtyDomain: null
			})
		}
	}
	onClickDeleteDomainHandler = (domain: string) => () => {
		const domainWhitelist = this.state.domainWhitelist.filter((value) => { return value !== domain });
		this.setState({ domainWhitelist });
	}

	onClickCreateGateway = async () => {
		const description = this.state.description;
		if (description === null || description.length === 0) {
			this.props.handleErrorAction(`Description: ${description} is not valid`, null);
			return;
		}
		const domainWhitelist = this.state.domainWhitelist;
		if (domainWhitelist.length === 0) {
			this.props.handleErrorAction(`Domein whitelist is empty`, null);
			return;
		}

		let callbackConfig: models.GatewayCallbackConfig | null = null;
		const callbackUrl = this.state.callbackUrl;
		const callbackMethod = this.state.callbackMethod;
		const callbackCaCert = this.state.callbackCaCert;
		if (callbackUrl !== null) {
			if (!validator.isURL(callbackUrl, URL_VALIDATOR_CONFIG_GATEWAY)) {
				this.props.handleErrorAction(`Callback url: ${callbackUrl} is not valid`, null);
				return;
			}
			if (callbackMethod === null) {
				this.props.handleErrorAction(`Callback method is not defined`, null);
				return;
			}
			callbackConfig = {
				url: callbackUrl,
				type: callbackMethod,
				rootCertificate: callbackCaCert
			}
		}

		const gateway: models.CreateGatewayRequest = {
			description,
			enabled: true,
			config: {
				allowUnsignedRequests: this.state.allowUnsignedRequests,
				domainWhitelist
			},
			callbackConfig
		}

		try {
			const body = await cps.createGateway(gateway);
			this.setState({ respData: body, gatewayUuid: body.gatewayId, isEdit: false });
		} catch (e) {
			this.props.handleErrorAction(`Cann't create new gateway.`, e);
		}
	}

	onClickGenerateSecretsGateway = async () => {
		const gatewayUuid = this.state.gatewayUuid;
		if (gatewayUuid === null) {
			this.props.handleErrorAction(`Gateway Uuid is not defined`, null);
			return;
		}
		try {
			const body = await cps.changeGatewaySecrets(gatewayUuid);
			this.setState({ respData: body, gatewayUuid: body.gatewayId, isEdit: false });
		} catch (e) {
			this.props.handleErrorAction(`Cann't generate new secrets.`, e);
		}
	}

	onClickDemoOrder = async () => {
		const gatewayUuid = this.state.gatewayUuid;
		if (gatewayUuid === null) {
			this.props.handleErrorAction(`Gateway Uuid is not defined`, null);
			return;
		}
		this.setState({ showDemoOrderPopup: true });
	}

	onClickUpdateGateway = async () => {
		const gatewayUuid = this.state.gatewayUuid;
		if (gatewayUuid === null) {
			this.props.handleErrorAction(`Cann't update gateway. GatewayUuid is not defined`, null);
			return;
		}
		const description = this.state.description;
		if (description === null || description.length === 0) {
			this.props.handleErrorAction(`Cann't update gateway. Description: ${description} is not valid`, null);
			return;
		}
		const domainWhitelist = this.state.domainWhitelist;
		if (domainWhitelist.length === 0) {
			this.props.handleErrorAction(`Cann't update gateway. Domain whitelist is empty`, null);
			return;
		}

		let callbackConfig: models.GatewayCallbackConfig | null = null;
		const callbackUrl = this.state.callbackUrl;
		const callbackMethod = this.state.callbackMethod;
		const callbackCaCert = this.state.callbackCaCert;
		if (callbackUrl !== null) {
			if (!validator.isURL(callbackUrl, URL_VALIDATOR_CONFIG_GATEWAY)) {
				this.props.handleErrorAction(`Cann't update gateway. Callback url: ${callbackUrl} is not valid`, null);
				return;
			}
			if (callbackMethod === null) {
				this.props.handleErrorAction(`Cann't update gateway. Callback method is not defined`, null);
				return;
			}
			callbackConfig = {
				url: callbackUrl,
				type: callbackMethod,
				rootCertificate: callbackCaCert
			}
		}

		const gateway: models.UpdateGatewayRequest = {
			description,
			enabled: this.state.enabled !== null ? this.state.enabled : undefined,
			config: {
				allowUnsignedRequests: this.state.allowUnsignedRequests,
				domainWhitelist
			},
			callbackConfig
		}

		try {
			await cps.updateGateway(gatewayUuid, gateway);
			this.setState({ isEdit: false });
			this.props.getGatewaysListAction();
		} catch (e) {
			this.props.handleErrorAction(`Cann't update gateway by uuid: ${gatewayUuid}.`, e);
		}
	}

	onClickCloseRespPopUp = () => {
		this.setState({
			respData: null
		});
	}

	render() {
		return (
			<div className={styles.gateway_construct}>
				{
					<Container className="card mb-4">
						<Row>
							<Col xs={12} sm={6} >
								<div className="ml-4 mt-4">
									{
										this.state.gatewayUuid !== null &&
										<h6>{this.state.gatewayUuid}</h6>
									}
									<label className="font-italic text-muted mt-4 mb-4">Description</label>
									<div>
										<textarea
											className={styles.gate_description_textarea}
											placeholder="Write gateway description"
											disabled={!this.state.isEdit}
											rows={2}
											value={this.state.description ? this.state.description : ""}
											onChange={(event: any) => this.setState({ description: event.target.value })}
										/>
									</div>
									<Form.Check
										type="checkbox"
										label="enabled"
										checked={this.state.enabled}
										onChange={(event: any) => this.setState({ enabled: event.target.checked })}
										disabled={!this.state.isEdit}
									/>
								</div>
								<div className="mt-4 ml-4">
									<label className="font-italic text-muted mt-2">Config</label>
									<div>
										<Form.Check
											type="checkbox"
											label="allowUnsignedRequests"
											disabled={!this.state.isEdit}
											checked={this.state.allowUnsignedRequests}
											onChange={(event: any) => this.setState({ allowUnsignedRequests: event.target.checked })}
										/>
									</div>
									<div className="mt-4">
										<label className="font-italic text-muted">Domain Whitelist:</label>
										{
											this.state.isEdit &&
											<InputGroup className="mt-4">
												<FormControl
													id="gatewayDomainInput"
													placeholder="Write your domain url"
													aria-label="Domain Input"
													disabled={!this.state.isEdit}
													value={this.state.dirtyDomain ? this.state.dirtyDomain : ""}
													onChange={(event: any) => this.setState({ dirtyDomain: event.target.value })}
													onBlur={this.onBlurDomainHandler}
												/>
												<InputGroup.Append>
													<Button variant="outline-secondary" onClick={this.onClickAddDomainHandler}>Add</Button>
												</InputGroup.Append>
											</InputGroup>
										}
										{
											this.state.domainWhitelist.map((domain) => {
												return (
													<InputGroup key={domain} className="mt-4">
														<FormControl
															id="gatewayDomainInput"
															placeholder="Write your domain url"
															aria-label="Domain Input"
															disabled
															value={domain}
														/>
														{
															this.state.isEdit &&
															<InputGroup.Append>
																<Button variant="outline-secondary" onClick={this.onClickDeleteDomainHandler(domain)}>Delete</Button>
															</InputGroup.Append>
														}

													</InputGroup>
												)
											})
										}
									</div>
								</div>
							</Col>
							<Col xs={12} sm={6} className="mb-4">
								<div className="mt-4 ml-4 ">
									<label className="font-italic text-muted">Callback Config</label>
									<InputGroup className="mb-3">
										<DropdownButton
											as={InputGroup.Prepend}
											variant="outline-secondary"
											disabled={!this.state.isEdit}
											title={this.state.callbackMethod ? this.state.callbackMethod : "Method"}
											id="input-group-dropdown-1"
										>
											<Dropdown.Item onSelect={() => this.setState({ callbackMethod: models.GatewayCallbackType.Post })}>POST</Dropdown.Item>
											<Dropdown.Item onSelect={() => this.setState({ callbackMethod: models.GatewayCallbackType.Get })}>GET</Dropdown.Item>
										</DropdownButton>
										<FormControl
											placeholder="Write callback url"
											disabled={!this.state.isEdit}
											value={this.state.callbackUrl ? this.state.callbackUrl : ""}
											onChange={(event: any) => this.setState({ callbackUrl: event.target.value })}
											onBlur={this.onBlurCallbackUrlHandler}
										/>
									</InputGroup>
									<label className="font-italic text-muted mt-4 mb-4">CA certificate</label>
									<div>
										<textarea
											className={styles.gate_ca_cert_textarea}
											rows={7}
											placeholder="CA certificate"
											disabled={!this.state.isEdit}
											value={this.state.callbackCaCert ? this.state.callbackCaCert : ""}
											onChange={(event: any) => this.setState({ callbackCaCert: event.target.value })}
										/>
									</div>
								</div>
							</Col>
							<Col xs={12} sm={12} className="mb-4">
								<div className={styles.gate_activity_buttons_div}>
									{
										this.state.gatewayUuid !== null ?
											this.state.isEdit ?
												<Button variant="outline-primary" className={styles.gate_button} onClick={this.onClickUpdateGateway}>Update</Button> :
												<>
													<Button variant="outline-primary" className={styles.gate_button} onClick={() => this.setState({ isEdit: true })}>Edit</Button>
													<Button variant="outline-primary" className={styles.gate_button} onClick={this.onClickGenerateSecretsGateway}>Generate New Secrets</Button>
													<Button variant="outline-primary" className={styles.gate_button} onClick={this.onClickDemoOrder}>Demo Order</Button>
												</> :
											<Button variant="outline-primary" className={styles.gate_button} onClick={this.onClickCreateGateway}>Create</Button>
									}
								</div>
							</Col>
						</Row>
					</Container>
				}
				{
					this.state.respData !== null ?
						<Fragment>
							{/* <GatewaySecretsPopUp
								merchantName={this.props.}
								respData={this.state.respData}
								onClickClosePopUp={this.onClickCloseRespPopUp}
							/> */}
							<h1>GatewaySecretsPopUp Disabled</h1>
						</Fragment> :
						(this.state.showDemoOrderPopup && this.state.gatewayUuid !== null) ?
							<Fragment>
								<StepperIntegrationFlow
									gate = {
										{
											gatewayId: this.state.gatewayUuid,
											allowUnsignedRequests: this.state.allowUnsignedRequests
										}
									}
									onClickClosePopUp={() => this.setState({ showDemoOrderPopup: false })}
								/>
							</Fragment> :
							null
				}
			</div>
		)
	}
}

const mapState = (state: RootState) => ({
	gatewaysList: state.gateway.gatewaysList
});

const mapDispatch = {
	handleErrorAction: handleErrorAction,
	getGatewaysListAction: getGatewaysListAction
}

const connector = connect(mapState, mapDispatch);

export default connector(GatewayConstruct);