import React, { Component } from "react";
import { Table } from "react-bootstrap";
import { AccountsData } from "@cryptopay/dashboard-contract/models";
import LinkPermissions from "../../../common/components/LinkPermissions";
import { DashboardPermission } from "@cryptopay/dashboard-contract/models";

export enum AccountsColumn {
	ACCOUNT_ID = "ACCOUNT_ID",
	BALANCE = "BALANCE",
	CLIENT_ACCOUNT_ID = "CLIENT_ACCOUNT_ID",
	CREATED_AT = "CREATED_AT",
	CURRENCY = "CURRENCY",
	DEPOSIT_ADDRESS = "DEPOSIT_ADDRESS",
	//INCOME = "INCOME",
	DESCRIPTION = "DESCRIPTION",
	MERCHANT = "MERCHANT",
	NAME = "NAME",
	//ORDER_ID = "ORDER_ID",
	//OUTCOME = "OUTCOME",
	TAGS = "TAGS",
	//TRANSACTIONS = "TRANSACTIONS",	
	UPDATED_AT = "UPDATED_AT"
}

export const AccountsColumnsLabels: { [key in AccountsColumn]: string } = {
	NAME: "Name",
	ACCOUNT_ID: "Account Id",
	CLIENT_ACCOUNT_ID: "Client Account Id",
	//ORDER_ID: "Order Id",
	CURRENCY: "Currency",
	DESCRIPTION: "Description",
	//INCOME: "Income",
	//OUTCOME: "Outcome",
	BALANCE: "Balance",
	//TRANSACTIONS: "Transactions",
	MERCHANT: "Merchant",
	TAGS: "Tags",
	DEPOSIT_ADDRESS: "Deposit Address",
	CREATED_AT: "Created At",
	UPDATED_AT: "Updated At"
}

export const AccountsColumnsArray: Array<AccountsColumn> = [AccountsColumn.ACCOUNT_ID, AccountsColumn.CLIENT_ACCOUNT_ID,
AccountsColumn.NAME, AccountsColumn.CURRENCY, AccountsColumn.DESCRIPTION,
AccountsColumn.MERCHANT, AccountsColumn.TAGS, AccountsColumn.BALANCE,
AccountsColumn.CREATED_AT, AccountsColumn.UPDATED_AT,
AccountsColumn.DEPOSIT_ADDRESS
];

type State = {
}

type Props = {
	accounts: Array<AccountsData>;
	columns: Array<string>;
	permissions: DashboardPermission[];
}

export default class AccountsTable extends Component<Props, State> {

	render() {
		return (
			<Table responsive className="table-hover text-center">
				<thead>
					<tr>
						{
							this.props.columns.map((name, index) => {
								return (<th key={name}>{AccountsColumnsLabels[name as AccountsColumn]}</th>);
							})
						}
					</tr>
				</thead>
				<tbody>
					{
						this.props.accounts.map((account, index) => {
							return (<tr key={`account-${account.apiAccountId}-${index}`}>
								{
									this.props.columns.map((name, index) => {
										switch (name) {
											case AccountsColumn.ACCOUNT_ID:
												return (
													<td key={`${name}-${index}`}>
														<LinkPermissions
															permission={DashboardPermission.AccountView}
															to={`/account/${account.apiAccountId}`}
															label={account.apiAccountId !== null ? account.apiAccountId : ""}
														/>
													</td>);
											case AccountsColumn.BALANCE:
												return (<td key={`${name}-${index}`}>{account.balance}</td>);
											case AccountsColumn.CLIENT_ACCOUNT_ID:
												return (<td key={`${name}-${index}`}>{account.clientAccountId}</td>);
											case AccountsColumn.CREATED_AT:
												return (<td key={`${name}-${index}`}>{new Date(account.createdAt).toISOString()}</td>);
											case AccountsColumn.CURRENCY:
												return (<td key={`${name}-${index}`}>{account.currency}</td>);
											case AccountsColumn.DEPOSIT_ADDRESS:
												return (<td key={`${name}-${index}`}>{account.depositAddress}</td>);
											case AccountsColumn.DESCRIPTION:
												return (<td key={`${name}-${index}`}>{account.description}</td>);
											case AccountsColumn.MERCHANT:
												return (<td key={`${name}-${index}`}>{account.merchantName}</td>);
											case AccountsColumn.NAME:
												return (<td key={`${name}-${index}`}>{account.accountName}</td>);
											case AccountsColumn.TAGS:
												return (<td key={`${name}-${index}`}>{account.tags.toString()}</td>);
											case AccountsColumn.UPDATED_AT:
												return (<td key={`${name}-${index}`}>{account.updatedAt ? new Date(account.updatedAt).toISOString() : ""}</td>);
											default:
												console.log(`Cann't find view for column ${name}`);
												return null;
										}
									})
								}
							</tr>)
						})
					}
				</tbody>
			</Table>
		);
	}
}

