import React, { Component } from "react";
import { connect } from "react-redux";
import { Switch, Route, Redirect, Link, RouteComponentProps } from "react-router-dom";

import styles from "./index.module.scss";

import Gateway from "./gateway";
import General from "./general";
import Limits from "./limits";
import Subscriptions from "./subscriptions";
import Users from "./users";

import { RootState } from "../../../store/rootDuck";



//interface ParamsMerchantPath {
//	merchantId: string;
//}

type State = {

}

type Props = RouteComponentProps<{}> & {
}

export class Settings extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {};
	}

	componentDidMount() {
	}
	componentDidUpdate(prevProps: Props, prevState: State) {
	}

	parsePathToActiveMenu(): string | null {
		const pathArray = this.props.location.pathname.split("/");
		if (pathArray[1] !== "settings") {
			return null;
		}
		return pathArray[2];
	}

	render() {

		const activeMenu = this.parsePathToActiveMenu();

		return (
			<>
				<div className={styles.direction}>
					Settings
				</div>
				<div className={styles.nav_content}>
					<div className={activeMenu === "general" ? styles.nav_item_active : styles.nav_item}>
						<Link key="general" to="/settings/general">General</Link>
					</div>
					<div className={activeMenu === "limits" ? styles.nav_item_active : styles.nav_item}>
						<Link key="limits" to="/settings/limits">Limits And Commissions</Link>
					</div>
					<div className={activeMenu === "gateway" ? styles.nav_item_active : styles.nav_item}>
						<Link key="gateway" to="/settings/gateway">Gateway</Link>
					</div>
					<div className={activeMenu === "subscriptions" ? styles.nav_item_active : styles.nav_item}>
						<Link key="subscriptions" to="/settings/subscriptions">Subscriptions</Link>
					</div>
					<div className={activeMenu === "users" ? styles.nav_item_active : styles.nav_item}>
						<Link key="users" to="/settings/users">Users</Link>
					</div>
				</div>
				<div className={styles.content}>
					<Switch>
						<Route path="/settings/general" component={General} />
						<Route path="/settings/users" component={Users} />
						<Route path="/settings/limits" component={Limits} />
						<Route path="/settings/subscriptions" component={Subscriptions} />
						<Route path="/settings/gateway" component={Gateway} />
						<Route path="/settings">
							<Redirect to={`/settings/general`} />
						</Route>
					</Switch>
				</div>
			</>
		)
	}
}

const mapState = (state: RootState) => ({});

const mapDispatch = {

}

const connector = connect(mapState, mapDispatch);

export default connector(Settings);
