import React, { Component, Fragment } from "react";
import { Table, Button, ButtonGroup } from "react-bootstrap";
import * as models from "@cryptopay/dashboard-contract/models";

import styles from "./index.module.scss";

import { RootState } from "../../../../store/rootDuck";
import { connect } from "react-redux";
import { getMerchantLimitsAction, updateMerchantLimitAction } from "../../../../store/ducks/limits.duck";


type State = {
	editLimit: models.UpdateLimitData | null;
	limits: models.MerchantLimits | null;
}
type Props = {
	permissions: Array<models.DashboardPermission>;
	limits: models.MerchantLimits | null;
	getMerchantLimitsAction: typeof getMerchantLimitsAction;
	updateMerchantLimitAction: typeof updateMerchantLimitAction;
}

export class Limits extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {
			editLimit: null,
			limits: props.limits
		};
	}

	componentDidMount() {
		this.props.getMerchantLimitsAction();
	}
	componentDidUpdate(prevProps: Props, prevState: State) {
		if (this.props.limits !== prevProps.limits) {
			this.setState({ limits: this.props.limits });
		}
		this.props.getMerchantLimitsAction();
	}

	isEditMode = (instrumentName: string, modelName: string, pairName: string): boolean => {
		const editLimit = this.state.editLimit;
		if (editLimit !== null && editLimit.instrument === instrumentName && editLimit.model === modelName && editLimit.pair === pairName) {
			return true;
		}
		return false;
	}

	clickEditModeHandler = (instrumentName: string, modelName: string, pairName: string) => (event: any) => {
		const limits = this.state.limits;
		if (limits !== null) {
			const pair = (limits[instrumentName] && limits[instrumentName][modelName] && limits[instrumentName][modelName][pairName]) ? limits[instrumentName][modelName][pairName] : null;
			if (pair !== null) {
				this.setState({
					editLimit: {
						instrument: instrumentName,
						model: modelName,
						pair: pairName,
						pairData: pair
					}
				});
			}
		}

	}

	clickChangeLimitHandler = (instrumentName: string, modelName: string, pairName: string) => () => {
		const limits = this.state.limits;
		if (limits !== null) {
			const pairData = limits[instrumentName] && limits[instrumentName][modelName] && limits[instrumentName][modelName][pairName] ? limits[instrumentName][modelName][pairName] : null;
			if (pairData !== null) {
				this.setState({
					editLimit: {
						instrument: instrumentName,
						model: modelName,
						pair: pairName,
						pairData
					}
				});
			}

		}
	}

	clickUpdateLimitHandler = (instrumentName: string, modelName: string, pairName: string) => () => {
		const editLimit = this.state.editLimit;

		if (editLimit !== null && editLimit.instrument === instrumentName && editLimit.model === modelName && editLimit.pair === pairName) {
			this.props.updateMerchantLimitAction(editLimit);
			this.setState({
				editLimit: null
			})
		}
	}

	clickCancellUpdateLimitHandler = (instrumentName: string, modelName: string, pairName: string) => () => {
		this.setState({
			editLimit: null,
			limits: this.props.limits
		});
	}

	changeInputHandler = (key: string) => (event: any) => {
		const limits: models.MerchantLimits | null = this.state.limits ? { ...this.state.limits } : null;
		const editLimit = this.state.editLimit ? { ...this.state.editLimit } : null;
		const value = (event.target.value || event.target.value !== "-") ? event.target.value : null;
		if (limits !== null && editLimit !== null) {
			const instrumentName = editLimit.instrument;
			const instrument: models.Instrument | null = limits[instrumentName] ? { ...limits[instrumentName] } : null
			if (instrument !== null) {
				const modelName = editLimit.model;
				const model: models.Model | null = instrument[modelName] ? { ...instrument[modelName] } : null
				if (model !== null && editLimit.model === modelName) {
					const pairName = editLimit.pair;
					const pair: models.Pair | null = model[pairName] ? { ...model[pairName] } : null
					if (pair !== null && editLimit.pair === pairName) {
						switch (key) {
							case "expirationSeconds":
								pair.expirationSeconds = Number.parseInt(value); break;
							case "paidMinFactor":
								pair.paidMinFactor = value; break;
							case "paidMinFixed":
								pair.paidMinFixed = value; break;
							case "paidMaxFactor":
								pair.paidMaxFactor = value; break;
							case "paidMaxFixed":
								pair.paidMaxFixed = value; break;
							case "overpaidFactor":
								pair.overpaidFactor = value; break;
							case "overpaidFixed":
								pair.overpaidFixed = value; break;
							case "from.orderMin":
								pair.from.orderMin = value; break;
							case "from.orderMax":
								pair.from.orderMax = value; break;
							case "from.exchangeFeeFactor":
								pair.from.exchangeFeeFactor = value; break;
							case "from.exchangeFeeFixed":
								pair.from.exchangeFeeFixed = value; break;
							case "from.orderFeeFactor":
								pair.from.orderFeeFactor = value; break;
							case "from.orderFeeFixed":
								pair.from.orderFeeFixed = value; break;
							case "to.orderMin":
								pair.to.orderMin = value; break;
							case "to.orderMax":
								pair.to.orderMax = value; break;
							case "to.exchangeFeeFactor":
								pair.to.exchangeFeeFactor = value; break;
							case "to.exchangeFeeFixed":
								pair.to.exchangeFeeFixed = value; break;
							case "to.orderFeeFactor":
								pair.to.orderFeeFactor = value; break;
							case "to.orderFeeFixed":
								pair.to.orderFeeFixed = value; break;
						}
						model[pairName] = pair;
					} else {
						console.log("changeInputHandler, unknown pair", pairName);
						return;
					}
					instrument[modelName] = model;
				} else {
					console.log("changeInputHandler, unknown model", modelName);
					return;
				}
				limits[instrumentName] = instrument;
			} else {
				console.log("changeInputHandler, unknown instrument", instrumentName);
				return;
			}
			this.setState({ limits });
		} else {
			console.log("changeInputHandler, limits is not defined", limits);
			return;
		}
	}

	render() {
		const limits = this.props.limits;
		if (limits === null) {
			return null;
		}

		return (
			<div className={styles.merchant_limits}>
				<Table responsive className={`table-hover text-center`}>
					{
						Object.keys(limits).map((instrumentName) => {
							const instruments = limits[instrumentName];
							return (
								<Fragment key={instrumentName}>
										<thead>
											<tr>
												<th colSpan={14}>{instrumentName}</th>
											</tr>
										</thead>

										<tbody>
											{
												Object.keys(instruments).map((modelName) => {
													const models = instruments[modelName];
													return (
														<Fragment key={`${instrumentName}-${modelName}`}>
															{/*Start Instrument*/}
															<tr>
																<td colSpan={14}>Model: {modelName}</td>
															</tr>
															{
																Object.keys(models).map((pairName) => {
																	const pair = models[pairName];
																	return (
																		<Fragment key={`${instrumentName}-${modelName}-${pairName}`}>
																			{/* Start Pair */}
																			<tr>
																				<th colSpan={13} className={styles.ml_td_text_center}>{pair.from.currency}-{pair.to.currency}</th>
																				<td>
																					{
																						!this.isEditMode(instrumentName, modelName, pairName) ?
																							<Button onClick={this.clickChangeLimitHandler(instrumentName, modelName, pairName)} disabled={this.state.editLimit !== null} variant="outline-secondary">Change</Button> :
																							<ButtonGroup size="sm">
																								<Button onClick={this.clickUpdateLimitHandler(instrumentName, modelName, pairName)} variant="outline-secondary">Update</Button>
																								<Button onClick={this.clickCancellUpdateLimitHandler(instrumentName, modelName, pairName)} variant="outline-secondary">Cancel</Button>
																							</ButtonGroup>
																					}
																				</td>
																			</tr>
																			<tr>
																				<td rowSpan={2}>Currencies</td>
																				<td rowSpan={2}>Expiration Time</td>
																				<td rowSpan={2}>Order Min Amount</td>
																				<td rowSpan={2}>Order Max Amount</td>

																				<td colSpan={2}>Paid Min</td>
																				<td colSpan={2}>Paid Max</td>
																				<td colSpan={2}>Overpaid</td>
																				<td colSpan={2}>Exchange Fee</td>
																				<td colSpan={2}>Order Fee</td>
																			</tr>
																			<tr>
																				<td>Factor</td>
																				<td>Fixed</td>
																				<td>Factor</td>
																				<td>Fixed</td>
																				<td>Factor</td>
																				<td>Fixed</td>
																				<td>Factor</td>
																				<td>Fixed</td>
																				<td>Factor</td>
																				<td>Fixed</td>
																			</tr>
																			{/* Start pair.from and pair Limits */}
																			<tr className={styles.ml_tr}>
																				<td>{pair.from.currency}</td>
																				{/*EXPIRATION*/}
																				<td rowSpan={2} className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("expirationSeconds")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.expirationSeconds ? pair.expirationSeconds : "-"}
																					/>
																				</td>
																				{/*ORDER_MIN*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("from.orderMin")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.from.orderMin ? pair.from.orderMin : "-"}
																					/>
																				</td>
																				{/*ORDER_MAX*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("from.orderMax")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.from.orderMax ? pair.from.orderMax : "-"}
																					/>
																				</td>
																				{/*PAID_MIN*/}
																				<td rowSpan={2} className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("paidMinFactor")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.paidMinFactor ? pair.paidMinFactor : "-"}
																					/>
																				</td>
																				<td rowSpan={2} className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("paidMinFixed")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.paidMinFixed ? pair.paidMinFixed : "-"}
																					/>
																				</td>
																				{/*PAID_MAX*/}
																				<td rowSpan={2} className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("paidMaxFactor")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.paidMaxFactor ? pair.paidMaxFactor : "-"} />
																				</td>
																				<td rowSpan={2} className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("paidMaxFixed")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.paidMaxFixed ? pair.paidMaxFixed : "-"} />
																				</td>
																				{/*OVERPAID*/}
																				<td rowSpan={2} className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("overpaidFactor")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.overpaidFactor ? pair.overpaidFactor : "-"} />
																				</td>
																				<td rowSpan={2} className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("overpaidFixed")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.overpaidFixed ? pair.overpaidFixed : "-"} />
																				</td>
																				{/*EXCHANGE FEE*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("from.exchangeFeeFactor")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.from.exchangeFeeFactor ? pair.from.exchangeFeeFactor : "-"} />
																				</td>
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("from.exchangeFeeFixed")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.from.exchangeFeeFixed ? pair.from.exchangeFeeFixed : "-"} />
																				</td>
																				{/*ORDER FEE*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("from.orderFeeFactor")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.from.orderFeeFactor ? pair.from.orderFeeFactor : "-"} />
																				</td>
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input} onClick={this.changeInputHandler("from.orderFeeFixed")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.from.orderFeeFixed ? pair.from.orderFeeFixed : "-"} />
																				</td>
																			</tr>
																			{/* End First Limits */}
																			{/* Start pair.from and pair Limits */}
																			<tr>
																				<td>{pair.to.currency}</td>
																				{/*ORDER_MIN*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("to.orderMin")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.to.orderMin ? pair.to.orderMin : "-"} />
																				</td>
																				{/*ORDER_MAX*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("to.orderMax")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.to.orderMax ? pair.to.orderMax : "-"} />
																				</td>
																				{/*EXCHANGE FEE*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("to.exchangeFeeFactor")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.to.exchangeFeeFactor ? pair.to.exchangeFeeFactor : "-"} />
																				</td>
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("to.exchangeFeeFixed")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.to.exchangeFeeFixed ? pair.to.exchangeFeeFixed : "-"} />
																				</td>
																				{/*ORDER FEE*/}
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("to.orderFeeFactor")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.to.orderFeeFactor ? pair.to.orderFeeFactor : "-"} />
																				</td>
																				<td className={`${styles.ml_td_input} ${this.isEditMode(instrumentName, modelName, pairName) && styles.ml_td_active_input}`}>
																					<input type="text" className={styles.ml_input}
																						onClick={this.changeInputHandler("to.orderFeeFixed")}
																						disabled={!this.isEditMode(instrumentName, modelName, pairName)}
																						defaultValue={pair.to.orderFeeFixed ? pair.to.orderFeeFixed : "-"} />
																				</td>
																			</tr>
																			{/* End pair.to Limits */}
																			{/* End Pair */}
																		</Fragment>
																	)
																})
															}
														</Fragment>
													)
												})
											}
										</tbody>
								</Fragment>
							);
						})
					}
				</Table>
			</div>
		)
	}
}

const mapState = (state: RootState) => ({
	permissions: [],//state.auth.permissions,
	limits: state.limits.limits,
});

const mapDispatch = {
	getMerchantLimitsAction,
	updateMerchantLimitAction
}

const connector = connect(mapState, mapDispatch);

export default connector(Limits);