import React, { Component, ChangeEvent, SyntheticEvent } from "react";

import Autocomplete from '@material-ui/lab/Autocomplete';
import { TextField, Box } from "@material-ui/core";
import { KeyboardDatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import { Button, Row, Col, ButtonGroup } from "react-bootstrap";
import * as models from "@cryptopay/dashboard-contract/models";
import * as helper from "../../../../utils/helper";
import { cps } from "../../../../api/CryptoPayClient";
import * as dataToCsv from "../../../../utils/dataToCsv";
import * as dateUtil from "../../../../utils/dateUtil";


export type FilterForOrders = {
	merchant: models.FilterOrders["merchant"],
	instrument: models.FilterOrders["instrument"],
	status: models.FilterOrders["status"],
	state: models.FilterOrders["state"],
	currencyFrom: models.FilterOrders["currencyFrom"],
	currencyTo: models.FilterOrders["currencyTo"],
	apiOrderId: models.FilterOrders["apiOrderId"],
	clientOrderId: models.FilterOrders["clientOrderId"],
	cryptoAddress: models.FilterOrders["cryptoAddress"],
	dateFrom: models.FilterOrders["dateFrom"],
	dateTo: models.FilterOrders["dateTo"]
}

type State = {
	filter: FilterForOrders;
	downloadCsv: boolean;
};

type Props = {
	filter: models.FilterOrders;
	filterSettings: models.FilterSettingsOrders | null;
	changeFilter: (filter: FilterForOrders) => void
}

export default class OrdersFilter extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		this.state = {
			filter: props.filter,
			downloadCsv: false,
		}
	}

	componentDidMount() { }

	componentDidUpdate(prevProps: Props, prevState: State) {
		if (this.state.downloadCsv && prevState.downloadCsv !== this.state.downloadCsv) {
			this.downloadCsvHandler();
		}
		if (this.props.filter !== prevProps.filter) {
			this.setState ({filter: {...this.props.filter}});
		}
	}

	onChangeAutocompleteHandler = (name: string) => (event: ChangeEvent<{}>, value: any | null) => {
		if (value === null) {
			this.setState({ filter: Object.assign(this.state.filter, { [name]: undefined }) });
			return;
		}
		switch (name) {
			case "currencyTo":
			case "currencyFrom":
				this.setState({ filter: Object.assign(this.state.filter, { [name]: (value as helper.CurrencyPriority).value }) });
				break;
			default:
				this.setState({ filter: Object.assign(this.state.filter, { [name]: value }) });
		}

	}
	onChangeDateHandler = (name: string) => (date: Date | null) => {
		switch (name) {
			case "dateFrom":
				this.setState({ filter: Object.assign(this.state.filter, { [name]: date !== null ? dateUtil.startDay(date) : date }) });
				break;
			case "dateTo":
				this.setState({ filter: Object.assign(this.state.filter, { [name]: date !== null ? dateUtil.endDay(date) : date }) });
				break;
		}

	};

	onChangeTextFieldHandler = (name: string) => (event: SyntheticEvent) => {
		if (event.target instanceof HTMLInputElement) {
			this.setState({ filter: Object.assign(this.state.filter, { [name]: event.target.value }) });
		}
	};

	onClickExecuteFilterHandler = () => {
		this.props.changeFilter(this.state.filter);
	}

	onClickClearFilterHandler = () => {
		const cleanFilter: FilterForOrders = {
			merchant: undefined,
			instrument: undefined,
			status: undefined,
			state: undefined,
			currencyFrom: undefined,
			currencyTo: undefined,
			apiOrderId: undefined,
			clientOrderId: undefined,
			cryptoAddress: undefined,
			dateFrom: undefined,
			dateTo: undefined,
		};
		this.setState({ filter: cleanFilter });
		this.props.changeFilter(cleanFilter);
	}

	onClickDownloadCsvHandler = async () => {
		if (this.state.downloadCsv) {
			return;
		}
		this.setState({ downloadCsv: true });
	}

	downloadCsvHandler = async () => {
		try {
			const filter = this.props.filter;
			const result: Array<models.OrderCsvData> = [];
			let page = 1;
			let count = 1000;
			while (true) {
				const filterToRequest = {
					...filter,
					page,
					count
				};
				const data = await cps.getOrdersListForCsv(filterToRequest);
				if (data.length > 0) {
					result.push(...data);
				} else {
					break; // exit from loop when orders list is empty
				}
				page++;
			}
			await dataToCsv.downloadCSV<models.OrderCsvData>(result, { fields: dataToCsv.fieldsCsvForOrder }, `${Date.now()}.csv`);
		} catch (e) {
			console.log(e);
		} finally {
			this.setState({ downloadCsv: false });
		}
	}

	render() {

		const filter: FilterForOrders = this.state.filter;
		let filterSettings: models.FilterSettingsOrders;
		if (this.props.filterSettings === null) {
			// return error
			return null;
		}
		filterSettings = this.props.filterSettings;

		return (
			<MuiPickersUtilsProvider utils={DateFnsUtils}>
				<div className="mb-4">
					<Row className="align-items-center">
						<Col sm={2} xs={6}>
							<Autocomplete
								size="small"
								options={[...filterSettings.merchants]}
								renderInput={(params) => <TextField {...params} label="Merchant" variant="outlined" />}
								value={filter.merchant ? filter.merchant : null}
								onChange={this.onChangeAutocompleteHandler("merchant")}
							/>
						</Col>
						<Col sm={2} xs={6}>
							<Autocomplete
								size="small"
								options={[...filterSettings.instruments]}
								renderInput={(params) => <TextField {...params} label="Instrument" variant="outlined" />}
								value={filter.instrument ? filter.instrument : null}
								onChange={this.onChangeAutocompleteHandler("instrument")}
							/>
						</Col>
						<Col sm={2} xs={6}>
							<Autocomplete
								size="small"
								options={[...filterSettings.statuses]}
								renderInput={(params) => <TextField {...params} label="Status" variant="outlined" />}
								value={filter.status ? filter.status : null}
								onChange={this.onChangeAutocompleteHandler("status")}
							/>
						</Col>
						<Col sm={2} xs={6}>
							<Autocomplete
								size="small"
								options={[...filterSettings.states]}
								renderInput={(params) => <TextField {...params} label="State" variant="outlined" />}
								value={filter.state ? filter.state : null}
								onChange={this.onChangeAutocompleteHandler("state")}
							/>
						</Col>
						<Col sm={2} xs={6}>
							<Autocomplete
								size="small"
								options={helper.convertCurrenciesForAutocomplite([...filterSettings.currencyFrom]).sort((a, b) => { return a.priority - b.priority })}
								groupBy={(option) => option.type}
								getOptionLabel={(option) => option.value}
								renderInput={(params) => <TextField {...params} label="Currency from" variant="outlined" />}
								value={filter.currencyFrom ? helper.currenciesPriorityDict[filter.currencyFrom] : null}
								onChange={this.onChangeAutocompleteHandler("currencyFrom")}
							/>
						</Col>
						<Col sm={2} xs={4}>
							<Autocomplete
								size="small"
								options={helper.convertCurrenciesForAutocomplite([...filterSettings.currencyTo]).sort((a, b) => { return a.priority - b.priority })}
								groupBy={(option) => option.type}
								getOptionLabel={(option) => option.value}
								renderInput={(params) => <TextField {...params} label="Currency to" variant="outlined" />}
								value={filter.currencyTo ? helper.currenciesPriorityDict[filter.currencyTo] : null}
								onChange={this.onChangeAutocompleteHandler("currencyTo")}
							/>
						</Col>
					</Row>
				</div>
				<div className="mb-4">
					<Row className="align-items-center">
						<Col sm={3} xs={12}>
							<TextField
								id="outlined"
								fullWidth
								size="small"
								label="Order id"
								placeholder="Order id"
								onChange={this.onChangeTextFieldHandler("apiOrderId")}
								value={(filter.apiOrderId !== undefined) ? filter.apiOrderId : ''}
								variant="outlined"
							/>
						</Col>
						<Col sm={3} xs={12}>
							<TextField
								fullWidth
								size="small"
								label="Client order id"
								placeholder="Client order id"
								onChange={this.onChangeTextFieldHandler("clientOrderId")}
								value={(filter.clientOrderId !== undefined) ? filter.clientOrderId : ''}
								variant="outlined"
							/>
						</Col>
						<Col sm={2} xs={12}>
							<TextField
								fullWidth
								size="small"
								label="Crypto address"
								placeholder="Deposit or withdrawal address"
								onChange={this.onChangeTextFieldHandler("cryptoAddress")}
								value={(filter.cryptoAddress !== undefined) ? filter.cryptoAddress : ''}
								variant="outlined"
							/>
						</Col>
						<Col sm={2} xs={6}>
							<Box textAlign="center">
								<KeyboardDatePicker
									margin="none"
									variant="inline"
									label="Date from"
									format="dd/MM/yyyy"
									value={filter.dateFrom ? filter.dateFrom : null}
									onChange={this.onChangeDateHandler("dateFrom")}
									autoOk={true}
									KeyboardButtonProps={{
										'aria-label': 'change date',
									}}
								/>
							</Box>
						</Col>
						<Col sm={2} xs={6}>
							<Box textAlign="center">
								<KeyboardDatePicker
									margin="none"
									variant="inline"
									label="Date to"
									format="dd/MM/yyyy"
									value={filter.dateTo ? filter.dateTo : null}
									onChange={this.onChangeDateHandler("dateTo")}
									autoOk={true}
									KeyboardButtonProps={{
										'aria-label': 'change date',
									}}
								/>
							</Box>
						</Col>
					</Row>
				</div>
				<div>
					<Row className="align-items-center">
						<Col className="d-flex flex-row-reverse" sm={{ span: 4, offset: 8 }} xs={12}>
							<ButtonGroup size="sm">
								<Button onClick={this.onClickExecuteFilterHandler} variant="outline-success">Execute</Button>
								<Button onClick={this.onClickClearFilterHandler} variant="outline-warning">Clear filter</Button>
								<Button onClick={this.onClickDownloadCsvHandler} disabled={this.state.downloadCsv} variant="outline-secondary"> Download CSV </Button>
							</ButtonGroup>
						</Col>

					</Row>
				</div>
			</MuiPickersUtilsProvider>

		);
	}
}