import React, { Component, ChangeEvent } from "react";

import Autocomplete from '@material-ui/lab/Autocomplete';
import { TextField } from "@material-ui/core";
import { Button, Row, Col } from "react-bootstrap";
import ChipElement from "./ChipElement";

export interface MerchantsList {
	merchants: Array<string>
}

type State = {
	selectedMerchant?: string;
}

type Props = {
	merchants: MerchantsList["merchants"];
	filterMerchants: MerchantsList["merchants"];
	changeFilter: (filterPart: MerchantsList) => void;
}

export default class FilterByMerchant extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {}
	}

	onChangeMerchantInput = (event: ChangeEvent<{}>, value: string | null) => {
		this.setState({ selectedMerchant: value !== null ? value : undefined });
	}

	submitSelectedMerchant = (event: any) => {
		const selectedMerchant = this.state.selectedMerchant;
		const filterMerchants = this.props.filterMerchants.slice();
		if (selectedMerchant !== undefined) {
			if (filterMerchants.includes(selectedMerchant)) {
				return;
			}
			filterMerchants.push(selectedMerchant);
			this.props.changeFilter({ merchants: filterMerchants });
		}
	}

	onDeleteMerchantHandler = (merchant: string) => () => {
		let filterMerchants = this.props.filterMerchants;
		if (filterMerchants.includes(merchant)) {
			filterMerchants = filterMerchants.filter((item, index, array) => { return item !== merchant });
			this.props.changeFilter({ merchants: filterMerchants });
		}
	}
	onSelectAllMerchants = () => {
		this.props.changeFilter({ merchants: [] });
	}

	render() {
		const isFilterMerchantsEmpty = this.props.filterMerchants.length === 0;
		return (
			<div>
				<Row className="align-items-center">
					<Col sm={6} xs={12} className="my-1">
						{
							isFilterMerchantsEmpty &&
							<ChipElement
								label="Selected all"
								variant="disabled"
								className="m-2"
							/>
						}
						{
							!isFilterMerchantsEmpty &&
							<ChipElement
								label="Select all"
								onClick={this.onSelectAllMerchants}
								variant="delete"
								className="m-2"
							/>
						}
						{
							!isFilterMerchantsEmpty &&
							this.props.filterMerchants.map((merchant, index) => {
								return (
									<ChipElement
										key={merchant}
										label={merchant}
										onClick={this.onDeleteMerchantHandler(merchant)}
										variant="add"
										className="m-2"
									/>
								);
							})
						}
					</Col>
					<Col sm={4} xs={6} className="text-center my-1"> <Autocomplete
						size="small"
						options={this.props.merchants}
						renderInput={(params) => <TextField {...params} label="Select merchant" variant="outlined" />}
						onChange={this.onChangeMerchantInput}
					/>
					</Col>
					<Col sm={2} xs={6} className="text-center my-1">
						<Button onClick={this.submitSelectedMerchant} variant="outline-secondary">Add Merchant</Button>
					</Col>
				</Row>
			</div>
		);
	}
}