import React, { Component } from "react";
import { RouteComponentProps } from "react-router-dom";
import { ConnectedProps, connect } from "react-redux";

import { Accordion, Card, Row, Col } from "react-bootstrap";
import AccountsFilter, { FilterPartForAccounts } from "./components/AccountsFilter";
import AccountsTable, { AccountsColumnsLabels, AccountsColumnsArray } from "./components/AccountsTable";
import FilterByMerchant, { MerchantsList } from "../../common/components/FilterByMerchant";
import FilterByCurrency, { CurrenciesList } from "../../common/components/FilterByCurrency";
import PaginationActivity from "../../common/components/PaginationActivity";
import TableColumnSettings, { ColumnSettings } from "../../common/components/TableColumnSettings";

import { updateAccountsColumnsAction, getAccountsListAction, getAccountsSettingsAction } from "../../../store/ducks/accounts.duck";
import { RootState } from "../../../store/rootDuck";
import * as models from "@cryptopay/dashboard-contract/models";
import { PaginationData } from "../../common/components/PaginationActivity/PaginationActivity";

import * as helper from "../../../utils/helper";
import * as _ from "lodash";

type FilterPart = MerchantsList
	| CurrenciesList
	| PaginationData
	| FilterPartForAccounts;

type State = {
	filter: models.FilterAccounts;
}

type Props = RouteComponentProps<{}> &
	ConnectedProps<typeof connector> & {
		accounts: models.Accounts | null;
		columnSettings: ColumnSettings;
		filterSettings: models.FilterSettingsAccounts | null;
		permissions: Array<models.DashboardPermission>;
		
		getAccountsListAction: typeof getAccountsListAction,
		getAccountsSettingsAction: typeof getAccountsSettingsAction,
		updateAccountsColumnsAction: typeof updateAccountsColumnsAction
	}

export class Accounts extends Component<Props, State> {

	private readonly defaultFilter: models.FilterAccounts = {
		currencies: undefined,
		merchants: undefined,
		balanceCondition: undefined,
		apiAccountId: undefined,
		clientAccountId: undefined,
		tags: ["HOT", "USER", "FEE"],
		depositAddress: undefined,
		count: 25,
		page: 1
	};

	constructor(props: Props) {
		super(props);

		this.state = {
			filter: this.defaultFilter
		};
	}

	componentDidMount() {
		this.props.getAccountsSettingsAction();

		if (this.props.history.location.search !== "") {
			const filter = this.parseQueryParamsToFilter();
			this.setState({ filter });
		} else {
			this.props.history.replace(`/accounts?${helper.parseObjectToQueryParams(this.defaultFilter)}`);
			this.setState({ filter: { ...this.defaultFilter } });
		}
	}
	componentDidUpdate(prevProps: Props, prevState: State) {
		if (this.props.history.location.search === "") {
			// set default filter
			this.props.history.replace(`/accounts?${helper.parseObjectToQueryParams(this.defaultFilter)}`);
			this.setState({ filter: { ...this.defaultFilter } });
		} else {
			const filterByParams = this.parseQueryParamsToFilter();
			if (!_.isEqual(filterByParams, this.state.filter)) {
				this.setState({ filter: filterByParams });
			}
		}

		if (this.state.filter !== prevState.filter) {
			this.props.getAccountsListAction(this.state.filter);
		}
	}

	parseQueryParamsToFilter = (): models.FilterAccounts => {
		const searchParams = new URLSearchParams(this.props.history.location.search);

		const merchants = searchParams.get("merchants");
		const currencies = searchParams.get("currencies");

		const balanceCondition = searchParams.get("balanceCondition");
		const apiAccountId = searchParams.get("apiAccountId");
		const clientAccountId = searchParams.get("clientAccountId");
		const tags = searchParams.get("tags");
		const depositAddress = searchParams.get("depositAddress");
		const countStr = searchParams.get("count");
		const pageStr = searchParams.get("page");

		return {
			merchants: merchants !== null ? merchants.split(",") : undefined,
			currencies: currencies !== null ? currencies.split(",") : undefined,
			balanceCondition: balanceCondition !== null ? balanceCondition.split(",") : undefined,
			apiAccountId: apiAccountId !== null ? apiAccountId : undefined,
			//apiOrderId: apiOrderId !== null ? apiOrderId : undefined,
			clientAccountId: clientAccountId !== null ? clientAccountId : undefined,
			tags: tags !== null ? (tags).split(",") : this.defaultFilter.tags,
			depositAddress: depositAddress !== null ? depositAddress : undefined,
			count: (countStr !== null) ? Number.parseInt(countStr) : this.defaultFilter.count,
			page: (pageStr !== null) ? Number.parseInt(pageStr) : this.defaultFilter.page,
		}
	}

	changeDataFilterHandler = (
		filterPart: FilterPart
	) => {
		const filter = {
			...this.state.filter,
			...filterPart
		};
		this.props.history.push(`/accounts?${helper.parseObjectToQueryParams(filter)}`);
	}

	render() {
		return (
			<div>
				<Accordion defaultActiveKey="0" className="mb-4">
					<Card>
						<Card.Header>
							<Row className="text-center">
								<Col>
									<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} variant="link" eventKey="0" >
										<h6>Select Merchants</h6>
									</Accordion.Toggle>
								</Col>
								<Col>
									<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} variant="link" eventKey="1" >
										<h6>Select Currencies</h6>
									</Accordion.Toggle>
								</Col>
								<Col>
									<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} variant="link" eventKey="2" >
										<h6>Filter Assets</h6>
									</Accordion.Toggle>
								</Col>
								<Col>
									<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} variant="link" eventKey="3" >
										<h6>Column Settings</h6>
									</Accordion.Toggle>
								</Col>
							</Row>
						</Card.Header>
						<Accordion.Collapse eventKey="0">
							<Card.Body>
								<FilterByMerchant
									merchants={this.props.filterSettings !== null ? [...this.props.filterSettings.merchants] : []}
									filterMerchants={this.state.filter.merchants ? this.state.filter.merchants : []}
									changeFilter={this.changeDataFilterHandler}
								/>
							</Card.Body>
						</Accordion.Collapse>
						<Accordion.Collapse eventKey="1">
							<Card.Body>
								<FilterByCurrency
									currencies={this.props.filterSettings !== null ? [...this.props.filterSettings.currencies] : []}
									filterCurrencies={this.state.filter.currencies ? this.state.filter.currencies : []}
									changeFilter={this.changeDataFilterHandler}
								/>
							</Card.Body>
						</Accordion.Collapse>
						<Accordion.Collapse eventKey="2">
							<Card.Body>
								<AccountsFilter
									tagsSettings={this.props.filterSettings ? [...this.props.filterSettings.tags] : []}
									filter={this.state.filter}
									changeFilter={this.changeDataFilterHandler}
								/>
							</Card.Body>
						</Accordion.Collapse>
						<Accordion.Collapse eventKey="3">
							<Card.Body>
								<TableColumnSettings
									columns={AccountsColumnsArray}
									labels={AccountsColumnsLabels}
									activeColumns={this.props.columnSettings.activeColumns}
									updateActiveColumns={this.props.updateAccountsColumnsAction}
								/>
							</Card.Body>
						</Accordion.Collapse>
					</Card>
				</Accordion>

				<AccountsTable
					accounts={this.props.accounts !== null ? this.props.accounts.accounts : []}
					columns={this.props.columnSettings.activeColumns}
					permissions={this.props.permissions}
				/>
				<PaginationActivity
					countMax={this.props.accounts !== null ? this.props.accounts.countAccounts : 0}
					countOnPage={this.state.filter.count}
					page={this.state.filter.page}
					itemsLength={3}
					submitPagination={this.changeDataFilterHandler}
				/>
			</div>
		);
	}
}

const mapState = (state: RootState) => ({
	accounts: state.accounts.accounts,
	columnSettings: state.accounts.columnSettings,
	filterSettings: state.accounts.filterSettings,
	permissions: []//state.auth.permissions,
});

const mapDispatch = {
	getAccountsListAction,
	getAccountsSettingsAction,
	updateAccountsColumnsAction
}

const connector = connect(mapState, mapDispatch);

export default connector(Accounts);