import storage from "redux-persist/lib/storage";
import { put, call, takeEvery } from "redux-saga/effects";
import * as models from "@cryptopay/dashboard-contract/models";
import { cps } from "../../api/CryptoPayClient";
import { handleErrorAction } from "./general.duck";



/**
 * State of merchants
 */
export interface MerchantsState {
	settings: models.FilterSettingsMerchants | null;
	merchants: models.Merchants | null;
}

export const initialState: MerchantsState = {
	settings: null,
	merchants: null
}

// actions types:
export enum ActionTypes {
	CLEAR_MERCHANTS_LIST_STORAGE = "CLEAR_MERCHANTS_LIST_STORAGE",

	GET_MERCHANTS_LIST = "GET_MERCHANTS_LIST",
	GET_MERCHANTS_SETTINGS = "GET_MERCHANTS_SETTINGS",

	LOAD_MERCHANTS_LIST = "LOAD_MERCHANTS_LIST",
	LOAD_MERCHANTS_SETTINGS = "LOAD_MERCHANTS_SETTINGS",

};

export interface ClearMerchantsListAction {
	type: ActionTypes.CLEAR_MERCHANTS_LIST_STORAGE;
}
export function clearMerchantsListAction(): MerchantsActionTypes {
	return {
		type: ActionTypes.CLEAR_MERCHANTS_LIST_STORAGE
	}
}

export interface GetMerchantsListAction {
	type: ActionTypes.GET_MERCHANTS_LIST;
	payload: { filter: models.FilterMerchants }
}
export function getMerchantsListAction(filter: models.FilterMerchants): MerchantsActionTypes {
	return {
		type: ActionTypes.GET_MERCHANTS_LIST,
		payload: { filter }
	}
}

export interface GetMerchantsSettingsAction {
	type: ActionTypes.GET_MERCHANTS_SETTINGS;
}
export function getMerchantsSettingsAction(): MerchantsActionTypes {
	return {
		type: ActionTypes.GET_MERCHANTS_SETTINGS
	}
}


export interface LoadMerchantsListAction {
	type: ActionTypes.LOAD_MERCHANTS_LIST;
	payload: {
		merchants: models.Merchants | null
	}
}
export function loadMerchantsListAction (merchants: models.Merchants | null): MerchantsActionTypes {
	return {
		type: ActionTypes.LOAD_MERCHANTS_LIST,
		payload: { merchants }
	}
}

export interface LoadMerchantsSettingsAction {
	type: ActionTypes.LOAD_MERCHANTS_SETTINGS;
	payload: {
		settings: models.FilterSettingsMerchants | null
	}
}
export function loadMerchantsSettingsAction (settings: models.FilterSettingsMerchants | null): LoadMerchantsSettingsAction {
	return {
		type: ActionTypes.LOAD_MERCHANTS_SETTINGS,
		payload: { settings }
	}
}

export type MerchantsActionTypes = ClearMerchantsListAction 
	| GetMerchantsListAction 
	| GetMerchantsSettingsAction  
	| LoadMerchantsListAction
	| LoadMerchantsSettingsAction;

export function merchantsReducer(previousState = initialState, action: MerchantsActionTypes): MerchantsState {
	switch (action.type) {
		case ActionTypes.CLEAR_MERCHANTS_LIST_STORAGE:
			return initialState;
		case ActionTypes.LOAD_MERCHANTS_LIST:
			return {
				...previousState,
				merchants: action.payload.merchants
			}
		case ActionTypes.LOAD_MERCHANTS_SETTINGS:
			return {
				...previousState,
				settings: action.payload.settings
			}
		case ActionTypes.GET_MERCHANTS_LIST:
		case ActionTypes.GET_MERCHANTS_SETTINGS:
		default:
			return previousState;
	}
}

export const reducer = merchantsReducer;

export function* saga() {
	yield takeEvery(ActionTypes.GET_MERCHANTS_SETTINGS, workerGetMerchantsSettings);
	yield takeEvery(ActionTypes.GET_MERCHANTS_LIST, workerGetMerchantsList);
}

export function* workerGetMerchantsSettings(action: LoadMerchantsSettingsAction) {
	try {
		const settings = yield call(loadingSettings);
		yield put(loadMerchantsSettingsAction(settings));
	} catch (e) {
		yield put(loadMerchantsSettingsAction(null));
		yield put(handleErrorAction(null, e));
	}
	
}

export function* workerGetMerchantsList(action: GetMerchantsListAction) {
	try {
		const merchants = yield call(loadingMerchantsList, action.payload.filter);
		yield put(loadMerchantsListAction(merchants));
	} catch (e) {
		yield put(loadMerchantsListAction(null));
		yield put(handleErrorAction(null, e));
	}
}

export async function loadingSettings(): Promise<models.FilterSettingsMerchants> {
	return await cps.getMerchantsFilterSettings();
}

export async function loadingMerchantsList(filter: models.FilterMerchants): Promise<models.Merchants> {
	return await cps.getMerchantsList(filter);
}