import storage from "redux-persist/lib/storage";
import { put, call, takeEvery, select } from "redux-saga/effects";
import * as models from "@cryptopay/dashboard-contract/models";
import { cps } from '../../api/CryptoPayClient'
import { handleErrorAction } from "./general.duck";
import { GetMerchantsListAction } from "./merchants.duck";

export interface State {
	limits: models.MerchantLimits | null;
}

// actions
export enum ActionTypes {
	CLEAR_LIMITS_STORAGE = "CLEAR_LIMITS_STORAGE",

	GET_MERCHANT_LIMITS = "GET_MERCHANT_LIMITS",
	LOAD_MERCHANT_LIMITS = "LOAD_MERCHANT_LIMITS",
	UPDATE_MERCHANT_LIMIT = "UPDATE_MERCHANT_LIMIT"
};


export interface ClearLimitsStorageAction {
	type: typeof ActionTypes.CLEAR_LIMITS_STORAGE;
}
export function clearLimitsStorageAction(): LimitsActionTypes {
	return {
		type: ActionTypes.CLEAR_LIMITS_STORAGE
	};
}

export interface GetMerchantLimitsAction {
	type: typeof ActionTypes.GET_MERCHANT_LIMITS;
}
export function getMerchantLimitsAction(): LimitsActionTypes {
	return {
		type: ActionTypes.GET_MERCHANT_LIMITS
	};
}

export interface LoadMerchantLimitsAction {
	type: typeof ActionTypes.LOAD_MERCHANT_LIMITS;
	payload: { limits: models.MerchantLimits | null }
}
export function loadMerchantLimitsAction(limits: models.MerchantLimits | null): LimitsActionTypes {
	return {
		type: ActionTypes.LOAD_MERCHANT_LIMITS,
		payload: { limits }
	};
}

export interface UpdateMerchantLimitAction {
	type: typeof ActionTypes.UPDATE_MERCHANT_LIMIT;
	payload: { limitData: models.UpdateLimitData }
}
export function updateMerchantLimitAction(limitData: models.UpdateLimitData): LimitsActionTypes {
	return {
		type: ActionTypes.UPDATE_MERCHANT_LIMIT,
		payload: { limitData }
	};
}

export type LimitsActionTypes = ClearLimitsStorageAction
	| GetMerchantLimitsAction
	| LoadMerchantLimitsAction
	| UpdateMerchantLimitAction;

export const initialState: State = {
	limits: null
}

export function limitsReducer(previousState = initialState, action: LimitsActionTypes): State {
	switch (action.type) {
		case ActionTypes.CLEAR_LIMITS_STORAGE:
			return initialState;
		case ActionTypes.LOAD_MERCHANT_LIMITS:
			return {
				...previousState,
				limits: action.payload.limits
			}
		case ActionTypes.GET_MERCHANT_LIMITS:
		case ActionTypes.UPDATE_MERCHANT_LIMIT:
		default:
			return previousState;
	}
}

export const reducer = limitsReducer;

export function* saga() {
	yield takeEvery(ActionTypes.GET_MERCHANT_LIMITS, workerGetMerchantLimits);
	yield takeEvery(ActionTypes.UPDATE_MERCHANT_LIMIT, workerUpdateMerchantLimit);
}

export function* workerGetMerchantLimits(action: GetMerchantsListAction) {
	try {
		const limits = yield call(loadingMerchantLimits);
		yield put(loadMerchantLimitsAction(limits));
	} catch (e) {
		yield put(loadMerchantLimitsAction(null));
		yield put(handleErrorAction(null, e));
	}
}

export function* workerUpdateMerchantLimit(action: UpdateMerchantLimitAction) {
	try {
		yield call(updatingMerchantLimit, action.payload.limitData);
		// refresh limits
		yield put(getMerchantLimitsAction());
	} catch (e) {
		yield put(handleErrorAction(null, e));
	}
}

export async function loadingMerchantLimits(): Promise<models.MerchantLimits> {
	return await cps.getMerchantLimits();
}

export async function updatingMerchantLimit(limit: models.UpdateLimitData): Promise<void> {
	await cps.updateMerchantLimit(limit);
}