import storage from "redux-persist/lib/storage";
import { put, call, takeEvery } from "redux-saga/effects";
import { cps } from '../../api/CryptoPayClient'
import * as models from "@cryptopay/dashboard-contract/models";
import { ColumnSettings } from "../../pages/common/components/TableColumnSettings";
import { AccountsColumn } from "../../pages/home/accounts/components/AccountsTable";
import { handleErrorAction } from "./general.duck";

/**
 * State of Accounts
 */
export interface AccountsState {
	filterSettings: models.FilterSettingsAccounts | null;
	accounts: models.Accounts | null;
	columnSettings: ColumnSettings;
}

export const initialState: AccountsState = {
	filterSettings: null,
	accounts: null,
	columnSettings: {
		activeColumns: [AccountsColumn.ACCOUNT_ID, AccountsColumn.MERCHANT, AccountsColumn.CURRENCY,
			AccountsColumn.BALANCE, AccountsColumn.TAGS, AccountsColumn.DESCRIPTION
		]
	}
}

// actions types:
export enum ActionTypes {
	CLEAR_ACCOUNTS_LIST_STORAGE = "CLEAR_ACCOUNTS_LIST_STORAGE",

	GET_ACCOUNTS_LIST = "GET_ACCOUNTS_LIST",
	GET_ACCOUNTS_SETTINGS = "GET_ACCOUNTS_SETTINGS",

	LOAD_ACCOUNTS_LIST = "LOAD_ACCOUNTS_LIST",
	LOAD_ACCOUNTS_SETTINGS = "LOAD_ACCOUNTS_SETTINGS",

	UPDATE_ACCOUNTS_COLUMNS = "UPDATE_ACCOUNTS_COLUMNS",
};


export interface ClearAccountsListStorageAction {
	type: ActionTypes.CLEAR_ACCOUNTS_LIST_STORAGE;
}
export function clearAccountsListStorageAction(): AccountsListActionTypes {
	return {
		type: ActionTypes.CLEAR_ACCOUNTS_LIST_STORAGE
	}
}

export interface GetAccountsListAction {
	type: ActionTypes.GET_ACCOUNTS_LIST;
	payload: { filter: models.FilterAccounts }
}
export function getAccountsListAction(filter: models.FilterAccounts): AccountsListActionTypes {
	return {
		type: ActionTypes.GET_ACCOUNTS_LIST,
		payload: { filter }
	}
}

export interface GetAccountsSettingsAction {
	type: ActionTypes.GET_ACCOUNTS_SETTINGS;
}
export function getAccountsSettingsAction(): AccountsListActionTypes {
	return {
		type: ActionTypes.GET_ACCOUNTS_SETTINGS
	}
}

export interface LoadAccountsListAction {
	type: ActionTypes.LOAD_ACCOUNTS_LIST;
	payload: {
		accounts: models.Accounts | null
	}
}
export function loadAccountsListAction (accounts: models.Accounts | null): AccountsListActionTypes {
	return {
		type: ActionTypes.LOAD_ACCOUNTS_LIST,
		payload: { accounts }
	}
}

export interface LoadAccountsSettingsAction {
	type: ActionTypes.LOAD_ACCOUNTS_SETTINGS;
	payload: {
		settings: models.FilterSettingsAccounts | null
	}
}
export function loadAccountsSettingsAction (settings: models.FilterSettingsAccounts | null): AccountsListActionTypes {
	return {
		type: ActionTypes.LOAD_ACCOUNTS_SETTINGS,
		payload: { settings }
	}
}

export interface UpdateAccountsColumnsAction {
	type: ActionTypes.UPDATE_ACCOUNTS_COLUMNS;
	payload: {
		activeColumns: Array<string>;
	}
}
export function updateAccountsColumnsAction(activeColumns: Array<string>): AccountsListActionTypes {
	return {
		type: ActionTypes.UPDATE_ACCOUNTS_COLUMNS,
		payload: { activeColumns }
	}
}

export type AccountsListActionTypes = ClearAccountsListStorageAction
	| GetAccountsListAction
	| GetAccountsSettingsAction
	| LoadAccountsListAction
	| LoadAccountsSettingsAction
	| UpdateAccountsColumnsAction;

export function accountsReducer(previousState = initialState, action: AccountsListActionTypes): AccountsState {
	switch (action.type) {
		case ActionTypes.CLEAR_ACCOUNTS_LIST_STORAGE:
			return initialState;
		case ActionTypes.LOAD_ACCOUNTS_SETTINGS:
			return Object.assign({}, previousState, {
				filterSettings: action.payload.settings,
			});
		case ActionTypes.LOAD_ACCOUNTS_LIST:
			return Object.assign({}, previousState, {
				accounts: action.payload.accounts
			});
		case ActionTypes.UPDATE_ACCOUNTS_COLUMNS:
			return Object.assign({}, previousState, {
				columnSettings: { activeColumns: action.payload.activeColumns }
			});
		case ActionTypes.GET_ACCOUNTS_SETTINGS:
		case ActionTypes.GET_ACCOUNTS_LIST:
		default:
			return previousState;
	}
}

export const reducer = accountsReducer;

export function* saga() {
	yield takeEvery(ActionTypes.GET_ACCOUNTS_LIST, workerGetAccountsList);

	yield takeEvery(ActionTypes.GET_ACCOUNTS_SETTINGS, workerGetAccountsSettings);
}

export function* workerGetAccountsList(action: GetAccountsListAction) {
	try {
		const accounts: models.Accounts = yield call(loadingAccountsListData, action.payload.filter);
		yield put(loadAccountsListAction(accounts));
	} catch (e) {
		yield put(loadAccountsListAction(null));
		yield put(handleErrorAction(null, e));
	}
}

export function* workerGetAccountsSettings(action: GetAccountsSettingsAction) {
	try {
		const settings: models.FilterSettingsAccounts = yield call(loadingFilterSettings);
		yield put(loadAccountsSettingsAction(settings));
	} catch (e) {
		yield put(loadAccountsSettingsAction(null));
		yield put(handleErrorAction(null, e));
	}
}

export async function loadingFilterSettings(): Promise<models.FilterSettingsAccounts> {
	return await cps.getAccountsFilterSettings();
}

export async function loadingAccountsListData(filter: models.FilterAccounts): Promise<models.Accounts> {
	return await cps.getAccountsList(filter);
}