	//TODO: Should be used for query params generation
	public toQueryParams<E extends t.Any>(encoder: E, data: t.TypeOf<E>): string {

		const encoded = Typifier.encode(encoder, data, "query");

		const query = _.chain(encoded)
			.toPairs()
			.map(([k, v]) => `${encodeURIComponent(k)}=${encodeURIComponent(v.toString())}`)
			.value()
			.join("&");

		return query;
	}

