import React, { Component } from "react";
import { Col, Row, Container, Badge, ListGroup } from "react-bootstrap";
import { connect, ConnectedProps } from "react-redux";
import * as models from "@cryptopay/dashboard-contract/models";
import * as order from "../../../store/ducks/order.duck";
import { RouteComponentProps } from "react-router-dom";
import { RootState } from "../../../store/rootDuck";
import { DashboardPermission } from "@cryptopay/dashboard-contract/models";
import LinkPermissions from "../../common/components/LinkPermissions";

import { getHost } from '../../../api/CryptoPayClient';

interface ParamsOrderPath {
	orderId: string;
}

type Props = RouteComponentProps<ParamsOrderPath> &
	ConnectedProps<typeof connector> &
{
	permissions: Array<DashboardPermission>;
	orderData: models.WithdrawalOrder | models.DepositOrder | null;
	getOrder: typeof order.getOrderAction;
}
type State = {
}

export class Order extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		const params = props.match.params;
		props.getOrder(params.orderId);
	}

	viewBadgeForStatus = (status: string): "danger" | "warning" | "success" | undefined => {
		switch (status) {
			case 'REJECTED':
			case 'CANCELED':
				return 'danger';
			case 'PENDING':
			case 'SLEEPING':
				return 'warning';
			case 'ACCEPTED':
				return 'success';
			default:
				return undefined;
		}
	}

	render() {
		if (this.props.orderData === null) {
			return null;
		}

		const order = this.props.orderData;
		const withdrawal: undefined | models.WithdrawalData = (order as models.WithdrawalOrder).withdrawal;
		const deposit: undefined | models.DepositData = (order as models.DepositOrder).deposit;

		return (
			<Container className="mb-4">
				<h5 className="mb-4">{order.apiOrderId}</h5>
				<h6 className="ml-4">General</h6>
				<Container className="card mb-4">
					<Row className="mb-4 mt-4">
						<Col xs={6} md={5} className="mt-4 pl-4">
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Order Id</label>
									<div>{order.apiOrderId}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Client Order Id</label>
									<div>{order.clientOrderId}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Client Tag</label>
									<div>{order.clientOrderTag}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Merchant</label>
									<div>{order.merchant}</div>
								</div>
							</div>
						</Col>
						<Col xs={6} md={2} className="mt-4 pl-4">
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Currency From</label>
									<div>{order.from.currency}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Amount From</label>
									<div>{order.from.amount}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Currency To</label>
									<div>{order.to.currency}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Amount To</label>
									<div>{order.to.amount}</div>
								</div>
							</div>
						</Col>
						<Col xs={6} md={3} className="mt-4 pl-4">
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Created At</label>
									<div>{new Date(order.createdAt).toISOString()}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Last Updated At</label>
									<div>{new Date(order.updatedAt).toISOString()}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Expired At</label>
									<div>{order.expiredAt !== null && new Date(order.expiredAt).toISOString()}</div>
								</div>
								{
									order.rejectReason !== null &&
									<div className="mb-4">
										<label className="font-italic text-muted">Rejection Reason</label>
										<div>{order.rejectReason}</div>
									</div>
								}
							</div>
						</Col>
						<Col xs={6} md={2} className="mt-4 pl-4">
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Status</label>
									<div><Badge variant={this.viewBadgeForStatus(order.status)}>{order.status}</Badge></div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">State</label>
									<div>{order.state}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Instrument</label>
									<div>{order.instrument}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Model</label>
									<div>{order.model}</div>
								</div>
							</div>
						</Col>
					</Row>
				</Container>
				<h6 className="ml-4">Financial</h6>
				<Container className="card mb-4">
					<Row className="mb-4 mt-4">
						<Col xs={6} md={{ span: 4, offset: 1 }} className="mt-4 pl-4">
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Account From</label>
									<div>
										<LinkPermissions
											permission={DashboardPermission.AccountView}
											to={`/account/${order.from.accountId}`}
											label={order.from.accountId}
										/>
									</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Amount From</label>
									<div>{order.from.amount} {order.from.currency}</div>
								</div>
							</div>
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Account To</label>
									<div>
										<LinkPermissions
											permission={DashboardPermission.AccountView}
											to={`/account/${order.to.accountId}`}
											label={order.to.accountId}
										/>
									</div>
								</div>
								<div>
									<label className="font-italic text-muted">Amount To</label>
									<div>{order.to.amount} {order.to.currency}</div>
								</div>
							</div>
						</Col>
						<Col xs={6} md={{ span: 2, offset: 1 }} className="mt-4 pl-4">
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Symbol</label>
									<div>{order.rate.symbol}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Initial Rate</label>
									<div>{order.rate.initial}</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Exchange Rate</label>
									<div>{order.rate.exchange} 123</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Exchange Rate Fee</label>
									<div>1 %</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Exchange fee</label>
									<div>1</div>
								</div>
							</div>
						</Col>
						<Col xs={6} md={{ span: 3, offset: 1 }} className="mt-4 pl-4">
							<div>
								<div className="mb-4">
									<label className="font-italic text-muted">Percent Fee</label>
									<div> %</div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Fixed Fee</label>
									<div></div>
								</div>
								<div className="mb-4">
									<label className="font-italic text-muted">Order fee</label>
									<div></div>
								</div>
							</div>
						</Col>
					</Row>
				</Container>
				{
					withdrawal !== undefined &&
					<div>
						<h6 className="ml-4">Crypto Withdrawal Data</h6>
						<Container className="card mb-4">
							<Row className="mb-4 mt-4">
								<Col xs={12} md={{ span: 4, offset: 2 }} className="mt-4 pl-4">
									<div className="mb-4">
										<label className="font-italic text-muted">Network</label>
										<div>{withdrawal.network}</div>
									</div>
									<div className="mb-4">
										<label className="font-italic text-muted">Destination Address</label>
										<div><a href={withdrawal.addressExplorerUrl} target="_blank" rel="noopener noreferrer">{withdrawal.address}</a></div>
									</div>
								</Col>
								<Col xs={6} md={{ span: 4, offset: 2 }} className="mt-4 pl-4">
									<div className="mb-4">
										<label className="font-italic text-muted">Kind</label>
										<div>{withdrawal.kind}</div>
									</div>
									<div className="mb-4">
										<label className="font-italic text-muted">Network Fee Level</label>
										<div>{withdrawal.networkFeeLevel}</div>
									</div>
								</Col>
								<Col xs={12} md={12} className="mt-4 pl-4">
									<h6 className="text-muted">Transactions: </h6>
									<ListGroup variant="flush">
										{withdrawal.transactions.map((transaction, index) => {
											return (
												<ListGroup.Item key={`${index}-${transaction.txHash}`}>
													<Row>
														<Col xs={6} md={8}>
															<label className="font-italic text-muted">Hash: </label>
															<div className="mb-4"><a href={transaction.txHashExplorerUrl} target="_blank" rel="noopener noreferrer">{transaction.txHash}</a></div>
															<label className="font-italic text-muted">Withdrawal Id: </label>
															<div>{transaction.withdrawalId}</div>
														</Col>
														<Col xs={6} md={4}>
															<div><label className="font-italic text-muted">Status: </label> {transaction.status}</div>
															<div><label className="font-italic text-muted">Confirmations: </label> {transaction.confirmations}</div>
														</Col>
													</Row>
												</ListGroup.Item>
											);
										})}
									</ListGroup>
								</Col>
							</Row>
						</Container>
					</div>
				}
				{
					deposit !== undefined &&
					<div>
						<h6 className="ml-4">Crypto Deposit Data</h6>
						<Container className="card mb-4">
							<Row className="mb-4 mt-4">
								<Col xs={12} md={{ span: 4, offset: 1 }} className="mt-4 pl-4">
									<div>
										<div className="mb-4">
											<label className="font-italic text-muted">Destination Address</label>
											<div><a href={deposit.addressExplorerUrl} target="_blank" rel="noopener noreferrer">{deposit.address}</a></div>
										</div>
										<div className="mb-4">
											<label className="font-italic text-muted">Uri</label>
											<div>{deposit.uri}</div>
											<img src={`${getHost()}dashboard/payment-code/svg?payUri=${encodeURIComponent(deposit.uri)}`} alt="Payment QR Code" />
										</div>
									</div>
								</Col>
								<Col xs={6} md={{ span: 2, offset: 1 }} className="mt-4 pl-4">
									<div>
										<div className="mb-4">
											<label className="font-italic text-muted">Kind</label>
											<div>{deposit.kind}</div>
										</div>
										<div className="mb-4">
											<label className="font-italic text-muted">Amount</label>
											<div>{deposit.amount}</div>
										</div>
									</div>
								</Col>
								<Col xs={6} md={{ span: 3, offset: 1 }} className="mt-4 pl-4">
									<div>
										<div className="mb-4">
											<label className="font-italic text-muted">Network</label>
											<div>{deposit.network}</div>
										</div>
										{
											(order as models.DepositOrder).paidStatus &&
											<div className="mb-4">
												<label className="font-italic text-muted">Paid Status</label>
												<div>{(order as models.DepositOrder).paidStatus}</div>
											</div>
										}
									</div>
								</Col>
								<Col xs={12} md={12} className="mt-4 pl-4">
									<h6 className="text-muted">Transactions: </h6>
									<ListGroup variant="flush">
										{deposit.transactions.map((transaction, index) => {
											return (
												<ListGroup.Item key={`${index}-${transaction.txHash}`}>
													<Row>
														<Col xs={6} md={8}>
															<label className="font-italic text-muted">Hash: </label>
															<div className="mb-4"><a href={transaction.txHashExplorerUrl} target="_blank" rel="noopener noreferrer">{transaction.txHash}</a></div>
															<label className="font-italic text-muted">Deposit Id: </label>
															<div>{transaction.depositId}</div>
														</Col>
														<Col xs={6} md={4}>
															<div className="mb-4"><label className="font-italic text-muted">Amount: </label> {transaction.amount} {deposit.kind}</div>
															<div className="mb-4"><label className="font-italic text-muted">Status: </label> {transaction.status}</div>
															<div><label className="font-italic text-muted">Confirmations: </label> {transaction.confirmations}</div>
														</Col>
													</Row>
												</ListGroup.Item>
											);
										})}
									</ListGroup>
								</Col>
							</Row>
						</Container>
					</div>
				}
			</Container>
		);
	}
}

const mapState = (state: RootState) => ({
	orderData: state.order.orderData,
	permissions: []//state.auth.permissions
});

const mapDispatch = {
	getOrder: order.getOrderAction,
}

const connector = connect(mapState, mapDispatch);

export default connector(Order);
