import storage from "redux-persist/lib/storage";
import { put, call, takeEvery } from "redux-saga/effects";
import * as models from "@cryptopay/dashboard-contract/models";
import { cps } from '../../api/CryptoPayClient'
import { handleErrorAction } from "./general.duck";

export interface OrderState {
	orderData: models.DepositOrder | models.WithdrawalOrder | null;
}

// actions
export enum ActionTypes {
	CLEAR_ORDER_STORAGE = "CLEAR_ORDER_STORAGE",
	GET_ORDER = "GET_ORDER",
	LOAD_ORDER = "LOAD_ORDER"
};


export interface ClearOrderStorageAction {
	type: typeof ActionTypes.CLEAR_ORDER_STORAGE;
}
export function clearOrderStorageAction(): ClearOrderStorageAction {
	return {
		type: ActionTypes.CLEAR_ORDER_STORAGE
	};
}

export interface GetOrderAction {
	type: typeof ActionTypes.GET_ORDER;
	payload: { orderId: string }
}
export function getOrderAction(orderId: string): OrderActionTypes {
	return {
		type: ActionTypes.GET_ORDER,
		payload: { orderId }
	};
}

export interface LoadOrderAction {
	type: typeof ActionTypes.LOAD_ORDER;
	payload: { orderData: models.DepositOrder | models.WithdrawalOrder | null }
}
export function loadOrderAction(orderData: models.DepositOrder | models.WithdrawalOrder | null): OrderActionTypes {
	return {
		type: ActionTypes.LOAD_ORDER,
		payload: { orderData }
	};
}

export type OrderActionTypes = ClearOrderStorageAction | GetOrderAction | LoadOrderAction;

export const initialState: OrderState = {
	orderData: null
}

export function orderReducer(previousState = initialState, action: OrderActionTypes): OrderState {
	switch (action.type) {
		case ActionTypes.CLEAR_ORDER_STORAGE:
			return initialState;
		case ActionTypes.LOAD_ORDER: {
			const state = Object.assign({}, previousState)
			state.orderData = action.payload.orderData;
			return state;
		}
		case ActionTypes.GET_ORDER:
		default:
			return previousState;
	}
}

export const reducer = orderReducer;

export function* saga() {
	yield takeEvery(ActionTypes.GET_ORDER, workerGetOrder);
}

export function* workerGetOrder(action: GetOrderAction) {
	try {
		const order = yield call(loadingOrder, action.payload.orderId);
		yield put(loadOrderAction(order));
	} catch (e) {
		yield put(loadOrderAction(null));
		yield put(handleErrorAction(null, e));
	}

}

export async function loadingOrder(apiOrderId: string): Promise<models.DepositOrder | models.WithdrawalOrder> {
	return await cps.getOrder(apiOrderId);
}
