/**
 * Create React App entry point. This and `public/index.html` files can not be
 * changed or moved.
 */
import React from "react";
import ReactDOM from "react-dom";
import store, { persistor } from "./app/store/store";
import App from './App';
import "./index.scss"; // Standard version
// import "./sass/style.react.rtl.css"; // RTL version
// IE 11 polyfills
import "react-app-polyfill/ie11";
import "react-app-polyfill/stable";
// Fonts
//import "socicon/css/socicon.css";
//import "@fortawesome/fontawesome-free/css/all.min.css";
//import "./_metronic/_assets/plugins/line-awesome/css/line-awesome.css";
//import "./_metronic/_assets/plugins/flaticon/flaticon.css";
//import "./_metronic/_assets/plugins/flaticon2/flaticon.css";

/**
 * Inject metronic interceptors for axios.
 *
 * @see https://github.com/axios/axios#interceptors
 */
//setupAxios(axios, store);


if (process.env.REACT_APP_BUILD_CONFIGURATION === 'release') {
	// Release - is a full operation WITHOUT any delevelopment artifacts. In other words: IT IS FOR PRODUCTION
	ReactDOM.render(
		<App store={store} persistor={persistor} />,
		document.getElementById("root")
	);
} else if (process.env.REACT_APP_BUILD_CONFIGURATION === 'snapshot') {
	// Snapshot - is a full operation build with real API backend and allows to include devel features like performance tools, advanced logging etc.
	// This may is used
	// - ALWAYS, in evolution, presentation, test zones
	// - RARELY, in production zone to debug hard issues.
	ReactDOM.render(
		<App store={store} persistor={persistor} />,
		document.getElementById("root")
	);
} else if (process.env.REACT_APP_BUILD_CONFIGURATION === 'snapshot-mockup') {
	// Snapshot Mockup - is a backend less build for UI demo (uses mocked API Service)
	// This may is used as GitPages for the project
	ReactDOM.render(
		<App store={store} persistor={persistor} />,
		document.getElementById("root")
	);
} else if (process.env.REACT_APP_BUILD_CONFIGURATION === 'local') {
	// Local - is a build used in delevelopment purposes
	ReactDOM.render(
		<App store={store} persistor={persistor} />,
		document.getElementById("root")
	);
} else {
	throw new Error(`Unsupported value of REACT_APP_BUILD_CONFIGURATION variable: ${process.env.REACT_APP_BUILD_CONFIGURATION}`);
}
