const data = {
	labels: [
	  "Label 1",
	  "Label 2",
	  "Label 3",
	  "Label 4",
	  "Label 5",
	  "Label 6",
	  "Label 7",
	  "Label 8",
	  "Label 9",
	  "Label 10",
	  "Label 11",
	  "Label 12",
	  "Label 13",
	  "Label 14",
	  "Label 15",
	  "Label 16"
	],
	datasets: [
	  {
		// label: 'dataset 1',
		backgroundColor: "green",
		data: [15, 20, 25, 30, 25, 20, 15, 20, 25, 30, 25, 20, 15, 10, 15, 20]
	  },
	  {
		// label: 'dataset 2',
		backgroundColor: "#f3f3fb",
		data: [10, 12, 10, 30, 25, 20, 15, 20, 25, 30, 25, 20, 15, 10, 15, 20]
	  }
	]
  }

function createChartCountOrders(context: CanvasRenderingContext2D) {
	new Chart(context, {
		data,
		type: "bar",
		options: {
		  title: { display: false },
		  tooltips: {
			intersect: false,
			mode: "nearest",
			xPadding: 10,
			yPadding: 10,
			caretPadding: 10
		  },
		  legend: { display: false },
		  responsive: true,
		  maintainAspectRatio: false,
		//  barRadius: 4,
		  scales: {
			xAxes: [{ display: false, stacked: true }],
			yAxes: [{ display: false, stacked: true }]
		  },
		  layout: { padding: { left: 0, right: 0, top: 0, bottom: 0 } }
		}
	  })
}

function createChartTurnover(context: CanvasRenderingContext2D) {
	new Chart(context, {
		type: "line",
		data: {
			//Bring in data
			labels: ["Jan", "Feb", "March"],
			datasets: [
				{
					label: "Turnover",
					data: [86, 67, 91],
				}
			]
		},
		options: {
			//Customize chart options
		}
	});
}