import { Subscription, SubscriptionData, SubscriptionList, TopicInfoList } from "@cryptopay/dashboard-contract";
import storage from "redux-persist/lib/storage";
import { call, put,takeEvery } from "redux-saga/effects";
import { cps } from "../../api/CryptoPayClient";
import { RootState } from "../rootDuck";
import { handleErrorAction } from "./general.duck";

export interface State {
	notificationTopicList: TopicInfoList;
	subscriptionList: SubscriptionList;
}

export enum ActionTypes {
	LOAD_NOTIFICATION_TOPIC_LIST = "LOAD_NOTIFICATION_TOPIC_LIST",
	PUT_NOTIFICATION_TOPIC_LIST = "PUT_NOTIFICATION_TOPIC_LIST",
	LOAD_SUBSCRIPTION_LIST = "LOAD_SUBSCRIPTION_TOPIC_LIST",
	PUT_SUBSCRIPTION_LIST = "PUT_SUBSCRIPTION_TOPIC_LIST",
	CREATE_SUBSCRIPTION = "CREATE_SUBSCRIPTION",
	DELETE_SUBSCRIPTION = "DELETE_SUBSCRIPTION",
}

export const initialState: State = {
	notificationTopicList: [],
	subscriptionList: [],
}

export interface LoadNotificationTopicList {
	type: ActionTypes.LOAD_NOTIFICATION_TOPIC_LIST;
}

export interface PutNotificationTopicList {
	type: ActionTypes.PUT_NOTIFICATION_TOPIC_LIST;
	payload: TopicInfoList;
}

export interface LoadSubscriptionList {
	type: ActionTypes.LOAD_SUBSCRIPTION_LIST;
}

export interface PutSubscriptionList {
	type: ActionTypes.PUT_SUBSCRIPTION_LIST;
	payload: SubscriptionList;
}

export interface CreateSubscription {
	type: ActionTypes.CREATE_SUBSCRIPTION,
	payload: SubscriptionData,
}

export interface DeleteSubscription {
	type: ActionTypes.DELETE_SUBSCRIPTION,
	payload: string,
}

export function loadNotificationTopicList(): LoadNotificationTopicList {
	return {
		type: ActionTypes.LOAD_NOTIFICATION_TOPIC_LIST,
	}
}

export function putNotificationTopicList(topicList: TopicInfoList): PutNotificationTopicList {
	return {
		type: ActionTypes.PUT_NOTIFICATION_TOPIC_LIST,
		payload: topicList,
	}
}

export function loadSubscriptionList(): LoadSubscriptionList {
	return {
		type: ActionTypes.LOAD_SUBSCRIPTION_LIST,
	}
}

export function putSubscriptionList(subscriptionList: SubscriptionList): PutSubscriptionList {
	return {
		type: ActionTypes.PUT_SUBSCRIPTION_LIST,
		payload: subscriptionList,
	}
}

export function createSubscription(subscption: SubscriptionData): CreateSubscription {
	return {
		type: ActionTypes.CREATE_SUBSCRIPTION,
		payload: subscption,
	}
}

export function deleteSubscription(id: string): DeleteSubscription {
	return {
		type: ActionTypes.DELETE_SUBSCRIPTION,
		payload: id,
	}
}

export type CallbacksActionTypes = PutNotificationTopicList | PutSubscriptionList;

export function callbackReducer(previousState = initialState, action: CallbacksActionTypes) {
	switch (action.type) {
		case ActionTypes.PUT_NOTIFICATION_TOPIC_LIST: {
			return {
				...previousState,
				notificationTopicList: action.payload,
			}
		}
		case ActionTypes.PUT_SUBSCRIPTION_LIST: {
			return {
				...previousState,
				subscriptionList: action.payload,
			}
		}
		default: {
			return previousState;
		}
	}
}

export function* saga() {
	yield takeEvery(ActionTypes.LOAD_NOTIFICATION_TOPIC_LIST, fetchNotificationTopicList);
	yield takeEvery(ActionTypes.LOAD_SUBSCRIPTION_LIST, fetchSubscriptionList);
	yield takeEvery(ActionTypes.CREATE_SUBSCRIPTION, createingSubscription);
	yield takeEvery(ActionTypes.DELETE_SUBSCRIPTION, deletingSubscription);
}

function* fetchNotificationTopicList() {
	try {
		const topicList = yield call(loadingNotificationTopicList);
		yield put(putNotificationTopicList(topicList));
	} catch (e) {
		yield put(handleErrorAction(null, e));
	}
}

async function loadingNotificationTopicList() {
	return await cps.getNotificationTopicInfoList();
}

function* fetchSubscriptionList() {
	try {
		const subsciptionList = yield call(loadingSubscriptionList);
		yield put(putSubscriptionList(subsciptionList));
	} catch (e) {
		yield put(handleErrorAction(null, e));
	}
}

async function loadingSubscriptionList() {
	return await cps.getNotificationSubscriptionList();
}

function* createingSubscription({ payload }: CreateSubscription) {
	try {
		yield call(createsSubscription, payload);
		yield call(fetchSubscriptionList)
	} catch (e) {
		yield put(handleErrorAction(null, e));
	}
}

async function createsSubscription(subscription: SubscriptionData) {
	return await cps.createNotificationSubscription(subscription);
}

function* deletingSubscription({ payload }: DeleteSubscription) {
	try {
		yield call(deletesSubscription, payload);
		yield call(fetchSubscriptionList)
	} catch (e) {
		yield put(handleErrorAction(null, e));
	}
}

async function deletesSubscription(id: string) {
	console.log(id);
	return await cps.deleteNotificationSubscription(id as Subscription["subscriptionId"]);
}

export const reducer = callbackReducer;

export const getMerchantState = (state: RootState) => state;