import React, { Component } from "react";
import * as models from "@cryptopay/dashboard-contract/models";
import { RouteComponentProps } from "react-router-dom";
import { cps } from "../../../../api/CryptoPayClient";
import * as helper from "../../../../utils/helper";

interface ParamsOrderPath {
	orderId: string;
}

type Props = RouteComponentProps<ParamsOrderPath> &
{

}
type State = {
	data: any | null;
}

export default class OrderWorkflow extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		this.state = {
			data: null
		}
	}

	componentDidMount() {
		if (this.state.data === null) {
			try {
				const filter = this.parseQueryParamsToFilter(this.props);
				const params = this.props.match.params;

				cps.getOrderWorkflowTicks(params.orderId, filter).then((data) => {
					this.setState({ data });
					const json = JSON.stringify(data, null, 2);

					window.document.write('<html><body><pre>' + json + '</pre></body></html>');
					window.document.close();

				});

				if (this.props.history.location.search === "") {
					this.props.history.replace(`/order/${params.orderId}/workflow?${helper.parseObjectToQueryParams(filter)}`);
				}
			} catch (e) {
				console.log(e);
				this.setState({ data: null });
			}


		}

	}

	parseQueryParamsToFilter = (props: Props): models.FilterOrderWorkflowTick => {
		const searchParams = new URLSearchParams(props.history.location.search);

		const executedFromStr = searchParams.get("executedFrom");
		const executedToStr = searchParams.get("executedTo");
		const countStr = searchParams.get("count");
		const pageStr = searchParams.get("page");

		return {
			executedFrom: (executedFromStr !== null) ? new Date(executedFromStr) : undefined,
			executedTo: (executedToStr !== null) ? new Date(executedToStr) : undefined,
			count: (countStr !== null) ? Number.parseInt(countStr) : 100,
			page: (pageStr !== null) ? Number.parseInt(pageStr) : 1,
		}
	}

	render() {
		return (
			<div>{this.state.data !== null ? "Data loaded" : "Data didn't load"}</div>
		);
	}
}