function ParseUrlFromEnvs(envName: string): URL {
	const urlStr: string | undefined = process.env[envName];
	if (urlStr === undefined) { throw new Error(`An ${envName} is not set`); }
	const url = new URL(urlStr);
	if (url.pathname.endsWith("/")) {
		return url;
	} else {
		const normalizedUrl: URL = new URL(urlStr + "/");
		return normalizedUrl;
	}
}

export const CEXPAY_PROCESSING_V2_API_URL: URL = ParseUrlFromEnvs("REACT_APP_CEXPAY_PROCESSING_V2_API_URL");

