import * as helper from "../utils/helper";
import {
	Account,
	Accounts,
	CreateGatewayRequest,
	CreateGatewayResponse,
	GatewaysList,
	FilterAccounts,
	FilterMerchants,
	FilterOrderWorkflowTick,
	FilterOrders,
	FilterSettingsAccounts,
	FilterSettingsMerchants,
	FilterSettingsOrders,
	FilterTransactions,
	MerchantLimits,
	Merchants,
	Order,
	OrderWorkflowTicks,
	OrdersListCsv,
	Orders,
	Subscription,
	SubscriptionData,
	SubscriptionList,
	SubscriptionWithSecret,
	TopicInfoList,
	Transactions,
	UpdateGatewayRequest,
	UpdateLimitData, IDashboardApi, MerchantChildren
} from "@cryptopay/dashboard-contract";

import { DashboardApiBase } from "./DashboardApiBase";

export class DashboardApi extends DashboardApiBase implements IDashboardApi {

	public async changeGatewaySecrets(
		gatewayId: string
	): Promise<CreateGatewayResponse> {
		return await this.authCall(
			"POST",
			`dashboard/settings/gateway/${gatewayId}/secrets`,
			undefined,
			CreateGatewayResponse
		);
	}

	public async createGateway(
		body: CreateGatewayRequest
	): Promise<CreateGatewayResponse> {
		return this.authCall(
			"POST",
			`dashboard/settings/gateway`,
			body,
			CreateGatewayResponse
		);
	}

	public async createNotificationSubscription(
		data: SubscriptionData
	): Promise<SubscriptionWithSecret> {
		return await this.authCall(
			"POST",
			`dashboard/notification/subscription`,
			data,
			SubscriptionWithSecret,
			SubscriptionData
		);
	}

	public async deleteNotificationSubscription(
		subscriptionId: Subscription["subscriptionId"]
	): Promise<void> {
		await this.authCall(
			"DELETE",
			`dashboard/notification/subscription/${subscriptionId}`,
			undefined,
			undefined
		);
	}

	public async getAccount(
		apiAccountId: string
	): Promise<Account> {
		return this.authCall(
			"GET",
			`dashboard/account/${apiAccountId}`,
			undefined,
			Account
		);
	}

	public async getAccountsFilterSettings(): Promise<FilterSettingsAccounts> {
		return this.authCall(
			"GET",
			`dashboard/account/filter-settings`,
			undefined,
			FilterSettingsAccounts
		);
	}

	public async getAccountsList(
		filter: FilterAccounts
	): Promise<Accounts> {
		return this.authCall(
			"GET",
			`dashboard/account?${helper.parseObjectToQueryParams(filter)}`,
			undefined,
			Accounts
		);
	}

	public async getGatewaysList(): Promise<GatewaysList> {
		return this.authCall(
			"GET",
			`dashboard/settings/gateway`,
			undefined,
			GatewaysList
		);
	}

	public async getAuthMerchantChildren(): Promise<MerchantChildren> {
		return this.authCall(
			"GET",
			`dashboard/merchant/children`,
			undefined,
			MerchantChildren
		);
	}

	public async getMerchantLimits(): Promise<MerchantLimits> {
		return this.authCall(
			"GET",
			`dashboard/settings/limit`,
			undefined,
			MerchantLimits
		);
	}

	public async getMerchantsFilterSettings(): Promise<FilterSettingsMerchants> {
		return this.authCall(
			"GET",
			`dashboard/merchant/filter-settings`,
			undefined,
			FilterSettingsMerchants
		);
	}

	public async getMerchantsList(
		filter: FilterMerchants
	): Promise<Merchants> {
		return this.authCall(
			"GET",
			`dashboard/merchant?${helper.parseObjectToQueryParams(filter)}`,
			undefined,
			Merchants
		);
	}

	public async getNotificationTopicInfoList(): Promise<TopicInfoList> {
		return this.authCall(
			"GET",
			`dashboard/notification/topic`,
			undefined,
			TopicInfoList
		);
	}

	public async getNotificationSubscriptionList(): Promise<SubscriptionList> {
		return this.authCall(
			"GET",
			`dashboard/notification/subscription`,
			undefined,
			SubscriptionList
		);
	}

	public async getOrder(
		apiOrderId: string
	): Promise<Order> {
		return this.authCall(
			"GET",
			`dashboard/order/${apiOrderId}`,
			undefined,
			Order
		);
	}

	public async getOrderWorkflowTicks(
		apiOrderId: string,
		filter: FilterOrderWorkflowTick
	): Promise<OrderWorkflowTicks> {
		return this.authCall(
			"GET",
			`dashboard/order/${apiOrderId}/workflow?${helper.parseObjectToQueryParams(filter)}`,
			undefined,
			OrderWorkflowTicks
		);
	}

	public async getOrdersList(
		filter: FilterOrders
	): Promise<Orders> {
		return this.authCall(
			"GET",
			`dashboard/order?${helper.parseObjectToQueryParams(filter)}`,
			undefined,
			Orders
		);
	}

	public async getOrdersFilterSettings(): Promise<FilterSettingsOrders> {
		return this.authCall(
			"GET",
			`dashboard/order/filter-settings`,
			undefined,
			FilterSettingsOrders
		);
	}

	public async getOrdersListForCsv(
		filter: FilterOrders
	): Promise<OrdersListCsv> {
		return this.authCall("GET",
			`dashboard/order/download?${helper.parseObjectToQueryParams(filter)}`,
			undefined,
			OrdersListCsv
		);
	}

	public async getTransactionsList(
		apiAccountId: string,
		filter: FilterTransactions
	): Promise<Transactions> {
		return this.authCall(
			"GET",
			`dashboard/account/${apiAccountId}/transaction?${helper.parseObjectToQueryParams(filter)}`,
			undefined,
			Transactions
		);
	}

	public async updateGateway(
		gatewayId: string,
		body: UpdateGatewayRequest
	): Promise<void> {
		return this.authCall(
			"PUT",
			`dashboard/settings/gateway/${gatewayId}`,
			body
		);
	}

	public async updateMerchantLimit(
		limit: UpdateLimitData
	): Promise<MerchantLimits> {
		return this.authCall(
			"PUT",
			`dashboard/settings/limit`,
			limit
		);
	}
}
