import React, { Component } from "react";

import { Table, Badge } from "react-bootstrap";
import { DashboardPermission } from "@cryptopay/dashboard-contract/models";
import * as models from "@cryptopay/dashboard-contract/models";
import LinkPermissions from "../../../common/components/LinkPermissions";


export enum OrdersColumn {
	AMOUNT_FROM = "AMOUNT_FROM",
	AMOUNT_TO = "AMOUNT_TO",
	CLIENT_ORDER_ID = "CLIENT_ORDER_ID",
	CREATED_AT = "CREATED_AT",
	CURRENCY_FROM = "CURRENCY_FROM",
	CURRENCY_TO = "CURRENCY_TO",
	INSTRUMENT = "INSTRUMENT",
	FEE_TOTAL = "FEE_TOTAL",
	MERCHANT = "MERCHANT",
	ORDER_ID = "ORDER_ID",
	REJECT_REASON = "REJECT_REASON",
	STATE = "STATE",
	STATUS = "STATUS",
	UPDATED_AT = "UPDATED_AT"
}


export const OrdersColumnsLabels: { [column in OrdersColumn]: string } = {
	AMOUNT_FROM: "Amount From",
	AMOUNT_TO: "Amount To",
	CLIENT_ORDER_ID: "Client Order Id",
	CREATED_AT: "Created at",
	CURRENCY_FROM: "Currency From",
	CURRENCY_TO: "Currency To",
	INSTRUMENT: "Instrument",
	FEE_TOTAL: "Total Fee",
	MERCHANT: "Merchant",
	ORDER_ID: "Order Id",
	REJECT_REASON: "Rejection Reason",
	STATE: "State",
	STATUS: "Status",
	UPDATED_AT: "Updated At"
}

export const OrdersColumnsArray: Array<OrdersColumn> = [OrdersColumn.MERCHANT, OrdersColumn.ORDER_ID,
OrdersColumn.CLIENT_ORDER_ID, OrdersColumn.INSTRUMENT, OrdersColumn.STATE, OrdersColumn.STATUS,
OrdersColumn.AMOUNT_FROM, OrdersColumn.CURRENCY_FROM, OrdersColumn.AMOUNT_TO, OrdersColumn.CURRENCY_TO,
OrdersColumn.FEE_TOTAL, OrdersColumn.REJECT_REASON, OrdersColumn.CREATED_AT, OrdersColumn.UPDATED_AT
];

type State = {
}

type Props = {
	orders: models.Orders["orders"];
	activeColumns: Array<string>;
	permissions: DashboardPermission[];
}

export default class OrdersTable extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {
		}
	}

	viewBadgeForStatus = (status: string): "danger" | "warning" | "success" | undefined => {
		switch (status) {
			case 'REJECTED':
			case 'CANCELED':
				return 'danger';
			case 'PENDING':
			case 'SLEEPING':
				return 'warning';
			case 'ACCEPTED':
				return 'success';
			default:
				return undefined;
		}
	}

	render() {
		return (
			<Table responsive className="table-hover">
				<thead>
					<tr>
						{
							this.props.activeColumns.map((name, index) => {
								return (<th key={name}>{OrdersColumnsLabels[name as OrdersColumn]}</th>);
							})
						}
					</tr>
				</thead>
				<tbody>
					{
						this.props.orders.map((order, index) => {
							return (<tr key={`order-${index}`}>
								{
									this.props.activeColumns.map((name, index) => {
										switch (name) {
											case OrdersColumn.MERCHANT:
												return (<td key={`${name}-${index}`}>{order.merchant}</td>);
											case OrdersColumn.ORDER_ID:
												return (<td key={`${name}-${index}`}>
													<LinkPermissions
														permission={DashboardPermission.OrderView}
														to={`/order/${order.apiOrderId}`}
														label={order.apiOrderId}
													/>
												</td>);
											case OrdersColumn.CLIENT_ORDER_ID:
												return (<td key={`${name}-${index}`}>{order.clientOrderId}</td>);
											case OrdersColumn.INSTRUMENT:
												return (<td key={`${name}-${index}`}>{order.instrument}</td>);
											case OrdersColumn.CREATED_AT:
												return (<td key={`${name}-${index}`}>{order.createdAt.toISOString()}</td>);
											case OrdersColumn.UPDATED_AT:
												return (<td key={`${name}-${index}`}>{order.updatedAt !== null && order.updatedAt.toISOString()}</td>);
											case OrdersColumn.STATUS:
												return (<td key={`${name}-${index}`}><Badge variant={this.viewBadgeForStatus(order.status)}>{order.status}</Badge></td>);
											case OrdersColumn.STATE:
												return (<td key={`${name}-${index}`}>{order.state}</td>);
											case OrdersColumn.FEE_TOTAL:
												return (<td key={`${name}-${index}`}></td>);
											case OrdersColumn.REJECT_REASON:
												return (<td key={`${name}-${index}`}>{order.rejectReason}</td>);
											case OrdersColumn.CURRENCY_FROM:
												return (<td key={`${name}-${index}`}>{order.from.currency}</td>);
											case OrdersColumn.AMOUNT_FROM:
												return (<td key={`${name}-${index}`}>{order.from.amount}</td>);
											case OrdersColumn.CURRENCY_TO:
												return (<td key={`${name}-${index}`}>{order.to.currency}</td>);
											case OrdersColumn.AMOUNT_TO:
												return (<td key={`${name}-${index}`}>{order.to.amount}</td>);
											default:
												return null;
										}
									})
								}
							</tr>);
						})
					}
				</tbody>
			</Table>
		);
	}
}