import { DashboardPermission } from "@cryptopay/dashboard-contract/models";
import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";
import { put, takeEvery } from "redux-saga/effects";

export enum NavElement {
	ACCOUNTS = "ACCOUNTS",
	HOME = "HOME",
	MERCHANTS = "MERCHANTS",
	ORDERS = "ORDERS",
	SETTINGS = "SETTINGS"
}

export interface State {
	navElements: Array<NavElement>;
}

export enum ActionTypes {
	UPDATE_NAV_ELEMENTS = "UPDATE_NAV_ELEMENTS",
	SET_NAV_ELEMENT = "SET_NAV_ELEMENT"
}

export interface SetNavElementsAction {
	type: ActionTypes.SET_NAV_ELEMENT;
	payload: { navElements: Array<NavElement> }
}
export function setNavElementsAction(navElements: Array<NavElement>): LayoutActionTypes {
	return {
		type: ActionTypes.SET_NAV_ELEMENT,
		payload: { navElements }
	};
}

export interface UpdateNavElementsAction {
	type: ActionTypes.UPDATE_NAV_ELEMENTS;
	payload: { permissions: DashboardPermission[] }
}
export function updateNavElementsAction(permissions: DashboardPermission[]): LayoutActionTypes {
	return {
		type: ActionTypes.UPDATE_NAV_ELEMENTS,
		payload: { permissions }
	};
}



export type LayoutActionTypes = SetNavElementsAction | UpdateNavElementsAction;

const initialState = {
	navElements: new Array<NavElement>()
};

export function layoutReducer(previousState = initialState, action: LayoutActionTypes): State {
	switch (action.type) {
		case ActionTypes.SET_NAV_ELEMENT:
			return {
				...previousState,
				navElements: action.payload.navElements
			}
		case ActionTypes.UPDATE_NAV_ELEMENTS:
		default:
			return previousState;
	}
}

export const reducer = persistReducer({ storage, key: "layout" }, layoutReducer);

export function* saga() {
	yield takeEvery(ActionTypes.UPDATE_NAV_ELEMENTS, workerUpdateNavElements);
}

export function* workerUpdateNavElements(action: UpdateNavElementsAction) {
	const navElements: Array<NavElement> = new Array<NavElement>();

	for (const permission of action.payload.permissions) {
		switch (permission) {
			case DashboardPermission.AccountList:
				navElements.push(NavElement.ACCOUNTS);
				break;
			case DashboardPermission.MerchantList:
				navElements.push(NavElement.MERCHANTS);
				break;
			case DashboardPermission.MerchantView:
				navElements.push(NavElement.HOME);
				navElements.push(NavElement.SETTINGS);
				break;
			case DashboardPermission.OrderList:
				navElements.push(NavElement.ORDERS);
				break;
		}
	}

	yield put(setNavElementsAction(navElements));
}