import storage from "redux-persist/lib/storage";
import { put, call, takeEvery } from "redux-saga/effects";
import { cps } from '../../api/CryptoPayClient'
import * as models from "@cryptopay/dashboard-contract/models";
import { handleErrorAction } from "./general.duck";
import { RootState } from "../rootDuck";

/**
 * State of Accounts
 */
export interface AccountState {

	accountData: models.Account | null;
	transactionsFilter: models.FilterTransactions;
	selectedTransaction: any | null;
	transactions: models.Transactions | null;
}

export const initialState: AccountState = {
	accountData: null,
	transactionsFilter: {
		type: undefined,
		status: undefined,
		dateFrom: undefined,
		dateTo: undefined,
		count: 10,
		page: 1
	},
	transactions: null,
	selectedTransaction: null
}

// actions types:
export enum ActionTypes {
	CLEAR_ACCOUNT_STORAGE = "CLEAR_ACCOUNT_STORAGE",
	GET_ACCOUNT = "GET_ACCOUNT",
	GET_TRANSACTION = "GET_TRANSACTION",
	GET_TRANSACTIONS_LIST = "GET_TRANSACTIONS_LIST",
	LOAD_ACCOUNT = "LOAD_ACCOUNT",
	LOAD_TRANSACTION = "LOAD_TRANSACTION",
	LOAD_TRANSACTIONS_LIST = "LOAD_TRANSACTIONS_LIST"
};

export interface ClearAccountStorageAction {
	type: ActionTypes.CLEAR_ACCOUNT_STORAGE;
}
export function clearAccountStorageAction(): AccountActionTypes {
	return {
		type: ActionTypes.CLEAR_ACCOUNT_STORAGE
	}
}

export interface GetAccountAction {
	type: ActionTypes.GET_ACCOUNT;
	payload: { accountId: string }
}
export function getAccountAction(accountId: string): AccountActionTypes {
	return {
		type: ActionTypes.GET_ACCOUNT,
		payload: { accountId }
	}
}

export interface GetTransactionAction {
	type: ActionTypes.GET_TRANSACTION;
	payload: { transactionId: string }
}
export function getTransactionAction(transactionId: string): AccountActionTypes {
	return {
		type: ActionTypes.GET_TRANSACTION,
		payload: { transactionId }
	}
}

export interface GetTransactionsListAction {
	type: ActionTypes.GET_TRANSACTIONS_LIST;
	payload: { apiAccountId: string, filter: models.FilterTransactions }
}
export function getTransactionsListAction(
	apiAccountId: string,
	filter: models.FilterTransactions
): AccountActionTypes {
	return {
		type: ActionTypes.GET_TRANSACTIONS_LIST,
		payload: { apiAccountId, filter }
	}
}

export interface LoadAccountAction {
	type: ActionTypes.LOAD_ACCOUNT;
	payload: { account: models.Account | null }
}
export function loadAccountAction(
	account: models.Account | null
): AccountActionTypes {
	return {
		type: ActionTypes.LOAD_ACCOUNT,
		payload: { account }
	}
}

export interface LoadTransactionAction {
	type: ActionTypes.LOAD_TRANSACTION;
	payload: { transaction: any | null }
}
export function loadTransactionAction(
	transaction: any | null
): AccountActionTypes {
	return {
		type: ActionTypes.LOAD_TRANSACTION,
		payload: { transaction }
	}
}

export interface LoadTransactionsListAction {
	type: ActionTypes.LOAD_TRANSACTIONS_LIST;
	payload: {
		transactions: models.Transactions | null;
		filter: models.FilterTransactions;
	}
}
export function loadTransactionsListAction(
	transactions: models.Transactions | null,
	filter: models.FilterTransactions
): AccountActionTypes {
	return {
		type: ActionTypes.LOAD_TRANSACTIONS_LIST,
		payload: { transactions, filter }
	}
}

export type AccountActionTypes = ClearAccountStorageAction
	| GetAccountAction
	| GetTransactionAction
	| GetTransactionsListAction
	| LoadAccountAction
	| LoadTransactionAction
	| LoadTransactionsListAction;

export function accountReducer(previousState = initialState, action: AccountActionTypes): AccountState {
	switch (action.type) {
		case ActionTypes.CLEAR_ACCOUNT_STORAGE:
			return { ...initialState };
		case ActionTypes.LOAD_ACCOUNT:
			return {
				...previousState,
				accountData: action.payload.account
			}
		case ActionTypes.LOAD_TRANSACTIONS_LIST:
			return {
				...previousState,
				transactions: action.payload.transactions,
				transactionsFilter: action.payload.filter
			}
		case ActionTypes.GET_ACCOUNT:
		case ActionTypes.GET_TRANSACTION:
		case ActionTypes.GET_TRANSACTIONS_LIST:
		default:
			return previousState;
	}
}

export const reducer = accountReducer;

export function* saga() {
	yield takeEvery(ActionTypes.GET_ACCOUNT, workerGetAccount);
	yield takeEvery(ActionTypes.GET_TRANSACTION, workerGetTransaction);
	yield takeEvery(ActionTypes.GET_TRANSACTIONS_LIST, workerGetTransactionsList);
}

export function* workerGetAccount(action: GetAccountAction) {
	try {
		const account: models.Account = yield call(loadingAccount, action.payload.accountId);
		yield put(loadAccountAction(account));
	} catch (e) {
		yield put(loadAccountAction(null));
		yield put(handleErrorAction(null, e));
	}
}

export function* workerGetTransaction(action: GetTransactionAction) {
	try {
		throw new Error("get transaction not implemented yet!");
	} catch (e) {
		yield put(handleErrorAction("Get transaction action not implemented yet", e));
	}
}

export function* workerGetTransactionsList(action: GetTransactionsListAction) {
	try {
		const transactions: models.Transactions = yield call(loadingTransactionsList, action.payload.apiAccountId, action.payload.filter);
		yield put(loadTransactionsListAction(transactions, action.payload.filter));
	} catch (e) {
		yield put(loadTransactionsListAction(null, action.payload.filter));
		yield put(handleErrorAction(null, e));
	}
}

export async function loadingAccount(accountId: string): Promise<models.Account> {
	return await cps.getAccount(accountId);
}

export async function loadingTransactionsList(accountId: string, filter: models.FilterTransactions): Promise<models.Transactions> {
	return await cps.getTransactionsList(accountId, filter);
}

export const getAccountData = (state: RootState) => state.account.accountData;
export const getTransactionsFilter = (state: RootState) => state.account.transactionsFilter;