import React, { Component } from "react";
import { connect } from "react-redux";
import { Link, RouteComponentProps, withRouter } from "react-router-dom";

import Cookies from "js-cookie";

import styles from "./index.module.scss";

import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import { Dropdown, DropdownButton } from "react-bootstrap";
// import UserMenuPopover from "./UserMenuPopover";

import { RootState } from "../../store/rootDuck";
// import { UserState } from "../../store/ducks/auth.duck";
import { NavElement } from "../../store/ducks/layout.duck";

export type Props = RouteComponentProps<{}> & {
	// user: UserState | null;
	// merchantChildren: Array<string> | null;
	navElements: Array<NavElement>;
}

export type State = {
	activeMenu: NavElement | null;
}


export class NavbarMenu extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		const activeMenu = this.parsePathToActiveMenu(this.props.location.pathname);
		this.state = {
			activeMenu
		};
	}

	priorityNavElements: Array<NavElement> =
		[NavElement.HOME, NavElement.ACCOUNTS, NavElement.ORDERS, NavElement.MERCHANTS, NavElement.SETTINGS];

	componentDidUpdate(prevProps: Props) {
		if (this.props.location.pathname !== prevProps.location.pathname) {
			const newActiveMenu = this.parsePathToActiveMenu(this.props.location.pathname);
			if (newActiveMenu !== this.state.activeMenu) {
				this.setState({ activeMenu: newActiveMenu });
			}
		}
	}

	parsePathToActiveMenu(pathname: string): NavElement | null {
		const pathArray = this.props.location.pathname.split("/");
		switch (pathArray[1]) {
			case "merchant":
				return NavElement.HOME;
			case "orders":
				return NavElement.ORDERS;
			case "merchants":
				return NavElement.MERCHANTS;
			case "accounts":
				return NavElement.ACCOUNTS;
			case "settings":
				return NavElement.SETTINGS;
			default:
				return null;
		}
	}

	render() {

		const impersonateMerchant: string | undefined = Cookies.get("XSRF-TOKEN");

		return (
			<>
				<div className={`d-flex justify-content-center align-items-center ${styles.brand_div}`}>
					<div>
						<img
							alt="Logo"
							src="images/logo_white_41x48.png"
						/>
					</div>
				</div>
				<Navbar className={`${styles.navbar_menu}`} expand="md">
					<Navbar.Toggle aria-controls="basic-navbar-nav" />
					<Navbar.Collapse id="basic-navbar-nav">
						<Nav className="mr-auto">
							<Link className={`${styles.nav_link} ${this.state.activeMenu === NavElement.ORDERS ? styles.nav_link_active : styles.nav_link_passive}`} to="/orders">Orders</Link>
							<a className={`${styles.nav_link} ${styles.nav_link_passive}`} href="https://dashboard-next.cryptopay.band/" target="_blank">Manage</a>
						</Nav>
					</Navbar.Collapse>
					<Navbar.Collapse className="justify-content-end">
						{/* <DropdownButton
							alignRight
							title={this.props.impersonateMerchant ? this.props.impersonateMerchant : "Select Merchant"}
							className={styles.dropdown_merchant}
						>
							{
								this.props.merchantChildren !== null &&
								this.props.merchantChildren.map((value, index) => {
									return <Dropdown.Item key={`${index}-${value}`} className={styles.dropdown_item}>{value}</Dropdown.Item>
								})
							}
						</DropdownButton> */}
					</Navbar.Collapse>

				</Navbar>
			</>
		);
	}

}

const mapState = (state: RootState) => ({
	navElements: state.layout.navElements
});

const mapDispatch = {
}

const connector = connect(mapState, mapDispatch);

export default withRouter(connector(NavbarMenu));

