import React, { Component, ChangeEvent } from "react";

import { TextField } from "@material-ui/core";
import Autocomplete from '@material-ui/lab/Autocomplete';
import { Button, Row, Col } from "react-bootstrap";
import ChipElement from "./ChipElement";

import * as helper from "../../../utils/helper";

export interface CurrenciesList {
	currencies: Array<string>
}

type State = {
	selectedCurrency?: string;
}

type Props = {
	currencies: CurrenciesList["currencies"];
	filterCurrencies: CurrenciesList["currencies"];
	changeFilter: (filterPart: CurrenciesList) => void;
}

export default class FilterByCurrency extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {}
	}

	onChangeCurrencyAutocomplete = (event: ChangeEvent<{}>, value: helper.CurrencyPriority | null) => {
		if (value !== null) {
			this.setState({ selectedCurrency: value.value });
		}
	}

	submitSelectedCurrency = (event: any) => {
		const selectedCurrency = this.state.selectedCurrency;
		const filterCurrencies = this.props.filterCurrencies.slice();
		if (selectedCurrency !== undefined) {
			if (filterCurrencies.includes(selectedCurrency)) {
				return;
			}
			filterCurrencies.push(selectedCurrency);
			this.props.changeFilter({ currencies: filterCurrencies });
		}
	}

	onDeleteCurrencyHandler = (currency: string) => () => {
		let filterCurrencies = this.props.filterCurrencies;
		if (filterCurrencies.includes(currency)) {
			filterCurrencies = filterCurrencies.filter((item, index, array) => { return item !== currency });
			this.props.changeFilter({ currencies: filterCurrencies });
		}
	}
	onSelectAllCurrencies = () => {
		this.props.changeFilter({ currencies: [] });
	}

	render() {
		const isFilterCurrenciesEmpty = this.props.filterCurrencies.length === 0;
		return (
			<div>
				<Row className="align-items-center">
					<Col sm={6} xs={12} className="my-1">
						{
							isFilterCurrenciesEmpty &&
							<ChipElement
								label="Selected all"
								variant="disabled"
								className="m-2"
							/>
						}
						{
							!isFilterCurrenciesEmpty &&
							<ChipElement
								label="Select all"
								onClick={this.onSelectAllCurrencies}
								variant="delete"
								className="m-2"
							/>
						}
						{
							!isFilterCurrenciesEmpty &&
							this.props.filterCurrencies.map((currency, index) => {
								return (
									<ChipElement
										key={currency}
										label={currency}
										onClick={this.onDeleteCurrencyHandler(currency)}
										variant="add"
										className="m-2"
									/>
								);
							})
						}
					</Col>
					<Col sm={4} xs={6} className="text-center my-1">
						<Autocomplete
							size="small"
							options={helper.convertCurrenciesForAutocomplite(this.props.currencies).sort((a, b) => { return a.priority - b.priority })}
							groupBy={(option) => option.type}
							getOptionLabel={(option) => option.value}
							renderInput={(params) => <TextField {...params} label="Select Currency" variant="outlined" />}
							onChange={this.onChangeCurrencyAutocomplete}
						/>
					</Col>
					<Col sm={2} xs={6} className="text-center my-1">
						<Button onClick={this.submitSelectedCurrency} variant="outline-secondary">Add Currency</Button>
					</Col>
				</Row>
			</div>
		);
	}
}

