import React, { Component } from "react";

import DateFnsUtils from "@date-io/date-fns";
import { Box, Link } from "@material-ui/core";
import { KeyboardDatePicker, MuiPickersUtilsProvider } from "@material-ui/pickers";
import { Row, Col, Button } from "react-bootstrap";
import * as dateUtil from "../../../utils/dateUtil";
import { handleErrorAction } from "../../../store/ducks/general.duck";

export interface DateFilter {
	dateFrom?: Date;
	dateTo?: Date;
}

type State = DateFilter & {

};

type Props = DateFilter & {
	changeFilter: (filterPart: DateFilter) => void;
	isDateRequired?: boolean;
	errorHandler?: typeof handleErrorAction;
};

export default class FilterByDate extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		this.state = {
			dateFrom: props.dateFrom,
			dateTo: props.dateTo
		}
	}

	static getDerivedStateFromProps (nextProps: Props, prevState: State) {
		return ({
			dateFrom: nextProps.dateFrom,
			dateTo: nextProps.dateTo
		})
	}

	chooseLazyDateHandler = (dateFrom: Date, dateTo: Date) => (): void => {
		this.props.changeFilter({ dateFrom, dateTo });
	}

	changeDateHandler = (dateName: keyof State) => (date: Date | null): void => {
		switch (dateName) {
			case "dateFrom":
				this.setState ({ dateFrom: date !== null ? date : undefined });
				return;
			case "dateTo":
				this.setState ({ dateTo: date !== null ? date : undefined })
				return;
		}
	}

	clickDateFilterHandler = (): void => {

		if (this.props.isDateRequired) {
			if (!this.state.dateFrom || !this.state.dateTo) {
				if (this.props.errorHandler) {
					this.props.errorHandler ('Invalid date. "Date From" and "Date To" are required fields', null);
				} else {
					console.error ('Invalid date. "Date From" and "Date To" are required fields');
				}
				return;
			}
		}

		this.props.changeFilter({
			dateFrom: this.state.dateFrom,
			dateTo: this.state.dateTo
		});
	}

	render() {
		return (
			<MuiPickersUtilsProvider utils={DateFnsUtils}>
				<Row className="text-center">
					<Col sm={6} xs={12} className="my-1">
						<Box display="flex" justifyContent="center" alignItems="center">
							<KeyboardDatePicker
								margin="none"
								variant="inline"
								label="Date from"
								format="dd/MM/yyyy, HH:mm"
								value={this.state.dateFrom}
								onChange={this.changeDateHandler("dateFrom")}
								autoOk={true}
								KeyboardButtonProps={{
									'aria-label': 'change date',
								}}
								className="mx-2"
							/>
							<KeyboardDatePicker
								margin="none"
								variant="inline"
								label="Date to"
								format="dd/MM/yyyy, HH:mm"
								value={this.state.dateTo}
								onChange={this.changeDateHandler("dateTo")}
								autoOk={true}
								KeyboardButtonProps={{
									'aria-label': 'change date',
								}}
								className="mx-2"
							/>
							<Button onClick={this.clickDateFilterHandler} variant="outline-secondary" className="mx-2">Set date</Button>
						</Box>

					</Col>
					<Col sm={6} xs={12} className="my-1">
						<ul className="text-center mb-0 pl-0">
							<ul className="list-inline">
								<li className="list-inline-item mx-2">
									<Link className="h6" onClick={this.chooseLazyDateHandler(dateUtil.startDay(), dateUtil.endDay())}>Today </Link>
								</li>
								<li className="list-inline-item mx-2">
									<Link className="h6" onClick={this.chooseLazyDateHandler(dateUtil.dayToDate(), dateUtil.endDay())}>Last 24 hour</Link>
								</li>
								<li className="list-inline-item mx-2">
									<Link className="h6" onClick={this.chooseLazyDateHandler(dateUtil.lastMonth(), dateUtil.endDay())}>This month</Link>
								</li>
								<li className="list-inline-item mx-2">
									<Link className="h6" onClick={this.chooseLazyDateHandler(dateUtil.lastYear(), dateUtil.endDay())}>This year</Link>
								</li>
							</ul>
							<ul className="list-inline">
								<li className="list-inline-item mx-2">
									<Link className="h6" onClick={this.chooseLazyDateHandler(dateUtil.weekToDate(), dateUtil.endDay())}>Week to date</Link>
								</li>
								<li className="list-inline-item mx-2">
									<Link className="h6" onClick={this.chooseLazyDateHandler(dateUtil.monthToDate(), dateUtil.endDay())}>Month to date</Link>
								</li>
								<li className="list-inline-item mx-2">
									<Link className="h6" onClick={this.chooseLazyDateHandler(dateUtil.yearToDate(), dateUtil.endDay())}>Year to date</Link>
								</li>
							</ul>
						</ul>
					</Col>
				</Row>
			</MuiPickersUtilsProvider >
		);
	}
}