/**
 * Entry application component used to compose providers and render Routes.
 *
 * Note: Because of
 */

import React from "react";
import { Provider } from "react-redux";
import { BrowserRouter } from "react-router-dom";
import { PersistGate } from "redux-persist/integration/react";
import { LastLocationProvider } from "react-router-last-location";
import { Routes } from "./app/router/Routes";
import { LayoutSplashScreen } from "./app/layout/LayoutContext";
import I18nProvider from "./app/i18n/I18nProvider";
import InitialLayout from "./app/layout/InitialLayout";

export default function App({ store, persistor }: { store: any, persistor: any/*, basename: string */ }) {

	return (
		/* Provide Redux store */
		/* Provider has no property loading, so I have remove it and nothing changed - maybe some day we will foundout what was that. */
		<Provider store={store} /*loading={<LayoutSplashScreen />}*/>
			{/* Asynchronously persist redux stores and show `SplashScreen` while it's loading. */}
			<PersistGate persistor={persistor}>
				{/* Add high level `Suspense` in case if was not handled inside the React tree. */}
				<React.Suspense fallback={<LayoutSplashScreen />}>
					<InitialLayout appVersion={process.env.REACT_APP_VERSION}>
						{/* Override `basename` (e.g: `homepage` in `package.json`) */}
						<BrowserRouter basename={process.env.PUBLIC_URL}>
							{/*This library only returns the location that has been active before the recent location change in the current window lifetime.*/}
							<LastLocationProvider>
								{/* Provide Metronic theme overrides. */}
								{/*<ThemeProvider>*/}
								{/* Provide `react-intl` context synchronized with Redux state.  */}
								<I18nProvider>
									{/* Render routes with provided `Layout`. */}
									<Routes />
								</I18nProvider>
								{/*</ThemeProvider>*/}
							</LastLocationProvider>
						</BrowserRouter>
					</InitialLayout>
				</React.Suspense>
			</PersistGate>
		</Provider>
	);
}
