import { put, call, takeEvery, select } from "redux-saga/effects";
import * as models from "@cryptopay/dashboard-contract/models";
import { cps } from '../../api/CryptoPayClient';
import { handleErrorAction } from "./general.duck";

export interface State {
	gatewaysList: models.GatewaysList | null;
}

// actions
export enum ActionTypes {
	CLEAR_GATEWAY_STORAGE = "CLEAR_GATEWAY_STORAGE",

	//CREATE_GATEWAY = "CREATE_GATEWAY",

	GET_GATEWAYS_LIST = "GET_GATEWAYS_LIST",

	LOAD_GATEWAYS_LIST = "LOAD_GATEWAYS_LIST",

	SWITCH_GATEWAY = "SWITCH_GATEWAY"
};

export interface ClearGatewayStorageAction {
	type: typeof ActionTypes.CLEAR_GATEWAY_STORAGE;
}
export function clearGatewayStorageAction(): GatewayActionTypes {
	return {
		type: ActionTypes.CLEAR_GATEWAY_STORAGE
	};
}


//export interface CreateGatewayAction {
//	type: typeof ActionTypes.CREATE_GATEWAY;
//	payload: { gateway: models.CreateGatewayRequest }
//}
//export function createGatewayAction(gateway: models.CreateGatewayRequest): GatewayActionTypes {
//	return {
//		type: ActionTypes.CREATE_GATEWAY,
//		payload: { gateway }
//	};
//}

export interface GetGatewaysListAction {
	type: typeof ActionTypes.GET_GATEWAYS_LIST;
}
export function getGatewaysListAction(): GatewayActionTypes {
	return {
		type: ActionTypes.GET_GATEWAYS_LIST
	};
}

export interface LoadGatewaysListAction {
	type: typeof ActionTypes.LOAD_GATEWAYS_LIST;
	payload: { gateways: models.GatewaysList | null }
}
export function loadGatewaysListAction(gateways: models.GatewaysList | null): GatewayActionTypes {
	return {
		type: ActionTypes.LOAD_GATEWAYS_LIST,
		payload: { gateways }
	};
}


export type GatewayActionTypes = ClearGatewayStorageAction
	//| CreateGatewayAction
	| GetGatewaysListAction
	| LoadGatewaysListAction;

export const initialState: State = {
	gatewaysList: null
}

export function gatewayReducer(previousState = initialState, action: GatewayActionTypes): State {
	switch (action.type) {
		case ActionTypes.CLEAR_GATEWAY_STORAGE:
			return initialState;
		//case ActionTypes.CREATE_GATEWAY:
		//	return previousState;
		case ActionTypes.LOAD_GATEWAYS_LIST:
			return {
				...previousState,
				gatewaysList: action.payload.gateways
			}
		case ActionTypes.GET_GATEWAYS_LIST:
		default:
			return previousState;
	}
}

export const reducer = gatewayReducer;

export function* saga() {
	yield takeEvery(ActionTypes.GET_GATEWAYS_LIST, workerGetGatewaysList);
}

export function* workerGetGatewaysList (action: GetGatewaysListAction) {
	try {
		const gatewaysList = yield call (loadingGatewaysList);
		yield put (loadGatewaysListAction(gatewaysList));
	} catch (e) {
		yield put (loadGatewaysListAction(null));
		yield put (handleErrorAction(null, e));	
	}
}

export async function loadingGatewaysList(): Promise<models.GatewaysList> {
	return await cps.getGatewaysList();
}