import React, { Component } from "react";
import { RouteComponentProps } from "react-router-dom";
import { connect } from "react-redux";

import { Accordion, Card, Row, Col } from "react-bootstrap";
import MerchantsTable from "./components/MerchantsTable";
import FilterByMerchant, { MerchantsList } from "../../common/components/FilterByMerchant";
import FilterByDate, { DateFilter } from "../../common/components/FilterByDate";
import FilterByCurrency, { CurrenciesList } from "../../common/components/FilterByCurrency";
import PaginationActivity from "../../common/components/PaginationActivity";

import { handleErrorAction } from "../../../store/ducks/general.duck";
import { getMerchantsSettingsAction, getMerchantsListAction } from "../../../store/ducks/merchants.duck";
import { RootState } from "../../../store/rootDuck";
import * as models from "@cryptopay/dashboard-contract/models";
import { PaginationData } from "../../common/components/PaginationActivity/PaginationActivity";

import * as helper from "../../../utils/helper";
import * as dateUtil from "../../../utils/dateUtil";
import * as _ from "lodash";

type FilterPart = DateFilter
	| MerchantsList
	| CurrenciesList
	| PaginationData;

type State = {
	filter: models.FilterMerchants
}

type Props = RouteComponentProps<{}> & {
	merchants: models.Merchants | null;
	settings: models.FilterSettingsMerchants | null;
	getMerchantsSettingsAction: typeof getMerchantsSettingsAction,
	getMerchantsListAction: typeof getMerchantsListAction,
	handleErrorAction: typeof handleErrorAction
}

export class Merchants extends Component<Props, State> {

	private readonly defaultFilter: models.FilterMerchants = {
		merchants: [],
		currencies: ["USD", "EUR"],
		dateFrom: dateUtil.dayToDate(),
		dateTo: dateUtil.endDay(),
		count: 20,
		page: 1
	}

	constructor(props: Props) {
		super(props);
		this.state = {
			filter: this.defaultFilter
		};
	}

	componentDidMount() {
		this.props.getMerchantsSettingsAction();

		if (this.props.history.location.search !== "") {
			const filter = this.parseQueryParamsToFilter();
			this.setState({ filter });
		} else {
			this.props.history.replace(`/merchants?${helper.parseObjectToQueryParams(this.defaultFilter)}`);
			this.setState({ filter: { ...this.defaultFilter } });
		}
	}
	componentDidUpdate(prevProps: Props, prevState: State) {
		if (this.props.history.location.search === "") {
			// set default filter
			this.props.history.replace(`/merchants?${helper.parseObjectToQueryParams(this.defaultFilter)}`);
			this.setState({ filter: { ...this.defaultFilter } });
		} else {
			const filterByParams = this.parseQueryParamsToFilter();
			if (!_.isEqual(filterByParams, this.state.filter)) {
				this.setState({ filter: filterByParams });
			}
		}

		// update filter settings
		this.props.getMerchantsSettingsAction();
		// set default filter
		this.props.history.replace(`/merchants?${helper.parseObjectToQueryParams(this.defaultFilter)}`);
		this.setState({ filter: { ...this.defaultFilter } });

		if (this.state.filter !== prevState.filter) {
			this.props.getMerchantsListAction(this.state.filter);
		}
	}

	parseQueryParamsToFilter = (): models.FilterMerchants => {
		const searchParams = new URLSearchParams(this.props.history.location.search);

		const merchantsStr = searchParams.get("merchants");
		const currenciesStr = searchParams.get("currencies");
		const dateFromStr = searchParams.get("dateFrom");
		const dateToStr = searchParams.get("dateTo");
		const countStr = searchParams.get("count");
		const pageStr = searchParams.get("page");

		return {
			currencies: currenciesStr !== null ? currenciesStr.split(",") : undefined,
			merchants: merchantsStr !== null ? merchantsStr.split(",") : undefined,
			dateFrom: (dateFromStr !== null) ? new Date(dateFromStr) : this.defaultFilter.dateFrom,
			dateTo: (dateToStr !== null) ? new Date(dateToStr) : this.defaultFilter.dateTo,
			count: (countStr !== null) ? Number.parseInt(countStr) : 20,
			page: (pageStr !== null) ? Number.parseInt(pageStr) : 1,
		}
	}

	changeFilterDataHendler = (filterPart: FilterPart) => {		
		const filter = {
			...this.state.filter,
			...filterPart
		};
		this.props.history.push(`/merchants?${helper.parseObjectToQueryParams(filter)}`);
	}

	render() {
		return (
			<div>
				<Accordion defaultActiveKey="0" className="mb-4">
					<Card>
						<Card.Header>
							<Row className="text-center">
								<Col>
									<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} variant="link" eventKey="0" >
										<h6>Select Merchants</h6>
									</Accordion.Toggle>
								</Col>
								<Col>
									<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} variant="link" eventKey="1" >
										<h6>Select Currencies</h6>
									</Accordion.Toggle>
								</Col>
								<Col>
									<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} variant="link" eventKey="2" >
										<h6>Filter By Date</h6>
									</Accordion.Toggle>
								</Col>
							</Row>
						</Card.Header>
						<Accordion.Collapse eventKey="0">
							<Card.Body>
								<FilterByMerchant
									merchants={this.props.settings !== null ? [...this.props.settings.merchants] : []}
									filterMerchants={this.state.filter.merchants ? this.state.filter.merchants : []}
									changeFilter={this.changeFilterDataHendler}
								/>
							</Card.Body>
						</Accordion.Collapse>
						<Accordion.Collapse eventKey="1">
							<Card.Body>
								<FilterByCurrency
									currencies={this.props.settings !== null ? [...this.props.settings.currencies] : []}
									filterCurrencies={this.state.filter.currencies ? this.state.filter.currencies : []}
									changeFilter={this.changeFilterDataHendler}
								/>
							</Card.Body>
						</Accordion.Collapse>
						<Accordion.Collapse eventKey="2">
							<Card.Body>
								<FilterByDate
									dateFrom={this.state.filter.dateFrom}
									dateTo={this.state.filter.dateTo}
									changeFilter={this.changeFilterDataHendler}
									isDateRequired={true}
									errorHandler={this.props.handleErrorAction}
								/>
							</Card.Body>
						</Accordion.Collapse>
					</Card >
				</Accordion>
				<MerchantsTable 
					filterCurrencies={this.state.filter.currencies ? this.state.filter.currencies : []}
					filterMerchants={this.state.filter.merchants ? this.state.filter.merchants : []}
				/>
				<PaginationActivity
					countMax={this.props.merchants !== null ? this.props.merchants.countMerchants : 0}
					countOnPage={this.state.filter.count}
					page={this.state.filter.page}
					itemsLength={3}
					submitPagination={this.changeFilterDataHendler}
				/>
			</div >
		);
	}
}

const mapState = (state: RootState) => ({
	merchants: state.merchants.merchants,
	settings: state.merchants.settings
});

const mapDispatch = {
	getMerchantsSettingsAction,
	getMerchantsListAction,
	handleErrorAction
}

const connector = connect(mapState, mapDispatch);

export default connector(Merchants);
