import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";

export interface State {
	lang: Languages;
}

export enum Languages {
	en = "en"
}

export enum ActionTypes {
	SET_LANGUAGE = "SET_LANGUAGE"
}

export interface SetLanguageI18nAction {
	type: ActionTypes.SET_LANGUAGE;
	payload: { lang: Languages; }
}
export function setLanguageI18nAction(lang: Languages): SetLanguageI18nAction {
	return {
		type: ActionTypes.SET_LANGUAGE,
		payload: { lang }
	};
}

export type I18nActionTypes = SetLanguageI18nAction;

const initialState = {
	lang: Languages.en
};

export function i18nReducer(previousState = initialState, action: I18nActionTypes): State {
	switch (action.type) {
		case ActionTypes.SET_LANGUAGE:
			return { 
				...previousState,
				lang: action.payload.lang
			};
		default:
			return previousState;
	}
}

export const reducer = persistReducer({ storage, key: "i18n" }, i18nReducer);

