import React, { Component } from "react";
import * as models from "@cryptopay/dashboard-contract/models";
import { Button, Container, Row, Col, Form } from "react-bootstrap";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { RootState } from "../../../../../store/rootDuck";
import { getGatewaysListAction } from "../../../../../store/ducks/gateway.duck";


type State = {
	isShowPopUp: boolean;
}
type Props = {
	gatewaysList: models.GatewaysList | null,
	getGatewaysListAction: typeof getGatewaysListAction
}

export class GatewaysList extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {
			isShowPopUp: false
		}
	}

	componentDidMount() {
		this.props.getGatewaysListAction();
	}
	componentDidUpdate(prevProps: Props, prevState: State) {
		this.props.getGatewaysListAction();
	}


	render() {
		if (this.props.gatewaysList === null) {
			return null;
		}
		return (
			<div>
				{
					this.props.gatewaysList.map((gateway) => {
						return (
							<Container key={gateway.gatewayUuid} className="card mb-4">
								<Row>
									<Col xs={12} sm={6} className="">
										<h5 className="mt-4 mb-4 ml-4">Gateway: <Link to={`/settings/gateway/${gateway.gatewayUuid}`}>{gateway.gatewayUuid}</Link></h5>
										<Form.Check
											className="ml-4"
											type="checkbox"
											label="enabled"
											checked={gateway.enabled}
											disabled
										/>
									</Col>
									<Col xs={12} sm={6} >
										<div className="ml-4 mt-4">
											<label className="font-italic text-muted mt-4 mb-4">Description</label>
											<div>
												<p>{gateway.description}</p>
											</div>
										</div>
									</Col>
								</Row>
							</Container>
						)
					})
				}
				<Link to={`/settings/gateway/create`} className="my-2"><Button variant="outline-primary">Add Gateway</Button></Link>
			</div>
		)
	}
}

const mapState = (state: RootState) => ({
	gatewaysList: state.gateway.gatewaysList
});

const mapDispatch = {
	getGatewaysListAction: getGatewaysListAction
}

const connector = connect(mapState, mapDispatch);

export default connector(GatewaysList);