import { SubscriptionData, TopicInfoList } from "@cryptopay/dashboard-contract";
import React, { Component } from "react";
import { connect } from "react-redux";
import { loadNotificationTopicList, createSubscription } from "../../../../store/ducks/subscriptions.duck";
import { handleErrorAction } from "../../../../store/ducks/general.duck";
import { RootState } from "../../../../store/rootDuck";
import {
	InputLabel,
	FormControl,
	Select,
	MenuItem,
	TextField,
	Button,
	TextareaAutosize,
} from "@material-ui/core";

type State = {
	selectedTopic: string;
	selectedTopicDescription: string;
	certificate: string;
	url: string;
};

type Props = {
	topics: TopicInfoList;
	loadNotificationTopicList: typeof loadNotificationTopicList;
	createSubscription: typeof createSubscription;
	handleErrorAction: typeof handleErrorAction;
};

export class SubscriptionForm extends Component<Props, State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			selectedTopic: "",
			certificate: "",
			selectedTopicDescription: "",
			url: "",
		};
	}

	componentDidMount() {
		this.props.loadNotificationTopicList();
	}

	validURL = (str: string) => {
		var pattern = new RegExp('^(https?:\\/\\/)?'+ // protocol
		  '((([a-z\\d]([a-z\\d-]*[a-z\\d])*)\\.)+[a-z]{2,}|'+ // domain name
		  '((\\d{1,3}\\.){3}\\d{1,3}))'+ // OR ip (v4) address
		  '(\\:\\d+)?(\\/[-a-z\\d%_.~+]*)*'+ // port and path
		  '(\\?[;&a-z\\d%_.~+=-]*)?'+ // query string
		  '(\\#[-a-z\\d_]*)?$','i'); // fragment locator
		return !!pattern.test(str);
	}

	handleChange = (event: React.ChangeEvent<{value: unknown, name: unknown }>) => {
		this.setState({
			...this.state,
			[event.target.name as string]: event.target.value as string,
		})
	}

	handleTopicChange = (event: React.ChangeEvent<{value: unknown}>) => {
		const topic = this.props.topics.find(topic => topic.topic === event.target.value);
		if (topic) {
			this.setState({
				...this.state,
				selectedTopic: topic.topic,
				selectedTopicDescription: topic.description, 
			});
		}
	}

	handleCreate = () => {
		if (!this.state.selectedTopic.trim()) {
			this.props.handleErrorAction("Topic must be filled", null);
			return;
		}
		if (!this.state.url.trim()) {
			this.props.handleErrorAction("Url must be filled", null);
			return;
		}
		if (!this.validURL(this.state.url)) {
			this.props.handleErrorAction("Url must be vaild", null);
			return;
		}

		const subscription: SubscriptionData = {
			topic: this.state.selectedTopic,
			url: this.state.url,
			trustedCACertificatePEM: this.state.certificate,
		}
		this.props.createSubscription(subscription);
		this.setState({
			...this.state,
			selectedTopic: "",
			url: "",
			certificate: "",
			selectedTopicDescription: "",
		})
	}


	render() {
		return (<div>
 			<h5>Create new subscription.</h5>
			<div>
				<TextField 
					value={this.state.url}
					name="url"
					onChange={this.handleChange}
					label="Url"
					style={{minWidth: 200}}		
				/>
			</div>
			
 			<FormControl
			 style={{minWidth: 250}}
			 className="mb-2"
			 >
 				<InputLabel id="subscribtion-topic-label">Topic</InputLabel>
 				<Select
					labelId="subscribtion-topic-label"
 					id="subscribtion-topic-select"
 					value={this.state.selectedTopic}
 					onChange={this.handleTopicChange}
 				>
 					<MenuItem value=""><em>None</em></MenuItem>
 					{this.props.topics.map(topic => (
 						<MenuItem key={topic.topic} value={topic.topic}>{topic.topic}</MenuItem>
 					))}
 				</Select>
				 <p>{this.state.selectedTopicDescription}</p>
       		</FormControl>
			   <p>
			  	<TextareaAutosize 
			   		name="certificate"
					onChange={this.handleChange}
					value={this.state.certificate}
					rowsMin={5}
					cols={40}
				/>
				</p>
			 <Button
			 	variant="contained"
 				color="primary"
				onClick={this.handleCreate}
				className="mb-2"
 			>Create</Button>
		</div>);
	}
}

const mapState = (state: RootState) => ({
	topics: state.subscriptions.notificationTopicList,
});

const mapDispatch = {
	loadNotificationTopicList,
	createSubscription,
	handleErrorAction,
};


const connector = connect(mapState, mapDispatch);

export default connector(SubscriptionForm);
