import React, { Component } from "react";
import { Table } from "react-bootstrap";
import * as models from "@cryptopay/dashboard-contract/models";


type State = {

}

type Props = {
	transactions: Array<models.TransactionBase>;
}

export default class TransactionsTable extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		this.state = {}
	}

	render() {

		return (
			<div>
				<Table responsive className="table-hover">
					<thead>
						<tr>
							{/*<th>Type</th>*/}
							<th>Transacation Id</th>
							<th>Description</th>
							<th>Status</th>
							<th>Amount</th>
							<th>Date</th>
						</tr>
					</thead>
					<tbody>
						{
							this.props.transactions.map((transaction, index) => {
								return (
									<tr key={`${index}-${transaction.apiTransactionId}`}>
										{/*<td>{transaction.type}</td>*/}
										<td>{transaction.apiTransactionId}</td>
										<td>{transaction.description}</td>
										<td>{transaction.status}</td>
										<td>{transaction.amount}</td>
										<td>{new Date(transaction.createdAt).toISOString()}</td>
									</tr>
								)
							})
						}
					</tbody>
				</Table>
			</div>
		);
	}
}