import React, { Suspense, Component } from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import Account from "./account/Account";
import Accounts from "./accounts/Accounts";
import Merchant from "./merchant/Merchant";
import Merchants from "./merchants/Merchants";
import Order from "./order/Order";
import Orders from "./orders/Orders";
import OrderWorkflow from "./order/components/OrderWorkflow";
import Settings from "./settings";
import { LayoutSplashScreen } from "../../layout/LayoutContext";
import { DashboardPermission } from "@cryptopay/dashboard-contract/models";
import { RootState } from "../../store/rootDuck";
import { connect } from "react-redux";


type State = {};

type Props = {
	permissions: DashboardPermission[];
}

export class HomePage extends Component<Props, State> {
	
	render() {
		return (
			<Suspense fallback={<LayoutSplashScreen />}> 
				<Switch>

					<Route path="/order/:orderId/workflow" component={OrderWorkflow} />
					<Route path="/order/:orderId" component={Order} />
					<Route path="/orders" component={Orders} />
					<Route path="/account/:accountId" component={Account} />
					<Route path="/accounts" component={Accounts} />
					<Route path="/merchant" component={Merchant} />
					
					<Route path="/settings" component={Settings} />

					<Route path="/merchants" component={Merchants} />
					<Route path="/">
						{
							this.props.permissions.includes(DashboardPermission.OrderList) ?
							<Redirect from="/" to={`/orders`} /> :
							this.props.permissions.includes(DashboardPermission.MerchantView) ?
							<Redirect from="/" to={`/merchant`} /> :
							<Redirect from="/" to={`/orders`} />
						}
					</Route>
					<Redirect to="/error/error-v1" />
				</Switch>

			</Suspense>
		);
	}

}

const mapState = (state: RootState) => ({
	permissions: []//state.auth.permissions
});

const mapDispatch = {};

const connector = connect(mapState, mapDispatch);

export default connector(HomePage);
