
import { Typifier } from "@cryptopay/dashboard-contract";
import { InnerError } from "@cexiolabs/cryptopay.errors";
import * as t from "io-ts";

export async function apicall<E extends t.Any, D extends t.Any>(
	method: string, url: URL,
	body?: t.TypeOf<E>, decoder?: D,
	encoder?: E,
	overrideInit?: {
		readonly credentials?: RequestCredentials;
		readonly headers?: { readonly [key: string]: string };
	}

): Promise<t.TypeOf<D>> {

	const urlStr: string = url.toString();

	try {
		let req: string | null = null;

		if (body) {
			if (encoder) {
				req = JSON.stringify(Typifier.decode(encoder, body, "body"));
			}
			else {
				req = JSON.stringify(body);
			}
		}

		const headers: { [key: string]: string } = {};
		headers["Content-Type"] = "application/json";
		if (overrideInit !== undefined && overrideInit.headers !== undefined) {
			for (const key of Object.keys(overrideInit.headers)) {
				const value: string = overrideInit.headers[key];
				headers[key] = value;
			}
		}

		const fetchInit: RequestInit = {
			cache: 'no-cache',
			//credentials: 'omit',
			credentials: 'include',
			mode: "cors",
			redirect: "follow",
			method,
			headers
		};

		if (overrideInit !== undefined && overrideInit.credentials !== undefined) {
			fetchInit.credentials = overrideInit.credentials;
		}

		fetchInit.body = req;

		const response = await fetch(urlStr, fetchInit);

		if (response.status < 200 || response.status > 299) {
			throw new HttpStatusError(`Status ${response.status}: ${response.statusText}`, response.status, response.statusText);
		}

		const contentType = response.headers.get("Content-Type");
		if (contentType && contentType.includes("application/json")) {
			const rsp = await response.json();

			if (!decoder) {
				return rsp;
			}
			return Typifier.decode(decoder, rsp, "body");
		} else {
			return undefined;
		}

	} catch (e) {
		throw new ApiCallError(undefined, e, urlStr, method);
	}
}

export class HttpStatusError extends Error {

	public readonly code: number;
	public readonly reason?: string;

	constructor(message: string, code: number, reason?: string) {
		super(message);
		this.code = code;
		this.reason = reason;
	}
}

export class ApiCallError extends InnerError {

	public readonly url?: string;
	public readonly method?: string;

	constructor(message?: string, inner?: Error, url?: string, method?: string) {

		let msg = `Api call ${url ? url : ""} ${method ? `(${method})` : ""} failed.`;

		if (message) {
			msg += " " + message;
		}
		else {
			if (inner) {
				msg += " " + inner.message;
			}
		}

		super(msg, inner);

		this.url = url;
		this.method = method;
	}
}

