import React, { Component } from "react";
import { connect } from "react-redux";
import { RootState } from "../store/rootDuck";
import { checkAppVersionAction, handleErrorAction } from "../store/ducks/general.duck";


export type Props = {
	children: React.ReactNode;
	appVersion: string | undefined;
	checkAppVersionAction: typeof checkAppVersionAction;
	handleErrorAction: typeof handleErrorAction;
};

export type State = {};


export class InitialLayout extends Component<Props, State> {

	componentDidMount() {
		this._checkAppVersion();
	}

	private _checkAppVersion = () => {
		if (this.props.appVersion) {
			this.props.checkAppVersionAction(this.props.appVersion);
		} else {
			this.props.handleErrorAction("Application version is not defined", null);
		}
	}

	render() {
		return (
			<>
				{this.props.children}
			</>
		);
	}

}

const mapState = (state: RootState) => ({
});

const mapDispatch = {
	checkAppVersionAction,
	handleErrorAction
}

const connector = connect(mapState, mapDispatch);

export default connector(InitialLayout);