import React, { Component } from "react";
import { connect } from "react-redux";
import HighlightOffIcon from '@material-ui/icons/HighlightOff';

import styles from "./index.module.scss";

import { ErrorData } from "../../../../store/ducks/general.duck";
import { RootState } from "../../../../store/rootDuck";


type State = {
	isActive: boolean;
	error: ErrorData | null;
}

type Props = {
	error: ErrorData | null;
}

export class ErrorRender extends Component<Props, State> {

	constructor(props: Props) {
		super(props);
		
		this.state = {
			isActive: false,
			error: this.props.error,
		}	
	}

	componentDidUpdate (prevProps: Props) {
		if (this.state.isActive) {
			this.hideTimer(10000); // 10 sec
		}
		if (this.props.error !== null && prevProps.error !== this.props.error) {
			this.setState({isActive: true});	
		}
	}

	closeErrorHandler = () => {
		this.setState({isActive: false});
	}

	hideTimer = (ms: number) => {
		setTimeout(() => {this.setState({isActive: false})}, ms);
	}


	render() {
		return (
			<div className={`${styles.err_render} ${!this.state.isActive ? styles.hide_err_render : ""}`}>
				<div >
					<h6>Error</h6>
					<p>{this.props.error?.message}</p>
					<HighlightOffIcon 
						className={styles.cl_err_render}
						onClick={this.closeErrorHandler}	
					/>
				</div>
			</div>
		);
	}
}

const mapState = (state: RootState) => ({
	error: state.general.error
});

const mapDispatch = {
}

const connector = connect(mapState, mapDispatch);

export default connector(ErrorRender);