import { all } from "redux-saga/effects";
import { combineReducers } from "redux";

import * as account from "./ducks/account.duck";
import * as accounts from "./ducks/accounts.duck";
// import * as auth from "./ducks/auth.duck";
import * as i18n from "./ducks/i18n.duck";
import * as gateway from "./ducks/gateway.duck";
import * as general from "./ducks/general.duck";
import * as layout from "./ducks/layout.duck";
import * as limits from "./ducks/limits.duck";
import * as merchants from "./ducks/merchants.duck";
import * as order from "./ducks/order.duck";
import * as orders from "./ducks/orders.duck";
import * as subscriptions from "./ducks/subscriptions.duck";

export interface RootState {
	account: account.AccountState;
	accounts: accounts.AccountsState;
	// auth: auth.State;
	i18n: i18n.State;
	gateway: gateway.State;
	general: general.GeneralState;
	layout: layout.State,
	limits: limits.State;
	merchants: merchants.MerchantsState;
	order: order.OrderState;
	orders: orders.OrdersState;
	subscriptions: subscriptions.State;
}

export const rootReducer = combineReducers({
	account: account.reducer,
	accounts: accounts.reducer,
	// auth: auth.reducer,
	gateway: gateway.reducer,
	general: general.reducer,
	i18n: i18n.reducer,
	layout: layout.reducer,
	limits: limits.reducer,
	merchants: merchants.reducer,
	order: order.reducer,
	orders: orders.reducer,
	subscriptions: subscriptions.reducer,
});

export function* rootSaga() {
	yield all([account.saga(), accounts.saga(),
	//auth.saga(),
	gateway.saga(), general.saga(),
	layout.saga(), limits.saga(), merchants.saga(), order.saga(), orders.saga(),
	subscriptions.saga()
	]);
}
