import React from "react";
import { Redirect, Route, Switch, withRouter } from "react-router-dom";
import { shallowEqual, useSelector } from "react-redux";
import { useLastLocation } from "react-router-last-location";
import HomePage from "../pages/home/HomePage";
// import ErrorsPage from "../pages/errors/ErrorsPage";
// import LogoutPage from "../pages/auth/logout/Logout";
import { LayoutContextProvider } from "../layout/LayoutContext";
import Layout from "../layout/Layout"
import * as routerHelpers from "./RouterHelpers";
//import AuthPage from "../pages/auth/AuthPage";
//import { AuthStatus } from "../store/ducks/auth.duck";
import ErrorRender from "./../pages/common/components/ErrorRender";


export const Routes = withRouter(() => {

	const lastLocation = useLastLocation();
	routerHelpers.saveLastLocation(lastLocation);
	const { userLastLocation } = useSelector(
		({ auth }) => ({
			//authStatus: auth.authStatus,
			userLastLocation: routerHelpers.getLastLocation()
		}),
		shallowEqual
	);
	return (
		<>
			<LayoutContextProvider>
				<Switch>
					<Layout>
						<HomePage userLastLocation={userLastLocation} />
					</Layout>
				</Switch>
			</LayoutContextProvider>
			<ErrorRender />
		</>
	);
});
