import json2csv, { Parser } from "json2csv";
import * as models from "@cryptopay/dashboard-contract/models";

export function dataToCSV<T>(data: Array<T>, options: json2csv.Options<T>): string {
	const json2csvParser = new Parser(options);
	const csv = json2csvParser.parse(data);
	return csv;
}

export function downloadCSV<T> (data: Array<T>, options: json2csv.Options<T>, fileName: string) {
	var linkToDownload = document.createElement("a");    
	linkToDownload.style.display = "none";

	const csv = dataToCSV<T> (data, options);
	const blob = new Blob([csv], {type: "text/plain"});

	linkToDownload.href = window.URL.createObjectURL(blob);
	linkToDownload.download = fileName;

	document.body.appendChild(linkToDownload);
	linkToDownload.click();
	document.body.removeChild(linkToDownload);
}

