import React, { Component } from "react";
import { RouteComponentProps } from "react-router-dom";
import { ConnectedProps, connect } from "react-redux";

import { Accordion, Card } from "react-bootstrap";
import OrdersFilter, { FilterForOrders } from "./components/OrdersFilter";
import OrdersTable, { OrdersColumnsArray, OrdersColumnsLabels } from "./components/OrdersTable";
import PaginationActivity from "../../common/components/PaginationActivity";

import { RootState } from "../../../store/rootDuck";
import { DashboardPermission } from "@cryptopay/dashboard-contract/models";
import { getOrdersListAction, getOrdersSettingsAction, updateOrdersColumnsAction } from "../../../store/ducks/orders.duck";
import * as models from "@cryptopay/dashboard-contract/models";
import TableColumnSettings from "../../common/components/TableColumnSettings";
import { PaginationData } from "../../common/components/PaginationActivity/PaginationActivity";

import * as helper from "../../../utils/helper";
import * as _ from "lodash";

type FilterPart = PaginationData
	| FilterForOrders;

type State = {
	filter: models.FilterOrders;
};

type Props = RouteComponentProps<{}> &
	ConnectedProps<typeof connector> &
{
	activeColumns: Array<string>;
	filterSettings: models.FilterSettingsOrders | null;
	permissions: DashboardPermission[];
	orders: models.Orders | null;
	getOrdersListAction: typeof getOrdersListAction,
	getOrdersSettingsAction: typeof getOrdersSettingsAction,
	updateOrdersColumnsAction: typeof updateOrdersColumnsAction
}

export class Orders extends Component<Props, State> {

	private readonly defaultFilter: models.FilterOrders = {
		merchant: undefined,
		instrument: undefined,
		status: undefined,
		state: undefined,
		currencyFrom: undefined,
		currencyTo: undefined,
		apiOrderId: undefined,
		clientOrderId: undefined,
		cryptoAddress: undefined,
		dateFrom: undefined,
		dateTo: undefined,
		count: 20,
		page: 1,
		sortBy: "createdAt",
		sortIn: "DESC"
	}

	constructor(props: Props) {
		super(props);

		this.state = {
			filter: this.defaultFilter,
		};
	}

	componentDidMount() {
		this.props.getOrdersSettingsAction();

		if (this.props.history.location.search !== "") {
			const filter = this.parseQueryParamsToFilter();
			this.setState({ filter });
		} else {
			this.props.history.replace(`/orders?${helper.parseObjectToQueryParams(this.defaultFilter)}`);
			this.setState({ filter: { ...this.defaultFilter } });
		}
	}
	componentDidUpdate(prevProps: Props, prevState: State) {

		if (this.props.history.location.search === "") {
			// set default filter
			this.props.history.replace(`/orders?${helper.parseObjectToQueryParams(this.defaultFilter)}`);
			this.setState({ filter: { ...this.defaultFilter } });
		} else {
			const filterByParams = this.parseQueryParamsToFilter();
			if (!_.isEqual(filterByParams, this.state.filter)) {
				this.setState({ filter: filterByParams });
			}
		}

		if (this.state.filter !== prevState.filter) {
			this.props.getOrdersListAction(this.state.filter);
		}
	}

	parseQueryParamsToFilter = (): models.FilterOrders => {
		const searchParams = new URLSearchParams(this.props.history.location.search);

		const merchant = searchParams.get("merchant");
		const instrument = searchParams.get("instrument");
		const status = searchParams.get("status");
		const state = searchParams.get("state");
		const currencyFrom = searchParams.get("currencyFrom");
		const currencyTo = searchParams.get("currencyTo");
		const apiOrderId = searchParams.get("apiOrderId");
		const clientOrderId = searchParams.get("clientOrderId");
		const cryptoAddress = searchParams.get("cryptoAddress");
		const dateFromStr = searchParams.get("dateFrom");
		const dateToStr = searchParams.get("dateTo");
		const countStr = searchParams.get("count");
		const pageStr = searchParams.get("page");

		return {
			merchant: merchant !== null ? merchant : undefined,
			instrument: instrument !== null ? instrument : undefined,
			status: status !== null ? status : undefined,
			state: state !== null ? state : undefined,
			currencyFrom: currencyFrom !== null ? currencyFrom : undefined,
			currencyTo: currencyTo !== null ? currencyTo : undefined,
			apiOrderId: apiOrderId !== null ? apiOrderId : undefined,
			clientOrderId: clientOrderId !== null ? clientOrderId : undefined,
			cryptoAddress: cryptoAddress !== null ? cryptoAddress : undefined,
			dateFrom: (dateFromStr !== null) ? new Date(dateFromStr) : undefined,
			dateTo: (dateToStr !== null) ? new Date(dateToStr) : undefined,
			count: (countStr !== null) ? Number.parseInt(countStr) : this.defaultFilter.count,
			page: (pageStr !== null) ? Number.parseInt(pageStr) : this.defaultFilter.page,
			sortBy: this.defaultFilter.sortBy,
			sortIn: this.defaultFilter.sortIn
		};
	}

	changeDataFilterHandler = (filterPart: FilterPart) => {
		const filter = {
			...this.state.filter,
			...filterPart
		};
		this.props.history.push(`/orders?${helper.parseObjectToQueryParams(filter)}`);
	}

	render() {
		return (
			<div>
				<Accordion defaultActiveKey="0" className="mb-4">
					<Card>
						<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} eventKey="0" >
							<h6>Filter</h6>
						</Accordion.Toggle>

						<Accordion.Collapse eventKey="0">
							<Card.Body>
								<OrdersFilter
									filter={this.state.filter}
									filterSettings={this.props.filterSettings}
									changeFilter={this.changeDataFilterHandler}
								/>
							</Card.Body>
						</Accordion.Collapse>
					</Card >
					<Card>
						<Accordion.Toggle className="px-4 pt-3 pb-1 bg-light" as={Card.Header} eventKey="1">
							<h6>Table settings</h6>
						</Accordion.Toggle>
						<Accordion.Collapse eventKey="1">
							<Card.Body>
								<TableColumnSettings
									columns={OrdersColumnsArray}
									labels={OrdersColumnsLabels}
									activeColumns={this.props.activeColumns}
									updateActiveColumns={this.props.updateOrdersColumnsAction}
								/>
							</Card.Body>
						</Accordion.Collapse>
					</Card >
				</Accordion>

				<OrdersTable
					orders={this.props.orders !== null ? this.props.orders.orders : []}
					activeColumns={this.props.activeColumns}
					permissions={this.props.permissions}
				/>
				<PaginationActivity
					countMax={this.props.orders !== null ? this.props.orders.countOrders : 0}
					countOnPage={this.state.filter.count}
					page={this.state.filter.page}
					itemsLength={3}
					submitPagination={this.changeDataFilterHandler} />
			</div>
		);
	}
}

const mapState = (state: RootState) => ({
	activeColumns: state.orders.activeColumns,
	filterSettings: state.orders.filterSettings,
	orders: state.orders.orders,
	permissions: []//state.auth.permissions,
});

const mapDispatch = {
	getOrdersListAction,
	getOrdersSettingsAction,
	updateOrdersColumnsAction
};

const connector = connect(mapState, mapDispatch);

export default connector(Orders);
