import React, { Component } from "react";
import { TextField } from "@material-ui/core";
import { Row, Col, InputGroup } from "react-bootstrap";

import ChipElement from "../../../common/components/ChipElement";

export type FilterPartForAccounts = {}
	| { balanceCondition: Array<string> }
	| { apiAccountId: string }
	| { clientAccountId: string }
	| { depositAddress: string };

export type FilterForAccounts = {
	tags: Array<string> | undefined;
	balanceCondition: Array<string> | undefined;
	apiAccountId: string | undefined;
	clientAccountId: string | undefined;
	depositAddress: string | undefined;
}
type State = {
	disableActions: boolean;
}

type Props = {
	tagsSettings: Array<string>;
	filter: FilterForAccounts;
	changeFilter: (filterPart: FilterPartForAccounts) => void;
}

export default class AccountsFilter extends Component<Props, State> {

	disableActions: boolean;

	constructor(props: Props) {
		super(props);

		this.state = {
			disableActions: false
		}
		this.disableActions = false;
	}

	componentDidUpdate() {
		this.disableActions = false;
	}

	onChangeTagsHandler = (tag: string) => () => {
		if (this.props.filter.tags !== undefined) {
			const tags = [...this.props.filter.tags];
			if (tags.includes(tag)) {
				this.props.changeFilter({ tags: tags.filter(value => value !== tag) });
			} else {
				tags.push(tag);
				this.props.changeFilter({ tags });
			}
		} else {
			this.props.changeFilter({ tags: [tag] });
		}
	}

	onChangeBalanceCondition = (condition: string) => () => {
		if (this.props.filter.balanceCondition !== undefined) {
			const balanceCondition = [...this.props.filter.balanceCondition];
			if (balanceCondition.includes(condition)) {
				this.props.changeFilter({ balanceCondition: balanceCondition.filter(value => value !== condition) });
			} else {
				balanceCondition.push(condition);
				this.props.changeFilter({ balanceCondition });
			}
		} else {
			this.props.changeFilter({ balanceCondition: [condition] });
		}
	}

	onChangeTextFieldHandler = (name: string) => (event: any) => {

		// check if click not enter
		if (event.charCode && event.charCode !== 13) {
			return;
		}

		if (!this.disableActions) {
			this.disableActions = true;

			let value = event.target.value;
			if (value && value.length === 0) {
				value = undefined;
			}
			switch (name) {
				case "apiAccountId":
					if (this.props.filter.apiAccountId !== value) {
						this.props.changeFilter({ apiAccountId: value });
					}
					return;
				case "depositAddress":
					if (this.props.filter.depositAddress !== value) {
						this.props.changeFilter({ depositAddress: value });
					}
					return;
				case "clientAccountId":
					if (this.props.filter.clientAccountId !== value) {
						this.props.changeFilter({ clientAccountId: value });
					}
					return;
				default:
					console.error(`Cann't find handler for TextField name: ${name}`)
			}
		}
	}

	render() {
		return (
			<div>
				<Row className="align-items-center text-center">
					<Col sm={4} xs={12} className="my-1">
						<h6>Tags</h6>
						{
							this.props.tagsSettings.map((tag, index) => {
								if (this.props.filter.tags && this.props.filter.tags.includes(tag)) {
									return (
										<ChipElement
											key={`${tag}-${index}`}
											label={tag}
											onClick={this.onChangeTagsHandler(tag)}
											variant="add"
											className="m-2"
										/>
									);

								} else {
									return (
										<ChipElement
											key={`${tag}-${index}`}
											label={tag}
											onClick={this.onChangeTagsHandler(tag)}
											variant="delete"
											className="m-2"
										/>
									);
								}

							})
						}
					</Col>
					<Col sm={5} xs={12}>
						<TextField
							id="accountIdFilter"
							fullWidth
							size="small"
							label="Account Id"
							placeholder="Account Id"
							defaultValue={this.props.filter.apiAccountId}
							onKeyPress={this.onChangeTextFieldHandler("apiAccountId")}
							onBlur={this.onChangeTextFieldHandler("apiAccountId")}
							variant="outlined"
							className="mb-3"
						/>
						<TextField
							id="depositAddressFilter"
							fullWidth
							size="small"
							label="Deposit Address"
							placeholder="Deposit Address"
							defaultValue={this.props.filter.depositAddress}
							onKeyPress={this.onChangeTextFieldHandler("depositAddress")}
							onBlur={this.onChangeTextFieldHandler("depositAddress")}
							variant="outlined"
							className="mb-3"
						/>
						<TextField
							id="clientAccountIdFilter"
							fullWidth
							size="small"
							label="Client Account Id"
							placeholder="Client Account Id"
							defaultValue={this.props.filter.clientAccountId}
							onKeyPress={this.onChangeTextFieldHandler("clientAccountId")}
							onBlur={this.onChangeTextFieldHandler("clientAccountId")}
							variant="outlined"
							className="mb-3"
						/>
					</Col>
					<Col sm={3} xs={12} className="text-center my-1">
						<InputGroup className="mb-3">
							<InputGroup.Prepend>
								<InputGroup.Checkbox value="POSITIVE" aria-label="Positive Balance" checked={this.props.filter.balanceCondition && this.props.filter.balanceCondition.includes("POSITIVE")} onChange={this.onChangeBalanceCondition("POSITIVE")} />
								<InputGroup.Text>Positive Balance</InputGroup.Text>
							</InputGroup.Prepend>
						</InputGroup>
						<InputGroup className="mb-3">
							<InputGroup.Prepend>
								<InputGroup.Checkbox value="ZERO" aria-label="Zero balance" checked={this.props.filter.balanceCondition && this.props.filter.balanceCondition.includes("ZERO")} onChange={this.onChangeBalanceCondition("ZERO")} />
								<InputGroup.Text>Zero balance</InputGroup.Text>
							</InputGroup.Prepend>
						</InputGroup>
						<InputGroup className="mb-3">
							<InputGroup.Prepend>
								<InputGroup.Checkbox value="NEGATIVE" aria-label="Negative Balance" checked={this.props.filter.balanceCondition && this.props.filter.balanceCondition.includes("NEGATIVE")} onChange={this.onChangeBalanceCondition("NEGATIVE")} />
								<InputGroup.Text>Negative Balance</InputGroup.Text>
							</InputGroup.Prepend>
						</InputGroup>
					</Col>
				</Row>
			</div>
		);
	}
}