import { apicall, HttpStatusError, ApiCallError } from "./ApiCall";
import * as t from "io-ts";

export abstract class ApiClient {

	private readonly _options: ApiClientOptions;

	public get options(): ApiClientOptions {
		return this._options;
	}

	public constructor(options: ApiClientOptions) {

		this._options = options;
	}

	// protected abstract async tryReAuth(): Promise<boolean>

	protected abstract setAuthHeaders(headers: { [key: string]: string }): void

	public call<E extends t.Any, D extends t.Any>(method: string, path: string, body?: t.TypeOf<E>, decoder?: D, encoder?: E, init?: {
		readonly credentials?: RequestCredentials;
		readonly headers?: { readonly [key: string]: string };
	}): Promise<t.TypeOf<D>> {

		const apiUrl: URL = new URL(path, this.options.apiBaseUrl);

		return apicall(method, apiUrl, body, decoder, encoder, init);
	}

	public async authCall<E extends t.Any, D extends t.Any>(
		method: string,
		path: string,
		body?: t.TypeOf<E>,
		decoder?: D,
		encoder?: E,
		overrideInit?: {
			readonly credentials?: RequestCredentials;
			readonly headers?: { readonly [key: string]: string };
		}
	): Promise<D extends undefined ? void : t.TypeOf<D>> {

		const init: {
			credentials?: RequestCredentials;
			headers: { [key: string]: string };
		} = overrideInit !== undefined
				? { ...overrideInit, headers: { ...overrideInit.headers } }
				: { headers: {} };

		try {

			this.setAuthHeaders(init.headers);
			return await this.call(method, path, body, decoder, encoder, init);

		} catch (mainerr) {
			try {
				if (mainerr instanceof ApiCallError && mainerr.innerError instanceof HttpStatusError && mainerr.innerError.code === 401) {
					// Looks like auth error appears, trying to reauthorize silently
					// const result = await this.tryReAuth();
					// if (!result) {
						throw mainerr;
					// }
				} else {
					throw mainerr;
				}

				// this.setAuthHeaders(init.headers);
				// return await this.call(method, path, body, decoder, encoder, init);
			} catch (mainerrReconect) {
				throw mainerr;
			}
		}
	}
}

export interface ApiClientOptions {
	apiBaseUrl: URL;
}
