import React, { Component } from "react";

import ChipElement from "../../common/components/ChipElement";

export interface ColumnSettings {
	activeColumns: Array<string>;
}
type State = {
};

type Props = {
	columns: Array<string>;
	labels: { [key: string]: string };
	activeColumns: Array<string>;
	updateActiveColumns: (activeColumns: Array<string>) => void;
}

export default class TableColumnSettings extends Component<Props, State> {

	constructor(props: Props) {
		super(props);

		this.state = {}
	}

	columnDeleteHandler = (name: string) => () => {
		let activeColumns = [...this.props.activeColumns];
		if (activeColumns.includes(name)) {
			activeColumns = activeColumns.filter((item, index, array) => { return item !== name });
		} else {
			activeColumns.push(name);
		}
		this.props.updateActiveColumns(activeColumns);
	}
	render() {
		return (
			<div>
				{
					this.props.activeColumns.map((column, index) => {
						if (this.props.columns.includes(column) && this.props.labels[column]) {
							return (
								<ChipElement 
									key={`${column}-${index}`} 
									label={this.props.labels[column]} 
									onClick={this.columnDeleteHandler(column)}
									variant="delete"
									className="m-2"
								/>
							);
						}
						return null;
					})
				}
				{
					this.props.columns.map((column, index) => {
						if (!this.props.activeColumns.includes(column) && this.props.labels[column]) {
							return (
								<ChipElement 
									key={`${column}-${index}`} 
									label={this.props.labels[column]} 
									onClick={this.columnDeleteHandler(column)}
									variant="add"
									className="m-2"
								/>								
							);
						}
						return null;
					})
				}
			</div>
		);
	}
}