import React, { Component } from "react";

import styles from "./index.module.scss";

export interface PaginationData {
	count: number;
	page: number;
}

type PaginationState = {
	isItemsLengthActive: boolean;
};

type PaginationProps = {
	countMax: number;
	countOnPage: number;
	page: number;
	itemsLength: number;
	submitPagination: (filter: PaginationData) => void;
}

export default class PaginationActivity extends Component<PaginationProps, PaginationState> {

	constructor(props: PaginationProps) {
		super(props);

		this.state = {
			isItemsLengthActive: false
		}
	}

	onClickPaginationHandler = (page: number) => () => {
		this.props.submitPagination({
			page,
			count: this.props.countOnPage
		});
	}

	onClickCountHandler = (countOnPage: number) => () => {
		this.props.submitPagination({
			page: 1,
			count: countOnPage
		});
		this.isItemsLengthActive();
	}

	isItemsLengthActive = () => {
        this.setState({
            isItemsLengthActive: !this.state.isItemsLengthActive
        })
    };

	render() {
		// if count of data less than 5 then pagination component will not display.
		if (this.props.countMax < 5 && this.props.countOnPage >= this.props.countMax) {
			return null;
		}
		const activePage = this.props.page;

		let maxPage = Math.ceil(this.props.countMax / this.props.countOnPage);
		if (maxPage < activePage) {
			maxPage = activePage;
		}

		let listItems: Array<number> = [];
		for (let i = (
			activePage <= this.props.itemsLength) ? 1 : activePage - this.props.itemsLength;
			i <= maxPage && i <= activePage + this.props.itemsLength;
			i++
		) {
			listItems.push(i);
		}
		//const ellipsisDown: number = (activePage - this.props.itemsLength * 2 < 1) ? 1 : activePage - this.props.itemsLength * 2
		//const ellipsisUp: number = (activePage + this.props.itemsLength * 2 > maxPage) ? maxPage : activePage + this.props.itemsLength * 2
		return (
			<div className={styles.pagination_div}>
				<div className={styles.pagination_buttons}>
					<button disabled={activePage === 1} className={activePage !== 1 ? styles.button_active : styles.button_inactive} onClick={this.onClickPaginationHandler(1)}><img alt="" src="images/chevron_left.svg" /> Prev</button>
					{
						listItems.map((value) => {
							return (<button key={`pagination-${value}`} className={value === activePage ? styles.button_number_selected : styles.button_number} onClick={this.onClickPaginationHandler(value)}>{value}</button>);
						})
					}
					<button disabled={activePage === maxPage} className={activePage < maxPage ? styles.button_active : styles.button_inactive}>Next <img alt="" src="images/chevron_right.svg" /></button>
				</div>

				<div className={styles.count_items}>
					<div className={styles.items_title}>Show Items</div>
					<div className={styles.choose_block}>
						<div className={`${styles.item_block}`}>
							<div className={styles.item_choose} onClick={this.isItemsLengthActive}>
								<div className={styles.value}>{this.props.countOnPage}</div>
								<img src="images/arrow.svg" alt='arrow' />
							</div>
						</div>
						{
							this.state.isItemsLengthActive && <div className={styles.sub_menu}>
								<div className={styles.element} onClick={this.onClickCountHandler(10)}>10</div>
								<div className={styles.element} onClick={this.onClickCountHandler(25)}>25</div>
								<div className={styles.element} onClick={this.onClickCountHandler(50)}>50</div>
								<div className={styles.element} onClick={this.onClickCountHandler(100)}>100</div>
							</div>
						}
					</div>
				</div>
			</div>
		);
	}
}