import { persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";
import { takeEvery, put, select } from "redux-saga/effects";
import { ApiCallError, HttpStatusError } from "../../api/ApiCall";
import { RootState } from "../rootDuck";
import { clearAccountStorageAction } from "./account.duck";
import { clearAccountsListStorageAction } from "./accounts.duck";
import { clearMerchantsListAction } from "./merchants.duck";
import { clearOrderStorageAction } from "./order.duck";
import { clearOrdersListStorageAction } from "./orders.duck";
//import { clearAuthStorageAction } from "./auth.duck";
//import * as models  from "@cryptopay/dashboard-contract/models";

export type GeneralState = {
	appVersion: string | null;
	error: ErrorData | null;
}
export const initialState: GeneralState = {
	appVersion: null,
	error: null
}

export type ErrorData = {
	message: string;
	error: Error | null;
}

export enum ActionTypes {
	CLEAR_STORAGE = "CLEAR_GENERAL_STORAGE",
	CHECK_APP_VERSION = "CHECK_APP_VERSION",
	HANDLE_ERROR = "HANDLE_ERROR",
	SAVE_ERROR = "SAVE_ERROR",
	SET_APP_VERSION = "SET_APP_VERSION"
};

export interface CheckAppVersionAction {
	type: ActionTypes.CHECK_APP_VERSION;
	payload: { appVersion: string }
}
export function checkAppVersionAction(appVersion: string): GeneralActionTypes {
	return {
		type: ActionTypes.CHECK_APP_VERSION,
		payload: { appVersion }
	}
}

export interface ClearStorageAction {
	type: ActionTypes.CLEAR_STORAGE;
}
export function clearStorageAction(): GeneralActionTypes {
	return {
		type: ActionTypes.CLEAR_STORAGE
	}
}

export interface HandleErrorAction {
	type: ActionTypes.HANDLE_ERROR;
	payload: { messageToDisplay: string | null, error: Error | null }
}
export function handleErrorAction(messageToDisplay: string | null, error: Error | null): GeneralActionTypes {
	return {
		type: ActionTypes.HANDLE_ERROR,
		payload: { messageToDisplay, error }
	}
}

export interface SaveErrorAction {
	type: ActionTypes.SAVE_ERROR;
	payload: { message: string, error: Error | null }
}
export function saveErrorAction(message: string, error: Error | null): GeneralActionTypes {
	return {
		type: ActionTypes.SAVE_ERROR,
		payload: { message, error }
	}
}

export interface SetAppVersionAction {
	type: ActionTypes.SET_APP_VERSION;
	payload: { appVersion: string }
}
export function setAppVersionAction(appVersion: string): GeneralActionTypes {
	return {
		type: ActionTypes.SET_APP_VERSION,
		payload: { appVersion }
	}
}

export type GeneralActionTypes = CheckAppVersionAction
	| ClearStorageAction
	| HandleErrorAction
	| SaveErrorAction
	| SetAppVersionAction;

export function generalReducer(previousState = initialState, action: GeneralActionTypes): GeneralState {
	switch (action.type) {
		case ActionTypes.CLEAR_STORAGE:
			return initialState;
		case ActionTypes.SAVE_ERROR:
			const error: ErrorData = {
				message: action.payload.message,
				error: action.payload.error
			}
			const state: GeneralState = Object.assign({}, previousState);
			state.error = error;
			return state;
		case ActionTypes.SET_APP_VERSION:
			return {
				...previousState,
				appVersion: action.payload.appVersion
			}
		case ActionTypes.HANDLE_ERROR:
		default:
			return previousState;
	}
}

export const reducer = persistReducer({ storage, key: "general" }, generalReducer);

export function* saga() {
	yield takeEvery(ActionTypes.CHECK_APP_VERSION, workerCheckAppVersion);
	yield takeEvery(ActionTypes.CLEAR_STORAGE, workerClearStorage);
	yield takeEvery(ActionTypes.HANDLE_ERROR, workerHandleError);
}

export function* workerCheckAppVersion(action: CheckAppVersionAction) {

	const appVersion = yield select(getAppVersion);
	if (appVersion === null || appVersion !== action.payload.appVersion) {
		yield put(clearStorageAction());
		yield put(setAppVersionAction(action.payload.appVersion));
	}
}

export function* workerClearStorage() {
	yield put(clearAccountStorageAction());
	yield put(clearAccountsListStorageAction());
	yield put(clearMerchantsListAction());
	//yield put(clearAuthStorageAction());
	yield put(clearOrderStorageAction());
	yield put(clearOrdersListStorageAction());;
}

export function* workerHandleError(action: HandleErrorAction) {

	console.error(`Error, message: ${action.payload.messageToDisplay}, error: ${action.payload.error}`)

	if (action.payload.messageToDisplay !== null) {
		yield put(saveErrorAction(action.payload.messageToDisplay, action.payload.error));
		return;
	}

	if (action.payload.error instanceof ApiCallError) {
		const innerError = action.payload.error.innerError
		if (innerError instanceof HttpStatusError) {
			const code = innerError.code;
			switch (code) {
				case 400:
				case 401:
				case 403:
				case 404:
				case 500:
				case 503:
					yield put(saveErrorAction(innerError.message, action.payload.error));
					return;
				default:
					yield put(saveErrorAction("Received api error", action.payload.error));
					return;
			}
		} else {
			yield put(saveErrorAction("Received api error", action.payload.error));
			return;
		}
	}

	yield put(saveErrorAction("Received error", action.payload.error));
}

export const getAppVersion = (state: RootState) => state.general.appVersion;
